// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router';
import store from '@/store';
import 'iview/dist/styles/iview.css';
import './assets/my-theme/index.less';
import '@/assets/common.css';
import '@/assets/styles/style.css';
import { Switch, Message } from 'iview';
import "babel-polyfill";
import vueI18n from 'vue-i18n';
import en from './i18n/en';
import zh from './i18n/zh';
Vue.use(vueI18n)
    // import MsgModal from '@/components/common/MsgModal';
Vue.config.productionTip = false;
Vue.component('i-switch', Switch);
Vue.prototype.$Message = Message;
Vue.prototype.$uid = JSON.parse(window.localStorage.studentinfo).uid;
// Vue.prototype.$host = 'http://test.menke.com';
Vue.filter('formatclass', function(value) {
        switch (value) {
            case 1:
                return '一对一';
                break;
            case 2:
                return '小课';
                break;
            case 3:
                return '大课';
                break;
            default:
                return ''
        }
    })
    // Vue.component('msg-modal',MsgModal);
if(
  localStorage.getItem('studentinfo') == null ||
  localStorage.getItem('studentinfo') == "" ||
  localStorage.getItem('studentkey') == null ||
  localStorage.getItem('studentkey') == "" 
){
  localStorage.removeItem('studentinfo');
  localStorage.removeItem('studentkey');
//  alert('没有studentinfo');
//  window.location.href = '/organweb/';
    window.location.href = '/web/';
}
// 自动设置语言
const lang = 'zh-cn';
const i18n = new vueI18n({
    locale: lang,
    messages: {
      'en-us': en,
      'zh-cn': zh
    }
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    i18n,
    router,
    store,
    components: { App },
    template: '<App />'
})


