<template>
  <div>
    <!-- 个人资料 资料编辑 -->
    <panel-title :crumbs="[{name:$t('Main.Common.UserInfo'),route:{path:'/UserCenter'}},{name:'收货地址',route:''}]" />
    <template>
      <div class="outerTable">
        <Card dis-hover style="height:65px;padding-top:4px;">
          <template>
            <span class='fl-text'>已保存了{{addressdata.length}}条收货地址，还能保存{{10-addressdata.length}}条收货地址</span>
            <div class="address-btn" @click="addNewAddress()">新增地址</div>
          </template>
        </Card>
        <template>
          <i-table ref="selection" stripe :columns="columns" :data="addressdata"></i-table>
        </template>
      </div>
    </template>
    <Modal v-model="isShowCloseModal" width="660px">
      <div slot="header" class="nm_headModal" v-if="adressType==0">新增地址</div>
      <div slot="header" class="nm_headModal" v-if="adressType==1">修改地址</div>
      <Form :label-width="150" :model="formInfo" ref="formInfo" :rules="infoValidate">
        <div class="totalModal">
          <div class="commonStyle">
            <FormItem label="选择地区：" required>
              <Row>
                <Col span="8">
                  <FormItem prop="pid">
                    <Select style="width:140px" v-model="formInfo.pid" @on-change="getCity(formInfo.pid)">
                      <Option :value="item.id" v-for="item in provinceList" :key="item.id">{{item.name}}</Option>
                    </Select>
                  </FormItem>
                </Col>
                <Col span="8">
                  <FormItem prop="cityid">
                    <Select style="width:140px;margin-left:2px;" v-model="formInfo.cityid" @on-change="getArea(formInfo.cityid)">
                      <Option :value="item.id" v-for="item in cityList" :key="item.id">{{item.name}}</Option>
                    </Select>
                  </FormItem>
                </Col>
                <Col span="8">
                  <FormItem prop="areaid">
                    <Select style="width:140px;margin-left:3px;" v-model="formInfo.areaid">
                      <Option :value="item.id" v-for="item in areaList" :key="item.id">{{item.name}}</Option>
                    </Select>
                  </FormItem>
                </Col>
              </Row>
            </FormItem>
          </div>
          <div class="commonStyle">
            <FormItem label="详细地址：" prop="address">
              <Input style="width: 426px;" type="textarea" :rows="2" :maxlength="50" placeholder="请输入详细地址信息，如道路、门牌号、小区、楼栋号、单元等信息，长度不超过50个字符" v-model="formInfo.address"/>
            </FormItem>
          </div>
          <div class="commonStyle">
            <FormItem label="邮政编码：" prop="zipcode">
              <Input style="width: 426px;" placeholder="如您不清楚邮递区号，请填写000000" v-model="formInfo.zipcode" :maxlength="6"/>
            </FormItem>
          </div>
          <div class="commonStyle">
            <FormItem label="收货人姓名：" prop="linkman">
              <Input style="width: 426px;" placeholder="长度不超过25个字符" v-model="formInfo.linkman" :maxlength="25"/>
            </FormItem>
          </div>
          <div class="commonStyle">
            <FormItem label="联系方式：" prop="mobile">
              <Input style="width: 426px;" placeholder="请填写6~20位电话或手机号码" v-model="formInfo.mobile" :maxlength="20"/>
            </FormItem>
          </div>
          <div>
            <Checkbox v-model="single" class="setChecked">设置为默认收货地址</Checkbox>
          </div>
        </div>
      </Form>
      <div slot="footer" class="nm_footerModal">
        <div class="nm_commonBtn nm_confirmBtn" @click="handleForm()">确定</div>
        <div class="nm_commonBtn nm_cancelBtn" @click="cancelEdit()">取消</div>
      </div>
    </Modal>
    <!-- 删除的提示弹框 -->
    <Modal v-model="deleteModal">
      <div slot="header" class="nm_headModal">温馨提示</div>
      <div class="nm_bodyModal">
        删除后将不可恢复，您确定要删除吗？
      </div>
      <div slot="footer" class="nm_footerModal">
        <!-- 确定 -->
        <div @click="ok()" class="nm_commonBtn nm_confirmBtn">{{$t('Main.Common.SaveBtn')}}</div>
        <!-- 取消 -->
        <div @click="cancel()" class="nm_commonBtn nm_cancelBtn">{{$t('Main.Common.CancelBtn')}}</div>
      </div>
    </Modal>
  </div>
</template>

<script>
import panelTitle from "@/components/panelTitle";
import areaCode from "@/components/areaCode";
import "@/assets/areaCode.css";
import {
  Input,
  Form,
  FormItem,
  Radio,
  RadioGroup,
  Select,
  Option,
  DatePicker,
  Switch,
  Card,
  Row,
  Col,
  Button,
  Tabs,
  TabPane,
  Upload,
  Icon,
  Modal,
  Table,
  Checkbox
} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import axios from "axios";
import city from "@/assets/city/city2.json";
import defaultHeader from "@/components/defaultHeader";
export default {
  name: "UserEdit",
  components: {
    Input,
    Form,
    FormItem,
    Radio,
    RadioGroup,
    Select,
    Option,
    DatePicker,
    "i-switch": Switch,
    "i-table": Table,
    Card,
    panelTitle,
    areaCode,
    Row,
    Col,
    Button,
    Tabs,
    TabPane,
    Upload,
    Icon,
    Modal,
    defaultHeader,
    Checkbox
  },
  props: {},
  data() {
    return {
      // 地址省市区
      provinceList: [],
      cityList: [],
      areaList: [],
      data: [],
      isShowCloseModal: false,
      deleteModal: false,
      addressId:'',
      single: false,
      adressType:'',
      formInfo: {
        pid: "",
        cityid: "",
        areaid: "",
        address: "",
        zipcode: "",
        linkman: "",
        mobile: "",
        area: ""
      },
      infoValidate: {
        pid: [
          { required: true, message: "请选择省", trigger: "blur", type: "number" }
        ],
        cityid: [
          { required: true, message: "请选择市",trigger: "blur",type: "number" }
        ],
        areaid: [
          // { required: true, message: "请选择区/县",trigger: "blur",type: "number" }
        ],
        address: [
          { required: true, message: "请输入详细地址", trigger: "blur" }
        ],
        zipcode: [
          { required: true, message: "请输入邮政编码", trigger: "blur" },
          { type: "string", message: "请输入正确的邮政编码", trigger: "blur", pattern: /^[0-9]{6}$/ }
        ],
        linkman: [
          { required: true, message: "请输入收货人姓名", trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "请输入联系方式", trigger: "blur" },
          { type: "string", min: 6, max: 20, message: "请填写6~20位电话或手机号码", trigger: "blur", pattern: /^[0-9\-]*$/ }
        ]
      },
      columns: [
        {
          title: "收货人",
          align: "center",
          key: "linkman"
        },
        {
          title: "所属地区",
          align: "center",
          key: "area"
        },
        {
          title: "详细地址",
          align: "center",
          key: "address"
        },
        {
          title: "邮政编码",
          align: "center",
          key: "zipcode"
        },
        {
          title: "电话",
          align: "center",
          key: "mobile"
        },
        {
          title: "操作",
          key: "orderEdit",
          render: (h, params, index) => {
            return h("div", [
              h(
                "span",
                {
                  style: {
                    color: "#3F9CE8",
                    cursor: "pointer"
                  },
                  on: {
                    click: () => {
                      this.adressType = 1;
                      // console.log(params.row)
                      this.formInfo = JSON.parse(JSON.stringify(params.row))
                      this.formInfo.areaid = Number(params.row.areaid)
                      // console.log(this.formInfo)
                      this.single = Boolean(params.row.isdefault)
                      this.isShowCloseModal = true;
                    }
                  }
                },
                "修改"
              ),
              h(
                "span",
                {
                  style: {
                    color: "#F5496D",
                    marginLeft: "30px",
                    cursor: "pointer"
                  },
                  on: {
                    click: () => {
                      this.addressId = params.row.id
                      this.deleteModal = true
                    }
                  }
                },
                "删除"
              )
            ]);
          },
          align: "center"
        },
        {
          title: "  ",
          key: "orderEdit",
          render: (h, params, index) => {
            if(params.row.isdefault==1){
              return h("div", [
                h(
                  "span",
                  {
                    style: {
                      color: "#4D4D4D",
                    },
                    on: {
                      click: () => {}
                    }
                  },
                  "当前默认地址"
                )
              ]);
            }else{
              return h("div", [
              h(
                "span",
                {
                  style: {
                    color: "#3F9CE8",
                    cursor: "pointer"
                  },
                  on: {
                    click: () => {
                      this.formInfo = JSON.parse(JSON.stringify(params.row))
                      this.single = true
                      Server.post(
                        Path.addOrUpdateAddress,
                        {
                          pid: this.formInfo.pid,
                          cityid: this.formInfo.cityid,
                          areaid: this.formInfo.areaid,
                          address: this.formInfo.address,
                          linkman: this.formInfo.linkman,
                          mobile: this.formInfo.mobile,
                          zipcode: this.formInfo.zipcode,
                          isdefault: Number(this.single),
                          id: this.formInfo.id
                        },
                        res => {
                          let { code, data } = res;
                          if (code === 0) {
                            this.getAddress();
                            this.$Message.success(res.info);
                          } else {
                            this.$Message.info(res.info);
                          }
                        }
                      );
                    }
                  }
                },
                "设为默认地址"
              )
            ]);
            }
          },
          align: "center"
        }
      ],
      addressdata: []
    };
  },
  created() {
    this.getAddress();
  },
  watch:{
		isShowCloseModal(val, oldVal){
			if(val == false){
				this.$refs.formInfo.resetFields()
			}
		}
	},
  methods: {
    // 新增地址
    addNewAddress(){
      this.adressType = 0;
      this.isShowCloseModal=true;
      this.single = false;
      this.formInfo = {};
    },
    // 获取收货地址
    getAddress() {
      this.addressdata = []
      Server.post(Path.getAddressList, {}, res => {
        let { code, data } = res;
        if (code === 0) {
          for(var i=0;i<data.length;i++){
            var obj = {}
            obj.linkman = data[i].linkman
            obj.area = data[i].pname+' '+data[i].cname+' '+data[i].aname
            obj.address = data[i].address
            obj.zipcode = data[i].zipcode
            obj.mobile = data[i].mobile
            obj.isdefault = data[i].isdefault
            obj.id = data[i].id
            obj.pid = data[i].pid
            obj.cityid = data[i].cityid
            obj.areaid = data[i].areaid
            this.addressdata.push(obj)
          }
          this.getProvince();
        } else {
          this.$Message.info(res.info);
        }
      });
    },
    // 查询省
    getProvince() {
      Server.post(
        Path.getCityList,
        {
          parentid: 7
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.provinceList = data;
          } else {
            this.$Message.info(res.info);
          }
        }
      );
    },
    // 查询市
    getCity(findArea) {
      // this.cityList = [];
      // this.areaList = [];
      // this.cityId = 0;
      // this.areaId = 0;
      Server.post(
        Path.getCityList,
        {
          parentid: findArea
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            if (data.length == 0) {
              //如果选择完省市级列表返回为空数组，那么清空areaList
              // this.areaList = [];
              // this.areaId = 0;
            }
            this.cityList = data;
          } else {
            this.$Message.info(res.info);
          }
        }
      );
    },
    // 查询区/县
    getArea(findCity) {
      Server.post(
        Path.getCityList,
        {
          parentid: findCity
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.areaList = data;
          } else {
            this.$Message.info(res.info);
          }
        }
      );
    },
    // 提交表单
    handleForm() {
      this.$refs.formInfo.validate(valid => {
        if (valid) {
          Server.post(
            Path.addOrUpdateAddress,
            {
              pid: this.formInfo.pid,
              cityid: this.formInfo.cityid,
              areaid: this.formInfo.areaid,
              address: this.formInfo.address,
              linkman: this.formInfo.linkman,
              mobile: this.formInfo.mobile,
              zipcode: this.formInfo.zipcode,
              isdefault: Number(this.single),
              id: this.formInfo.id
            },
            res => {
              let { code, data } = res;
              if (code === 0) {
                this.getAddress();
                this.$Message.success(res.info);
              } else {
                this.$Message.info(res.info);
              }
            }
          );
          this.isShowCloseModal = false;
        } else {
          
        }
      });
    },
    // 取消编辑
    cancelEdit(){
      this.isShowCloseModal=false;
      this.formInfo={};
      this.single = false;
    },
    // 确认删除
    ok(){
      Server.post(
        Path.deleteAddress,
        {
          id: this.addressId
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.getAddress()
            this.$Message.success(res.info);
          } else {
            this.$Message.info(res.info);
          }
        }
      );
      this.deleteModal = false;
    },
    // 取消删除
    cancel(){
      this.deleteModal = false;
    },
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/less/tab.less";
@import "~@/assets/base.less";
// 新增按钮
.address-btn {
  float: right;
  width: 88px;
  height: 32px;
  line-height: 32px;
  text-align: center;
  color: #3F9CE8;
  border: 1px solid #3F9CE8;
  border-radius: 2px;
  cursor: pointer;
}
// 地址数量
.fl-text {
  font-size: 14px;
}
// 地址弹框
.totalModal {
  margin-left: 2%;
  margin-top: 20px;
  .commonStyle {
    font-size: 15px;
    div {
      display: inline-block;
      :last-of-type {
        color: #7a8599;
      }
    }
  }
  .setChecked {
    margin-left: 450px;
  }
}
</style>



