<template>
  <div class="layout" id="header">
    <Layout style="height:80px">
      <Header class="green">
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo"><img :src="lemonLogo" alt=""></div>
          <div class="layout-logo"><img :src="lemonText" alt=""></div>
          <Menu ref="leftMenu" :active-name="activeName" theme="light" width="auto" :open-names="[openNames]" class="headRouter">
            <router-link to="/" exact class="ivu-menu-item" tag="li" >
              <span>学习中心</span>
            </router-link>
            <router-link to="/ArrangingLesson" class="ivu-menu-item" tag="li" >
              <span>我的课程</span>
            </router-link>
            <router-link to="/UserHomework" class="ivu-menu-item" tag="li" >
              <span>我的作业</span>
            </router-link>
            <router-link to="/UserOrder" class="ivu-menu-item" tag="li" >
              <span>我的订单</span>
            </router-link>
            <router-link to="/UserCenter" class="ivu-menu-item" tag="li" >
              <span>个人中心</span>
            </router-link>
            <!-- <li class="ivu-menu-item">
              <span>个人中心</span>
            </li> -->
          </Menu>
          <div class="layout-nav">
            <ul class="layout-nav-list">
              <li class="list-item list-item-border user_logout" @click="goWeb" title="返回首页">
                  <img src="@/assets/images/icon_home_righttop@2x.png">
                  <span class="homePage">网校首页</span>
              </li>
              <li class="list-item user_logout" @click="isShowCloseModal = true" title="退出">
                <img src="@/assets/images/icon_close_righttop@2x.png">
                <span>退出</span>
              </li>
            </ul>
          </div>
        </Menu>
      </Header>
    </Layout>
    <Modal v-model="isShowCloseModal">
      <!-- <div slot="header">退出确认</div> -->
      <div slot="header" class="nm_headModal">{{$t('Main.Header.Title')}}</div>
      <!-- 确定要退出登录吗？ -->
      <div class="nm_bodyModal">{{$t('Main.Header.logOut')}}</div>
      <div slot="footer" class="nm_footerModal">
        <!-- 确定 -->
        <div class="nm_commonBtn nm_confirmBtn" @click="logOut()">{{$t('Main.Common.SaveBtn')}}</div>
        <!-- 取消 -->
        <div class="nm_commonBtn nm_cancelBtn" @click="isShowCloseModal = false">{{$t('Main.Common.CancelBtn')}}</div>
      </div>
    </Modal>
  </div>
</template>

<script>
  import {Layout, Menu, MenuItem, Icon, Row, Col, Avatar,Modal,Button} from 'iview'
  import defaultHeader from "@/components/defaultHeader";
  import lemonLogo from "@/assets/images/lemonPic.png";
  import lemonText from "@/assets/images/lemonText@3x.png";
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import successPic from '@/assets/images/applysuccess@2x.png'
  export default {
    name: 'vheader',
    components: {
      'Layout': Layout,
      'Menu': Menu,
      'MenuItem': MenuItem,
      'Icon': Icon,
      Row,
      Col,
      Avatar,
      defaultHeader,
      Modal,
      Button,
      Icon,
    },
    data() {
      return {
        openNames: "MainIndex",
        activeName: "Main",
        isshow: false,
        data:{},
        organdata:{},
        isShowCloseModal: false,
        newInfo: false,
        messageData: [],
        newMessage: [],
        flag: true,
        messageContent: '',
        messageTime: '',
        messageTitle: '',
        message_flag:false,
        newMsgFlag:false,
        lemonLogo,
        lemonText,
        successPic,
        headTab:0,
      }
    },
    created(){
      this.getInfo();
      this.OrganInfo();
      this.getMessageList();
      var that = this;
      document.onclick = function(e){
        if(that.message_flag==true){
          if(e.target.className=='list-item list-item-border user_logout specialMessage'||e.target.className=='specialMessage'){
            that.message_flag = true;
          }else if(e.target.className=='newMessages'||e.target.className=='message_content'){
            that.message_flag = true;
          }else{
            that.message_flag = false;
          }
        }
      }
    },
    watch: {
      getHeadImg(val) {
        this.data.imageurl = val;
      },
      getName(val){
        this.data.nickname = val;
      },
      getChangeModal(val){
        this.applyResult = true
      }
    },
    computed: {
      getHeadImg() {
        return this.$store.store.state.headimg;
      },
      getName(){
        return this.$store.store.state.nickname;
      },
      getChangeModal(val){
        return this.$store.store.state.changeModal;
      }
    },
    methods: {
      showMenu: function() {
        this.isshow = true
      },
      hideMenu: function() {
        this.isshow = false
      },
      // 退出登录
      logOut: function() {
        let {token} = JSON.parse(window.localStorage.studentinfo);
        Server.post(Path.logout,{
          token:token
        },res=>{
          let {code,data} = res;
          window.localStorage.removeItem('studentinfo');
          window.localStorage.removeItem('studentkey');
          window.location.href =  '/web/';
        })
        this.isshow = false;
      },
      // 获取个人资料信息
      getInfo(){
        Server.post(Path.getStudentInfo,{
          studentid:this.$uid
        },res=>{
          // console.log(res)
          let { code, data } = res;
          if(code===0){
            this.data = data;
          }else{
            this.data = {};
          }
        });
      },
      //获取机构信息
      OrganInfo(){
        Server.post(Path.getOrganInfo,{
          
        },res=>{
          let { code, data } = res;
          if(code===0){
            this.organdata = data
          }else{
            this.organdata = {}
          }
        });
      },
      checkAll(){
        this.newInfo = false;
        this.$router.push('UserMessage')
      },
      // 转换时间格式
      timetrans(date){
        var date = new Date(date*1000);//如果date为10位不需要乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes())
        var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
        // return Y+M+D+h+m+s;
        return Y+M+D+h+m;
      },
      // 获取消息列表
      getMessageList(pagenum){
        Server.post(Path.getNewMsg,{
          "studentid": this.$uid,
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.status==1){
              this.newMsgFlag = true;
              this.newMessage = data.data;
              this.newTime = this.newMessage[this.newMessage.length-1].addtime
            }else{
              this.newMsgFlag = false;
            }
          }else{
            this.$Message.info(res.info)
          }
        });
      },
      // 展示消息
      showMessage(){
        if(this.message_flag==true){
          this.message_flag = false
        }else{
          this.getMessageList();
          // 改变消息状态
          if(this.newMsgFlag){
            Server.post(Path.updateMsgStatus,{
              "latelytime": this.newTime
            },(res)=>{
              let { code, data } = res;
              if(code===0){
                this.newMsgFlag = false;
              }else{
                // this.$Message.info(res.info)
              }
            }); 
          }      
          this.message_flag = true;
        }
      },
      // 跳转到官网
      goWeb(){
        window.open('/web/#/home');
      }
    }
  }
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
  #header {
    z-index: 9;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
  }
  .layout {
    background: #f5f7f9;
    position: relative;
  }
  .layout-logo {
    float: left;
    position: relative;
    left: 60px;
    top: 8px;
    // line-height: 46px;
    color: #fff;
    font-size: 20px;
  }

  .layout-logo  img{
    // width: 46px;
    height: 40px;
    display: inline-block;
    margin: -2px 10px 0 0;
    vertical-align: middle;
  }
  .layout-nav {
    float: right;
    margin-right: 30px;
    position: relative;
  }

  .ivu-menu-horizontal {
    height: 80px;
  }

  .ivu-menu-dark {
    background-color: #3F9CE8;
  }

.layout-nav-list {
  height: 80px;
  font-size: 0;
  line-height: 80px;
}

.layout-nav-list .list-item {
  display: inline-block;
  padding: 0 12px;
  font-size: 16px;
  height: 100%;
  color: #fff;
  cursor: pointer;
  overflow: hidden;
  span{
    font-size: 14px;
    vertical-align: middle;
    margin-left: 17px;
  }
}

.layout-nav-list .list-item img {
  vertical-align: middle;
}
.layout-nav-list .list-item .item-avatar {
  display: inline-block;
  margin: 0px 10px 0 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  vertical-align: middle;
  overflow: hidden;
}

.layout-nav-list .list-item .item-avatar #default-img{
  width: 40px;
  height:40px;
}

  .defaultHead{
    width: 100%!important;
    height: 100%!important;
    display: inline-block!important;
  }
  li.dropdown {
    position: relative;
    cursor: auto!important;
    max-width:182px;
    margin-right:0px;
    text-overflow:ellipsis;
    white-space:nowrap;
    // padding: 0!important;
  }
  .dropdown-menu {
    position: absolute;
    width: 360px;
    height: 350px;
    top: 80px;
    left: 30%;
    padding: 26px 30px;
    transform: translateX(-50%);
    background-color: #FFF;
    box-shadow:0px 0px 9px rgba(94,134,126,0.33);
    box-sizing: border-box;
  }
  .dropdown-menu  .nav-item {
    background-color: #F8FAFD;
    border-radius: 2px;
    padding-left: 20px;
    font-size: 12px;
    height: 45px;
    line-height: 45px;
    color: #8096AF;
    margin-bottom: 6px;
    cursor: pointer;
  }
  .dropdown-menu  .nav-item .nav-icon{
    height: 14px; 
    margin-top: 14px;
    font-size: 18px;
    margin-right: 10px;
    vertical-align: top;
  }
  .logOut {
    display: block;
    width: 100%;    
    height:47px; 
    line-height: 47px;
    background:rgba(248,250,253,1);
    border-radius: 2px;
    text-align: center;
    font-size: 12px;
    color: #8096AF;
    border: none;
    margin-top: 24px;
    cursor: pointer;
  }

  .user_logout{
    cursor: pointer;
    .homePage{
      padding-right: 26px;
      border-right: 1px dashed #fff;
    }
  }

  .userName {
    color: #000;
    font-size: 18px;
    vertical-align: middle;
    position: absolute;
    right: 120px;
    top: 31px;
  }
  .avatar-content {
    /* padding: 3px 37px 37px; */
    margin-bottom: 37px;
    margin-left: 37px;
    margin-right: 0;
    width: 80px;
    height: 80px;
    margin-right: 0px;
    border-radius: 50%;
    overflow: hidden;
  }
  .jiao{
  position: absolute;
  top: -8px;
  left: 49%;
  transform: translateX(-50%);
  height: 0px;
  width: 0px;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #FFF;
  border-left: 8px solid transparent;
}
.fade-enter-active, .fade-leave-active {
  transition: height .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  height: 0;
}
.commonSize{
  font-size: 14px;
}
.messageTime{
  float: right;
  color: #999;
}
.messageContent{
  margin-top: 20px;
}
.close{
  width: 84px;
  height: 40px;
  font-size: 13px;
  margin-bottom: 20px;
}
.checkall{
  background-color: #387BFF;
  color: #fff;
  box-shadow:0px 0px 10px rgba(56,123,255,0.49);
  margin-bottom: 20px;
  width: 84px;
  height: 40px;
  font-size: 13px;
}
.newMessages{
  position: absolute;
  width: 610px;
  height: 268px;
  background-color: #fff;
  color: #333333;
  right: 47px;
  margin-top: 8px;
  border-radius: 4px;
  box-shadow:0px 0px 54px rgba(163,163,163,0.34);
  // z-index: 9999!important;
}
.message_Title{
  height: 64px;
  text-align: center;
}
.message_content{
  height: 193px;
  font-size: 14px;
  margin-left: 30px;
  margin-right: 10px;
  padding-right: 10px;
  overflow: auto;
}
.content_detail{
  height: 50px;
  line-height: 18px;
  border-bottom: 1px solid #EBEBEB;
  position: relative;
  padding-top: 10px;
}
.content_detail:first-of-type{
  border-top: 1px solid #EBEBEB;
}
.content_detail:last-of-type{
  border-bottom:  none;
}
.message_content::-webkit-scrollbar  
{  
    width: 8px;  /*滚动条宽度*/
    // height: 76px!important;  /*滚动条高度*/
}  
  
/*定义滚动条轨道 内阴影+圆角*/  
.message_content::-webkit-scrollbar-track  
{  
    // -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
    border-radius: 10px;  /*滚动条的背景区域的圆角*/
    background-color: #EFEEF2;/*滚动条的背景颜色*/  
}  
  
/*定义滑块 内阴影+圆角*/  
.message_content::-webkit-scrollbar-thumb  
{  
    border-radius: 10px;  /*滚动条的圆角*/
    // -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
    background-color: #909296;  /*滚动条的背景颜色*/
} 
.message_time{
  position: absolute;
  right: 0;
  color: #9A9A9A;
}
.headRouter{
  display: inline-block;
  background-color: #3F9CE8;
  color: #fff;
  height: 80px;
  margin-left: 146px;
  &::after{
    visibility: hidden;
  }
  li{
    height: 80px;
    line-height: 50px;
  }
}
/*侧边栏背景颜色*/
.ivu-layout-sider,
.ivu-layout-sider-children,
.ivu-menu-light {
  background: #3F9CE8 !important;
  color: #fff;
  font-size: 16px;
  // height: 100%;
}
.ivu-menu-item {
  position: relative;
  padding: 13px 30px;
  color:#fff!important;
  font-size: 14px;
  padding: 13.5px 36px;
  &.router-link-active {
    color: #3F9CE8!important;
    background: #fff;
  }
  &:hover{
    background-color: #fff;
    color: #3F9CE8!important;
  }
}
</style>



