<template>
  <div id="LessonPlay">
    <panel-title :crumbs="[{name:'精彩瞬间',route:{path:'/UserMoment'}},{name:'精彩瞬间详情',route:''}]" />
    <Card class='br8 cardP0 mg-bottom-20' dis-hover>
      <div>
        <div class="LessonHead">
          <!-- <span class="headTitle">哈哈哈</span> -->
          <span class="headTime">课程名称：法规和国家</span>
          <span class="headName">班级名称：地方换个地方换个</span>
          <span class="headName">课时名称：推广和规范化</span>
        </div>
        <div class="playArea" v-if="playFlag">
          <div class="nonPlay">
            <img src="@/assets/images/replay@2x.png" alt="">
          </div>
          <!-- <div class="playText">课程回放制作中</div> -->
          <div class="playText">{{$t('Main.LessonPlay.PlayText')}}</div>
        </div>
        <div  v-else class="haveReplay">
          <div class="replayArea">
            <img :src= stop class="stopimg" @click="play" v-show="imgflag">
            <img :src="defaultCover" alt="" style="width:100%;height:99%;position:absolute" v-show="imgflag">
            <iframe name ="video" :src="playpath" width="100%" height="630px" frameborder="0" style="border:1px #CFDCE7 solid" class="innerBody"></iframe>
          </div>
          <div class="menuList">
            <div class="dash"></div>
            <Menu :theme="theme3" active-name="1" :active-key="index" v-for="(item,index) in rePlayData.video" :key="index">
              <span @click="videoShow(index)" class="listTest" :class="{'replaySpan': index===checkindex}">
                <span class="littleCircle" :class="{'replay-active': index ===checkindex }">●</span>
                <!-- 片段 -->
                <span style="margin-left: 10px;" class="playTitle" :class="{'replay-active': index ===checkindex }">{{$t('Main.LessonPlay.Part')}}{{index+1}}</span> 
                <span style="margin-left:20px;" class="playTitle" :class="{'replay-active': index ===checkindex }">{{item.duration}}</span>
              </span>
            </Menu>
          </div>
        </div>
      </div>  
    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import CardTitle from "@/components/common/CardTitle/CardTitle";
import right from "@/assets/images/right.png";
import bookImg from "@/container/InstitutionalSchedule/LessonList/assets/bookImg.png";
import { Card, Row, Col, Button, Icon, Avatar, Rate, Input } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultCover from "@/assets/images/defaultReplay.png";
import stop from '@/assets/images/iconstop.png';
export default {
  name: "LessonPlay",
  components: {
    panelTitle,
    right,
    Card,
    Row,
    Col,
    pagination,
    bookImg,
    CardTitle,
    Button,
    Icon,
    Avatar,
    Rate,
    Input,
    defaultCover,
    stop
  },
  created() {
    this.getReplay();
  },
  data() {
    return {
      valueDisabled: 2,
      value6: '',
      paramsData:{},
      date:'',
      playFlag:false,
      theme3:'light',
      playpath:'',
      checkindex: '',
      rePlayData:{},
      defaultCover,
      stop,
      imgflag: true
    };
  },
  methods: {
    // 点击播放按钮
    play(){
      this.checkindex = 0;
      this.playpath =this.rePlayData.video[0].playpath
      this.imgflag =!this.imgflag
    },
    // 点击播放列表
    videoShow(index){
      this.checkindex = index;
      this.playpath =this.rePlayData.video[index].playpath;
      this.imgflag = false;
    },
    // 获取回放信息
    getReplay(){
      let date = this.$route.query.paramsdate;
      // this.paramsData = JSON.parse(window.localStorage.getItem('replay'));
      this.paramsData = {}
      this.date = date;
      Server.post(Path.getLessonsPlayback,{
        'toteachid': 1
      },(res)=>{
        let { code, data } = res;
        if(code===0){
    //       this.rePlayData = {
    //     "teachername": "yujiafeng",
    //     "starttime": "",
    //     "lessonsname": "课时1",
    //     "video": [
    //         {
    //             "playpath": "http://global.talk-cloud.net/replay/754611620/10662/1525864227946/",
    //             "https_playpath": "https://global.talk-cloud.net/replay/754611620/10662/1525864227946/",
    //             "duration": "0时0分16秒",
    //             "part": 1
    //         },
    //         {
    //             "playpath": "http://global.talk-cloud.net/replay/754611620/10662/1525864227946/",
    //             "https_playpath": "https://global.talk-cloud.net/replay/754611620/10662/1525864227946/",
    //             "duration": "0时0分16秒",
    //             "part": 2
    //         },
    //         {
    //             "playpath": "http://global.talk-cloud.net/replay/754611620/10662/1525864290671/",
    //             "https_playpath": "https://global.talk-cloud.net/replay/754611620/10662/1525864290671/",
    //             "duration": "0时42分37秒",
    //             "part": 3
    //         },
    //         {
    //             "playpath": "http://global.talk-cloud.net/replay/754611620/10662/1525864290671/",
    //             "https_playpath": "https://global.talk-cloud.net/replay/754611620/10662/1525864290671/",
    //             "duration": "0时42分37秒",
    //             "part": 4
    //         }
    //     ]
    // }
          if(data.length==0){
            this.playFlag = true;
          }else{
            this.rePlayData = data
          }
        }else{
          // this.$Message.info(res.info);
          this.playFlag = true;
          this.rePlayData = {}
        }
      })
    }
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
.LessonHead{
  border-bottom: 1px solid @line;
  line-height: 57px;
  padding-left: 56px;
  font-size: 16px;
  color:#7A8599;
  .headTitle{
    font-size: 18px;
    color: @fz-Color1;
  }
  .headTime,.headName,.headTitle{
    margin-right: 20px;
  }
}
.playArea{
  width: 900px;
  height: 354px;
  margin: 56px auto;
  background-color: #F3F3F3;
  line-height: 354px;
  position: relative;
  .nonPlay{
    width: 116px;
    height: 180px;
    margin: 0 auto;
    padding-top: 52px;
  }
  .playText{
    position: absolute;
    left: 395px;
    top: 73px;
    color: #BFBFBF;
  }
}
.haveReplay{
  width: 90%;
  margin: 0 auto;
  margin-top: 33px;
  height: 668px;
  .replayArea{
    display: inline-block;
    width: 82%;
    float:left;
    position:relative
    .stopimg{
      position:  absolute;
      z-index: 1;
      width: 72px !important;
      height: 72px !important;
      cursor: pointer;
      margin-left: 50%;
      margin-top: 270px;
    }
  }
}
.menuList{
  display: inline-block;
  float:left;
  width: 18%;
  height: 630px;
  background: #F3F3F3;
  border: 1px solid #CFDCE7;
  border-left: none;
  box-sizing: border-box;
  margin-top: 0px;
  .dash{
    width: 1px;
    height: 626px;
    border-left: 1px dashed #CFDCE7;
    position: absolute;
    margin-left: 23px;
  }
  .listTest{
    height: 34px;
    line-height: 34px;
    display: inline-block;
    font-size:15px;
    width: 100%;
    border-left: 2px solid #F3F3F3;
    cursor: pointer;
    &.replaySpan{
      border-left: 2px solid #387BFF;
      background-color: #fff;
    }
    .littleCircle{
      margin-left: 15px;
      font-size: 19px;
      color: #CFDCE7;
      position: relative;
    }
  }
  .replay-active {
    color: #387BFF;
  }
  .playTitle{
    color:#8E9DAA;
  }
}
</style>



