<template>
  <div>
    <panel-title :crumbs="[{name:'分析反馈',route:''}]" />
    <div class="outerTable">
      <Card dis-hover style="padding-bottom:40px;">
        <div class="msgTitle">
          <div class="searchTitle">
            <input
              placeholder="请输入课时名称"
              class="searchForm"
              v-model.trim="keywords"
              @keydown.enter="getFeedbackList"
            />
            <span class="searchText" @click="getFeedbackList">搜 索</span>
          </div>
        </div>
        <div class="outTable">
          <i-table ref="selection" stripe :columns="columns" :data="data"></i-table>
        </div>
        <template>
          <pagination :total="total" class="mg-top-20" :current-page='current' @pagechange="pagechange" :display='display' v-if='data.length!=0'></pagination>
        </template>
      </Card>
    </div>
    <Modal v-model="checkInfo">
      <div slot="header" class="nm_headModal">老师点评</div>
      <div class="nm_bodyModal">{{info}}</div>
      <div slot="footer" class="nm_footerModal">
        <div class="nm_commonBtn nm_confirmBtn" @click="checkInfo = false">确定</div>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import defaultShow from "@/components/defaultShow";
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import pagination from "@/components/common/pagination";
  import {
    Input,
    Card,
    Table,
    Modal,
    Form,
    Col,
    Button,
    Row,
  } from "iview";
  export default {
    name: "OverView",
    components: {
      panelTitle,
      defaultShow,
      Card,
      Modal,
      Form,
      Col,
      Button,
      Row,
      pagination,
      "i-table": Table,
      Input
    },
    data() {
      return {
        total:1,
        current:1,
        display:20,
        keywords:'',
        checkInfo:false,
        info:'',
        columns: [
          {
            title: '序号',
            type:'index',
            align: "center",
            key: "number",
          },
          {
            title: '课程名称',
            align: "center",
            key: "curriculumname",
          },
          {
            title: '班级名称',
            align: "center",
            key: "gradename",
          },
          {
            title: '课时名称',
            align: "center",
            key: "periodname",
          },
          {
            title: '讲师',
            align: "center",
            key: "teachername",
          },
          {
            title: '分数',
            align: "center",
            key: "score",
          },
          {
            title: '点评',
            key: "evaluate",
            render: (h, params, index) => {
               return h('div',[
                  h(
                    "a",
                    {
                      style: {
                        color: "#3F9CE8",
                        cursor:'pointer',
                        textDecoration: 'underline',
                      },
                      on: {
                        'click': () => {
                          this.info = params.row.comment
                          this.checkInfo = true;
                        }
                      },
                    },
                    '查看'
                  )
                ])
            },
            align: "center"
          }
        ],
        data: [
          {
            number:1,
            coursename:'213',
            classname:'34',
            lessonname:'213',
            teacher:'34',
            score:'213',
            evaluate:'34',
          }
        ],
      }
    },
    created() {
      this.getFeedbackList();
    },
    methods:  {
      pagechange(val){
        this.current = val;
        this.getFeedbackList()
        window.scrollTo(0,0)
      },
      // 转换日期格式
      toDate(str) {
        let date = new Date(str*1000);
        let y = date.getFullYear(),
            m = (date.getMonth() + 1 < 10 ? `0${date.getMonth()+1}` : date.getMonth() + 1),
            d = (date.getDate() < 10 ? `0${date.getDate()}` : date.getDate());
        return `${y}-${m}-${d}`;
      },
      timetrans(date){
        var date = new Date(date*1000);//如果date为10位不需要乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes())
        var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
        // return Y+M+D+h+m+s;
        return Y+M+D+h+m;
      },
      // 获取分析反馈列表
      getFeedbackList(){
        Server.post(
          Path.getFeedbackList,
          {
            'pagenum':this.current,
            'search':this.keywords
          },
          res => {
            let { code, data } = res;
            if (code === 0) {
              this.total = data.pageinfo.total
              this.data = data.data
              this.$store.store.commit('updateFeedbackcount', data.pageinfo.total)
            } else {
              
            }
          }
        );
      }
    }
  }

</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
.outerTable{
  overflow: hidden;
  margin-bottom: 20px;
  .msgTitle{
    .backTitle{
      border-left: 2px solid #3F9CE8;
      padding-left: 15px;
      margin-top: -3px;
    }
    .searchTitle{
      margin: 20px 0 10px 50px;
      .searchForm{
        width: 260px;
        height: 40px;
        border: 1px solid #EBEBEB;
        border-radius:2px;
        font-size: 12px;
        padding-left: 8px;
        background:rgba(250,252,252,1);
      }
      .searchText{
        display: inline-block;
        width: 100px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        background-color: #3F9CE8;
        color: #fff;
        font-size: 14px;
        position: relative;
        left: 87px;
        top: 0px;
        cursor: pointer;
        border-radius:2px;
      }
    }
  }
  .outTable{
    padding-top: 25px;
    margin-left: 49px;
    margin-right: 49px;
  }
}
</style>



