// window.localStorage.setItem('studentkey', studentkey);


function getStr(arr, studentkey, starttime, token) {
  var tostr = arr ? getStrings(arr, "") : '';
  return md5(sha1(md5(tostr) + md5(studentkey) + starttime) + token);
}

function getStrings(data, str) {
  for (var x in data) {
    if (isJson(data[x])) {
      var i = 0;
      for (var k in data[x]) {
        i++;
        break;
      }
      if (i > 0) {
        str += x;
        str = getStrings(data[x], str);
      }
    } else if (isArray(data[x])) {
      if (data[x].length != 0) {
        str += x;
        str = getStrings(data[x], str);
      }
    } else {
      if (data[x] !== undefined && data[x] !== null && data[x] !== '' && data[x] !== false && data[x] !== true) {
        str += x.toString() + data[x].toString();
      }
    }
  }
  return str;
}

function isArray(o) {
  return Object.prototype.toString.call(o) == "[object Array]";
}

function isJson(obj) {
  return (
    typeof obj == "object" &&
    Object.prototype.toString.call(obj).toLowerCase() == "[object object]" &&
    !obj.length
  );
}

// let token = JSON.parse(window.localStorage.getItem("organinfo")).token;
// config http
let baseUrl = '';
if (process.env.NODE_ENV === 'development') {
  baseUrl = 'http://www.lemonedu.com/'
} else if (process.env.NODE_ENV === 'production') {
  baseUrl = '/'
}
http.defaults.baseURL = baseUrl;

// http.defaults.timeout = 2500
http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// cache-control: "max-age=0, private, must-revalidate"
// http.defaults.transformRequest = [function(data) {
//     return qs.stringify(data);
// }];
// http.defaults.headers.post['Cache-control'] = 'max-age=5'
// Add a request interceptor 解决ie下url带中文参数乱码问题

http.interceptors.request.use(function (config) {
  var starttime = Date.parse(new Date());
  // Do something before request is sent
  if (config.method == 'get') {
    config.url = encodeURI(config.url);
  }
  if (
    localStorage.getItem('studentinfo') == null ||
    localStorage.getItem('studentinfo') == "" ||
    localStorage.getItem('studentkey') == null ||
    localStorage.getItem('studentkey') == ""
  ) {
    // alert('没有studentinfo')
    localStorage.removeItem('studentinfo');
    localStorage.removeItem('studentkey');
    window.location.href = '/web/'
  } else {
    var token = JSON.parse(window.localStorage.getItem("studentinfo")).token;
    var data = qs.parse(config.data);
    var SignStr = getStr(
      data,
      localStorage.getItem("studentkey"),
      starttime,
      token
    )
  }
  config.headers.common['sign'] = SignStr;
  config.headers.common['token'] = token;
  config.headers.common['starttime'] = starttime;
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});
var count = 1
// Add a response interceptor
http.interceptors.response.use(function (response) {
  // console.log(store.store)
  // store.store.commit('updateState', 'response.data.code')
  // console.log(1,store.store.state)
  // Do something with response data
  if (response.data.code === -40666) {
    count++
    if(count==2){
      Message.error(response.data.info);
    }
    setTimeout(function () {
      window.localStorage.removeItem('studentinfo');
      window.localStorage.removeItem('studentkey');
      window.location.href = '/web/';
    }, 1500);
  } else {
    return response.data;
  }
}, function (error) {
  // Do something with response error
  if (error.response) {
    if (error.response.status === 400) {
      Message.error('数据错误！');
    } else if (error.response.status === 401) {
      // define window.app=vm in main.js
      // window.app.$router.push({name: 'Login'});
    } else if (error.response.status === 403) {
      Message.error('您没有操作权限哦！');
    } else if (error.response.status === 404) {
      Message.error('对象不存在！');
    } else if (error.response.status === 500) {
      Message.error('服务器发生了一些错误哦！');
    }
  }
  return Promise.reject(error);
});

export default http;



