<template>
  <div id="UserLessonList">
    <!-- 我的课程 -->
    <panel-title :crumbs="[{name:$t('Main.Common.MyCourse'),route:''}]" />
    <div class="nm_TabTitle">
      <div
      @click="tab(1)"
        :class="{nm_active:1 == num}"
      >
        <span class="nm_titleText">直播课</span>
      </div>
      <div 
      @click="tab(2)"
        :class="{nm_active:2 == num}"
      >
        <span class="nm_titleText">录播课</span>
      </div>
    </div>
    <default-show v-if="data.length==0"></default-show>
    <div class="nm_MainArea" v-else>
      <div class="evaluation-box" >
        <div class="nm_commonBtn changeClass" v-if="num==1" @click="changeClass">调  班</div>
        <div 
        class="evaluation-item"
        v-for="(item,index) in data"
        :key="index"
        >
          <div v-for="(innerItem,innerIndex) in item" class="innerList clearfloat" :key="innerIndex">
            <div class="left-time">
                <p class="time">{{innerItem.date.substring(0,4)}}</p>
                <p class="contentDate">{{innerItem.date.substring(5)}}</p>
            </div>
            <div class="time-title-icon border-green">
              <div class="time-title-icon-q bg-green"></div>
            </div>
            <div class="outerBox">
              <div class="eval-header-img fl">
                <img :src="innerItem.imageurl||''" alt="" @error="defaultImg">
              </div>
              <div class="eval-content">
                <div class="fl eval-name" :class="{activeName:num==2}">
                  <div class="couName">{{innerItem.coursename}}</div>
                  <div class="cName">{{innerItem.classname}}</div>
                </div>
                <div class="eval-time">
                  {{innerItem.subhead}}
                </div>
                <div class="middleBox">
                  <div class="eval-class-type teaName">
                    主讲老师：{{innerItem.teachername}}
                  </div>
                  <div class="eval-class-type couTime">
                    课程价格：￥{{innerItem.price}} / 课时
                  </div>
                  <router-link :to="{path:'/LessonContent',query:{schedulingid:innerItem.schedulingid,curriculumid:innerItem.curriculumid,type:innerItem.coursetype}}" v-if="num==1&&innerItem.ischangestatus==0">
                    <div class="nm_commonBtn checkButton">课时安排</div>
                  </router-link>
                  <div class="nm_commonBtn checkButton hadChange" v-if="num==1&&innerItem.ischangestatus==1">已调班</div>
                  <router-link :to="{path:'/LessonContent',query:{schedulingid:innerItem.schedulingid,curriculumid:innerItem.curriculumid,type:innerItem.coursetype}}" v-if="num==2">
                    <div class="nm_commonBtn checkButton">开始学习</div>
                  </router-link>
                </div>
                <div class="eval-class-type eval-progress" v-if="num==1">
                  <span>学习进度：</span>
                  <Progress :percent="Number(innerItem.learned)" hide-info :stroke-width="5" class="inner-progress"/>
                    <span class="progress-num">{{innerItem.learned}}%</span>
                  </Progress>
                </div>
              </div>
            </div>
          </div>
        </div>
        
      </div>
      
      <pagination :total="total" v-if="data.length!=0" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange" style="height:50px"></pagination>  
    </div>
    <Modal v-model="isShowCloseModal" width="528">
      <div slot="header" class="nm_headModal">调班申请</div>
      <Form :label-width="150" :model="formValidate" ref="formInfo" :rules="infoValidate">
        <div class="totalModal">
          <div class="commonStyle">
            <div>
              <FormItem prop="preClassId" label="班级名称：">
                <Select v-model="formValidate.preClassId" @on-change="getNewClass(formValidate.preClassId)"  class="formStyle">
                  <Option :value="item.schedulingid" v-for="item in preClass" :key="item.schedulingid">{{item.gradename}}</Option>
                </Select>
              </FormItem>
            </div>
          </div>
          <div class="commonStyle">
            <div class="rightAlign">剩余课时：</div>
            <div class="rightInfo">{{restLessonNum}}</div>
          </div>
          <div class="commonStyle">
            <div>
              <FormItem prop="newClassId" label="新班级名称：">
                <Select v-model="formValidate.newClassId" @on-change="getInfo(formValidate.newClassId)" class="formStyle">
                  <Option :value="item.schedulingid" v-for="item in newClass" :key="item.schedulingid" v-if="newClass.length!=0">{{item.gradename}}</Option>
                  <Option :value="noClass" v-if="newClass.length==0">暂无符合条件的班级</Option>
                </Select>
              </FormItem>
            </div>
          </div>
          <div class="commonStyle">
            <div class="rightAlign">老师/时间：</div>
            <div class="rightInfo totalInfo"><span class="infoName">{{teachername}}</span>{{info}}</div>
          </div>
        </div>
      </Form>
      <div slot="footer" class="nm_footerModal">
        <div class="nm_commonBtn nm_confirmBtn footerBtn" @click="submitChange()">提交申请</div>
      </div>
    </Modal>
    <Modal v-model="changeInfo">
      <div slot="header" class="nm_headModal">温馨提示</div>
      <div class="nm_bodyModal">
        您暂无可调的班级
      </div>
      <div slot="footer" class="nm_footerModal">
        <!-- 确定 -->
        <div @click="changeInfo = false" class="nm_commonBtn nm_confirmBtn">知道了</div>
      </div>
    </Modal>
    <Modal v-model="applyResult">
      <div slot="header" class="nm_headModal">申请提交成功</div>
      <div class="applyBody">
        <img :src="successPic" alt="" class="fl">
        <div class="msgBody fr">我们会在12小时以内通过系统消息或短信的形式通知您，请注意查收！</div>
      </div>
      <div slot="footer" class="nm_footerModal">
        <div @click="applyResult = false" class="nm_commonBtn nm_confirmBtn">确定</div>
      </div>
    </Modal>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import defaultShow from "@/components/defaultShow";
  import pagination from "@/components/common/pagination";
  import CardTitle from "@/components/common/CardTitle/CardTitle";
  import right from "@/assets/images/right.png";
  import Server from "@/service/request";
  import Path from "@/service/Path";
  import defaultClass from "@/assets/images/defaultClass.png";
  import successPic from '@/assets/images/applysuccess@2x.png'
  import {
    Card,
    Row,
    Col,
    Button,
    TimelineItem,
    Timeline,
    Modal,
    Select,
    Option,
    Progress,
    Form , 
    FormItem
  } from "iview";
  export default {
    name: "UserLessonList",
    components: {
      panelTitle,
      right,
      Card,
      Row,
      Col,
      pagination,
      CardTitle,
      Button,
      TimelineItem,
      Timeline,
      defaultShow,
      defaultClass,
      Modal,
      Select,
      Option,
      Progress,
      Form , 
      FormItem
    },
    created() {
      this.getLessonData(1,this.display,this.coursetype);
    },
    data() {
      return {
        right,
        successPic,
        total: null,
        current: 1,
        display:20,
        data: [],
        num:1,
        isShowCloseModal:false,
        selectItem:'',
        coursetype:2,
        preClass:[],
        newClass:[],
        formValidate:{
          preClassId:'',
          newClassId:'',
        },
        infoValidate: {
          preClassId: [
            { required: true, message: "请选择原班级名称",trigger: "change",type: "number" }
          ],
          newClassId: [
            { required: true, message: "请选择新班级名称",trigger: "change",type: "number" }
          ],
        },
        lessonid:'',
        restLessonNum:'',
        teachername:'',
        info:'',
        changeInfo:false,
        applyResult:false,
        noClass:''
      };
    },
    watch:{
      isShowCloseModal(val, oldVal){
        if(val == false){
          this.$refs.formInfo.resetFields()
        }
      }
    },
    methods: {
      // 头部tab切换
      tab(index){
        this.num = index;
        if(this.num==1){
          this.coursetype = 2
          this.getLessonData(1,this.display,this.coursetype);
        }else{
          this.coursetype = 1
          this.getLessonData(1,this.display,this.coursetype);
        }
      },
      // 设置课程默认封面
      defaultImg(e){
        e.target.src = defaultClass;
      },
      // 分页
      pagechange(currentPage) {
        this.current = currentPage;
        this.getLessonData(currentPage,this.display,this.coursetype);
        window.scrollTo(0,0)
      },
      //获取课程列表
      getLessonData(pagenum,limit,coursetype){
        Server.post(Path.getBuyCurriculum,{
          'studentid':this.$uid,
          'pagenum':pagenum||1,
          'limit':limit||this.display,
          'coursetype':coursetype
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.total = data.pageinfo.total;
            this.data = data.data;
          }else{
            this.data = [];
          }
        })
      },
      // 调班
      changeClass(){
        Server.post(Path.getAllClassList,{
          
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              this.changeInfo = true;
            }else{
              this.preClass = data;
              this.restLessonNum = '';
              this.info = '';
              this.teachername = '';
              this.isShowCloseModal=true
            }
          }else{
            this.$Message.error(res.info)
          }
        })
        
      },
      // 获取目标班级列表
      getNewClass(value){
        this.info = ''
        this.teachername = '';
        this.preClass.forEach((item,index)=>{
          if(item.schedulingid==value){
            this.lessonid = item.curriculumid
            this.newClass = item.classlist
            this.restLessonNum = item.surplusnum+'课时'
          }
        })
      },
      // 获取新班级的老师和时间
      getInfo(value){
        this.newClass.forEach((item,index)=>{
          if(item.schedulingid==value){
            this.teachername = item.teachername
            this.info = ' / '+item.starttime+' ~ '+item.endtime
          }
        })
      },
      // 确认调班
      submitChange(){
        this.$refs.formInfo.validate(valid => {
          if (valid) {
            Server.post(Path.submitapplyclasss,{
              'curriculumid':this.lessonid,
              'oldschedulingid':this.formValidate.preClassId,
              'newschedulingid':this.formValidate.newClassId
            },(res)=>{
              let { code, data } = res;
              if(code===0){
                this.applyResult = true
              }else{
                this.$Message.error(res.info)
              }
              this.isShowCloseModal=false;
              this.formValidate.preClassId = ''
              this.formValidate.newClassId = ''
              this.restLessonNum = ''
              this.info = ''
              this.teachername = '';
              this.newClass = []
            })
            this.isShowCloseModal=false
          } else {
            
          }
        });
      },
    }
  };

</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
#UserLessonList{
  margin-bottom: 6px;
  .nm_MainArea{
    padding-left: 0;
    padding-right: 4%;
    // 调班按钮
    .changeClass{
      position: absolute;
      top: -90px;
      right: -20px;
      height: 31px;
      line-height: 31px;
      text-align: center;
      border-top: none;
      color: #3F9CE8;
      border-radius: 2px;
      cursor: pointer;
      border: 1px solid #3F9CE8;
    }
    // 课程详情
    .evaluation-box {
      margin-left:128px;
      padding-left: 40px;
      border-left: 1px solid rgba(99,174,235,0.8);
      margin-top: 50px;
      position: relative;
      .evaluation-item {
        border-bottom: 1px solid #cfdce7;
        margin-right: 66px;
        position: relative;
        // 课程封面
        .eval-header-img {
          margin-top: -8px;
          width: 220px;
          height: 130px;
          margin-right: 30px;
          border-radius: 6px;
          overflow: hidden;
          position: relative;
          margin-bottom: 16px;
          img {
            position: absolute;
            left: -1px;
            top: -1px;
            width: 222px;
            height: 132px;
          }
        }
        // 课程详细信息
        .innerList{
          border-bottom: 1px solid #cfdce7;
          &:last-child {
            border-bottom: none;
          }
          .outerBox{
            margin-top: 22px;
            .eval-content {
              margin-bottom: 0;
              position: relative;
              margin-left: 250px;
              ul li {
                font-size: 14px;
              }
              &:nth-child(3) {
                margin-bottom: 5px;
                height: 30px;
              }
              .eval-class-type {
                font-size: 16px;
                margin-right: 40px;
                color: #565E66;
              }
              .teaName{
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
                max-width: 50%;
              }
              .couTime{
                position: relative;
                top: -6px;
              }
              .eval-progress {
                width: 345px;
                margin-top: 36px;
                font-size: 14px;
                .inner-progress{
                  display: inline-block;
                  width: 56%;
                  
                }
                .progress-num{
                  vertical-align: middle;
                  margin-left: 10px;
                  color: #FFAB39;
                  font-size: 14px;
                }
              }
              .eval-name {
                font-weight: bold;
                font-size: 18px;
                margin-left: 34px;
                margin-bottom: -6px;
                min-width: 20%;
                width: 88%;
                div{
                  display: inline-block;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  max-width: 47%;
                  float: left;
                }
                .cName{
                  font-weight: normal;
                  font-size: 16px;
                  color: #8e9daa;
                  line-height: 40px;
                  margin-left: 20px;
                }
              }
              .eval-time {
                color: #8e9daa;
                margin-right: 79px;
                font-size: 12px;
                width: 83%;
                white-space: nowrap; 
                overflow: hidden;
                text-overflow:ellipsis;
              }
              .activeName{
                margin-bottom: 16px;
              }
              .middleBox{
                margin: 12px 0 -25px 0;
                .checkButton {
                  background-color: #fff;
                  color: #FFAB39;
                  border: 1px solid #FFAB39;
                  height: 31px;
                  line-height: 31px;
                  position: absolute;
                  right: 0px;
                  top: 33px;
                  border-radius: 2px;
                }
                .hadChange{
                  cursor: auto;
                }
                div {
                  display: inline-block;
                  font-size: 14px
                }
              }
            }
          }
          // 左侧日期
          .left-time {
            position: absolute;
            top: 0px;
            left: -130px;
            text-align: right;
            color: #3F9CE8;
            font-family: 'PingFangSC-Regular';
            .time {
              font-size: 16px;
            }
            .contentDate {
              font-size: 12px;
            }
          }
          // 左侧时间轴
          .time-title-icon {
            position: absolute;
            background-color: #fff;
            border: 1px solid transparent!important;
            left: -48px;
            top: 0;
            border-radius: 50%;
            width:15px;
            height:15px; 
            .time-title-icon-q {
              width: 9px;
              height: 9px;
              border-radius: 50%;
              position: absolute;
              left: 1.5px;
              top: 1.5px;
              background:#3F9CE8;
              box-shadow:  0px 0px 0px 3px rgba(99,174,235,0.2)
            }
          }
        }
      }
    }
  }
  .footerBtn{
    width: 140px;
    height: 40px;
    line-height: 40px;
    font-weight:bold;
    margin-right: 0;
  }
}
// 调班弹框
.totalModal{
  margin-left: 6%;
  margin-top: 20px;
  .commonStyle{
    font-size: 15px;
    color: #7A8599;
    div{
      display: inline-block;
    }
    .formStyle{
      color: #7A8599;
      width:250px;
    }
    .rightInfo{
      margin-left: 8px;
      .infoName{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: inline-block;
        max-width: 135px;
        position: relative;
        top: 6px;
      }
    }
    .totalInfo{
      margin-top: -10px;
    }
    .rightAlign{
      width: 100px;
      text-align: right;
      color: #7A8599;
      font-size: 16px;
      margin-left: 38px;
      margin-bottom: 12px;
    }
  }
}
// 申请成功的弹框
.applyBody{
  height: 100px;
  margin-top: 20px;
  img{
    margin-left: 60px;
  }
  .msgBody{
    width: 272px;
    font-size: 16px;
    color: #808080;
    margin-top: 40px;
  }
}
</style>



