<template>
  <div id="LessonPlay">
    <!-- 我的课程 录播课-->
    <panel-title :crumbs="[{name:$t('Main.Common.MyCourse'),route:{path:'/ArrangingLesson'}},{name:'录播课',route:{path:'/LessonContent',query:{ordernum:ordernum,curriculumid:curriculumid,type:type}}},{name:'观看录播',route:''}]" />
    <Card class='br8 cardP0 mg-bottom-20' dis-hover>
      <div>
        <div class="haveReplay">
          <div class="outerReplay">
            <video controls webkit-playsinline="true" playsinline="true" :src="playpath" width="100%" height="600px" style="border:1px #CFDCE7 solid">
              您的浏览器不支持该视频播放
            </video>
          </div>
        </div>
      </div>
      <!-- <div style="padding:0 55px">
        <div class="lessonInfo">
          <img src="../assets/icon_evaluate.png">
          <span>课程评论：</span>
        </div>
