          <div class="person-avatar">
            <Avatar :src="defaultAvatar" class="avatar" />
          </div>
          <div class="preson-content">
            <div class="content-main">
              <h3>{{item.nickname}}</h3>
              <Rate show-text allow-half disabled class="rate" :value.sync="item.score">
                <span class="rate-srore">{{item.score}}分</span>
              </Rate>
            </div>
            <p class="content">{{item.content}}</p>
            <div class="ratting-footer">
              <p class="ratting-time">{{item.addtime}}</p>
            </div>
          </div>
        </div>  
      </div>
      <router-link to="/LessonEvaluate">
        <div class="comment">我要评论</div>
      </router-link> -->
    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import CardTitle from "@/components/common/CardTitle/CardTitle";
import right from "@/assets/images/right.png";
import bookImg from "@/container/InstitutionalSchedule/LessonList/assets/bookImg.png";
import { Card, Row, Col, Button, Icon, Avatar, Rate, Input } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultCover from "@/assets/images/defaultReplay.png";
import stop from "@/assets/images/iconstop.png";
import defaultAvatar from "@/assets/images/default.png";
export default {
  name: "LessonPlay",
  components: {
    panelTitle,
    right,
    Card,
    Row,
    Col,
    pagination,
    bookImg,
    CardTitle,
    Button,
    Icon,
    Avatar,
    Rate,
    Input,
    stop
  },
  created() {
    this.ordernum = this.$route.query.ordernum;
    this.curriculumid = this.$route.query.curriculumid;
    this.type = this.$route.query.type;
    this.lessonsid = this.$route.query.lessonsid;
    this.getReplay();
    this.getComment();
  },
  data() {
    return {
      valueDisabled: 2,
      value6: "",
      playpath: "",
      defaultCover,
      stop,
      defaultAvatar,
      imgflag: true,
      ordernum: "",
      type: "",
      curriculumid: "",
      lessonsid: "",
      commentArr: []
    };
  },
  methods: {
    // 获取录播视频
    getReplay() {
      Server.post(
        Path.watchPlayback,
        {
          lessonsid: this.lessonsid,
          courseid: this.curriculumid,
          studentid: this.$uid
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.playpath = 'http://'+data.cosurl;
          } else {
          }
      })
    },
    // 获取录播课评论
    getComment() {
      Server.post(
        Path.getRecordComment,
        {
          lessonsid: this.lessonsid,
          pagenum: 1
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.commentArr = data.data;
          } else {
          }
        }
      );
    }
  }
};
</script>
