<template>
  <div id="defaultData" class="totalDefault">
    <div class="default-img">
      <img src="./assets/images/data_default.png" alt="">
      <p class="fontColorr">{{noData}}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "defaultData",
  // props:{
  //   text:{
  //     // default:'暂无数据',
  //     // default:this.$t('Main.DefaultShow.default'),
  //     type: String,
  //   }
  // },
  data(){
    return {
      
    }
  },
  computed:{
    noData(){
      return this.$t('Main.DefaultShow.default')
    }
    
  }
}
</script>
<style scoped>
  .default-img{
    text-align: center;
    width: 100%;
    padding: 100px 0;
  }
  .fontColorr{
    color:rgba(164,171,179,1);
    font-size: 16px;
    margin-top: 20px;
  }
  .totalDefault{
    background-color:#fff;
    height:750px;
    padding-top:120px;
  }
</style>



