<template>
  <div id="default-img">
    <img class="defaultimg" :src="imgUrl" alt="" ref="img" />
  </div>
</template>
<script>
import defaultimg from './assets/img/default.png'
export default {
  name: "default-img",
  props:{
    sourceimg:{
      default:defaultimg,
      type:String
    }
  },
  created() {
    //do something after creating vue instance
    // console.log(this.sourceimg);
  },
  mounted() {
    //do something after mounting vue instance
    this.$refs.img.onerror = () => {
      this.imgUrl = defaultimg
    }
  },
  data(){
    return {
      imgUrl: this.sourceimg==''?defaultimg:this.sourceimg
    }
  },
  watch:{
    sourceimg:function(newVal,oldVal){
      this.imgUrl = newVal;
    }
  }
}
</script>
<style scoped>
.defaultimg{
  width: 100%;
  height: 100%;
}
</style>



