<template>
  <div>
    <!-- 我的收藏 -->
    <panel-title :crumbs="[{name:$t('Main.UserStar.Title'),route:''}]" />
    <!-- tab切换 -->
    <div class="nm_TabTitle">
      <div
      @click="tab(1)"
      :class="{nm_active:1 == num}"
      >
      <!-- 课程 -->
        <span class="nm_titleText">{{$t('Main.UserStar.CourseStar')}}</span>
      </div>
      <div 
      @click="tab(2)"
      :class="{nm_active:2 == num}" 
      >
      <!-- 老师 -->
        <span class="nm_titleText">{{$t('Main.UserStar.TeacherStar')}}</span>
      </div>
    </div>
    <template>
      <div class="nm_MainArea">
        <div v-if="num==1">
          <default-show v-if="classList.length==0" style="height:690px;padding-top:62px;"></default-show>
          <div
          v-else
          class="evaluation-item"
          v-for="(item,index) in classList"
          :key="index">
            <div class="eval-header-img fl">
            <img @error="defaultImg" :src="item.imageurl||''">
            </div>
            <div class="eval-content">
              <ul>
                <li>
                  <div class="fl eval-name">
                    {{item.coursename}}
                  </div>
                </li>
                <li>
                  <div class="fl eval-class-type" v-if="item.classtypes==1">
                    录播
                  </div>
                  <div class="fl eval-class-type" v-if="item.classtypes==2">
                    直播
                  </div>
                </li>
                <li>
                  <div class="fl eval-class-type" style="color:red" v-if="item.classtypes==1">
                    <!-- ￥ -->
                    {{$t('Main.Common.MoneyLogo')}}{{item.price}}
                  </div>
                  <div class="fl eval-class-type" style="color:red" v-if="item.classtypes==2">
                    <!-- ￥ -->
                    {{$t('Main.Common.MoneyLogo')}}{{item.price}} 
                    <span v-if="item.price!=item.maxprice">-- {{$t('Main.Common.MoneyLogo')}}{{item.maxprice}}</span>
                  </div>
                </li>
              </ul>
            </div>
            <div class="stateButton" @click="goDetailList(1,item.curriculumid,item.classtypes)">查看详情</div>
            <!-- 取消收藏 -->
            <div class="common_cancel" :class="{class_active:classFlag&&checkedClass===index}" @click="cancelClass(item.curriculumid)">{{$t('Main.UserStar.CancelStar')}}</div>
            <div class="icon_cancel" @click="cancelClassShow(index)">
              <Icon type="chevron-down" class="icon_bottom"></Icon>
            </div>
          </div>
          <pagination v-if="classList.length!=0" :total="totalClass" :display="displayClass" :current-page='currentClass' @pagechange="classChange" class="pageCommon"></pagination>
        </div>
        <div v-if="num==2">
          <default-show v-if="teacherList.length==0" style="height:690px;padding-top:62px;"></default-show>
          <div class="evaluation-item" v-for="(item,index) in teacherList" v-else :key="index">
            <div class="eval-header-img fl" id="special_head" style="overflow:hidden">
              <img @error="teacherImg" :src="item.imageurl||''" id="special_default">
            </div>
            <div class="eval-content">
              <ul>
                <li>
                  <div class="fl eval-name">
                    {{item.teachername}}
                  </div>
                </li>
                <li style="height:39px;width:76%">
                  <div class="courseText">{{item.profile}}</div>
                </li>
              </ul>
            </div>
            <div class="stateButton" @click="goDetailList(2,item.teacherid)">查看详情</div>
            <!-- 取消收藏 -->
            <div class="common_cancel" :class="{teacher_active:teacherFlag&&checkedTeacher===index}" @click="cancelTeacher(item.teacherid)">{{$t('Main.UserStar.CancelStar')}}</div>
            <div class="icon_cancel" @click="cancelTeacherShow(index)">
              <Icon type="chevron-down" class="icon_bottom"></Icon>
            </div>
          </div>
          <pagination v-if="teacherList.length!=0" :total="totalTeacher" :display="displayTeacher" :current-page='currentTeacher' @pagechange="teacherChange" class="pageCommon"></pagination>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import pagination from "@/components/common/pagination";
  import HeaderImg from '@/container/UserCenter/assets/images/avatar_upload.png';
  import defaultShow from "@/components/defaultShow";
  import defaultHeader from "@/components/defaultHeader";
  import defaultClass from "@/assets/images/defaultClass.png";
  import defaultTeacher from "@/assets/images/default.png";
  import {
    Card,
    Row,
    Col,
    Button,
    Tabs,
    TabPane,
    Icon,
  } from 'iview'
  import Server from '@/service/request';
  import Path from '@/service/Path';
  // import md5 from "md5";
  // import sha1 from "sha1";
  export default {
    name: "UserEdit",
    components: {
      Card,
      panelTitle,
      Row,
      Col,
      Button,
      Tabs,
      TabPane,
      Icon,
      pagination,
      defaultShow,
      defaultClass,
      defaultHeader,
      defaultTeacher
    },
    props: {},
    data() {
      return {
        num:1,
        totalClass:null,
        currentClass:1,
        displayClass:20,
        totalTeacher:null,
        currentTeacher:1,
        displayTeacher:20,
        classList:[],
        teacherList:[],
        classFlag:false,
        checkedClass:'',
        teacherFlag:false,
        checkedTeacher:'',
      }
    },
    created() {
      this.getClassList(1,this.displayClass);
    },
    methods: {
      defaultImg(e){
        e.target.src = defaultClass;
      },
      teacherImg(e){
        e.target.src = defaultTeacher;
      },
      // tab切换
      tab(index){
        if(index==1){
          this.currentClass = 1;
          this.getClassList(1,this.displayClass);
        }else{
          this.currentTeacher = 1;
          this.getTeacherList(1,this.displayTeacher);
        }
        this.num = index;
      },
      // 课程分页
      classChange: function(currentPage) {
        this.currentClass = currentPage;
        this.getClassList(currentPage,this.displayClass);
        window.scrollTo(0,0)
      },
      // 老师分页
      teacherChange: function(currentPage) {
        this.currentTeacher = currentPage;
        this.getTeacherList(currentPage,this.displayTeacher);
        window.scrollTo(0,0)
      },
      // 获取课程收藏列表
      getClassList(pagenum=1,limit=this.displayClass){
        Server.post(Path.classCollectList,{
          'studentid': this.$uid,
          'pagenum': pagenum,
          'limit':limit
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              this.classList = [];
            }else{
              this.classList = data.data;
              this.totalClass = data.pageinfo.total;
            }
          }else{
            this.classList = [];
          }
        })
      },
      // 获取教师收藏列表
      getTeacherList(pagenum=1,limit=this.displayTeacher){
        Server.post(Path.teacherCollectList,{
          'studentid': this.$uid,
          'pagenum': pagenum,
          'limit':limit
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              this.teacherList = [];
            }else{
              this.teacherList = data.data;
              this.totalTeacher = data.pageinfo.total;
            }
          }else{
            this.teacherList = [];
          }
        })
      },
      // 取消课程收藏
      cancelClassShow(index){
        this.checkedClass=index;
        if(this.classFlag == true){
          this.classFlag = false;
        }else{
          this.classFlag = true;
        }
      },
      cancelClass(curriculumid){
        Server.post(Path.cancelClassCollect,{
          'courseid':curriculumid,
          'studentid':this.$uid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$Message.success(res.info);
            this.classFlag = false;
            this.getClassList();
          }else{
            this.$Message.error(res.info);
          }
        })
      },
      // 取消老师收藏
      cancelTeacherShow(index){
        this.checkedTeacher=index;
        if(this.teacherFlag == true){
          this.teacherFlag = false;
        }else{
          this.teacherFlag = true;
        }
      },
      cancelTeacher(teacherid){
        Server.post(Path.cancelTeacherCollect,{
          'teacherid':teacherid,
          'studentid':this.$uid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$Message.success(res.info);
            this.teacherFlag = false;
            this.getTeacherList();
          }else{
            this.$Message.error(res.info);
          }
        })
      },
      // ajax封装
      ajax(options) {
        options = options || {};
        options.type = (options.type || "GET").toUpperCase();
        options.dataType = options.dataType || "json";
        var params = this.formatParams(options.data);
        //创建 - 非IE6 - 第一步
        if (window.XMLHttpRequest) {
              var xhr = new XMLHttpRequest();
          } else { //IE6及其以下版本浏览器
              var xhr = new ActiveXObject('Microsoft.XMLHTTP');
          }
          //接收 - 第三步
          xhr.onreadystatechange = function () {
              if (xhr.readyState == 4) {
                  var status = xhr.status;
                  if (status >= 200 && status < 300) {
                      options.success && options.success(xhr.responseText, xhr.responseXML);
                  } else {
                      options.fail && options.fail(status);
                  }
              }
          }
          //连接 和 发送 - 第二步
          if (options.type == "GET") {
              xhr.open("GET", options.url + "?" + params, true);
              xhr.send(null);
          } else if (options.type == "POST") {
              xhr.open("POST", options.url, false);
              //设置表单提交时的内容类型
              xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
              // // 设置签名
              // var token = JSON.parse(window.localStorage.getItem("studentinfo")).token;
              // var data = params;
              // var starttime = Date.parse(new Date());
              // var SignStr = this.getStr(
              //   data,
              //   localStorage.getItem("studentkey"),
              //   starttime,
              //   token
              // )
              // xhr.setRequestHeader("sign", SignStr);
              // xhr.setRequestHeader("token", token);
              // xhr.setRequestHeader("starttime", starttime);
              xhr.send(params);
          }
      },
      //格式化参数
      formatParams(data) {
        var arr = [];
        for (var name in data) {
            arr.push(encodeURIComponent(name) + "=" + encodeURIComponent(data[name]));
        }
        // arr.push(("v=" + Math.random()).replace(".",""));
        return arr.join("&");
      },
      // 跳转收藏页面
      goDetailList(diff,id,type){
        var that = this;
        var host = window.location.origin=='http://192.168.0.179:8080'?'http://www.lemonedu.com/':'/';
        that.ajax({
          url: host+"student/Homepage/isdelflag",
          type: "POST",
          data: {'type':diff, 'id':id },
          dataType: "json",
          success: function (response, xml) {
            response = JSON.parse(response)
            if(response.code==0){
              if(diff==1){
                var url = host+'web/#/courseDetails?id='+id+'&type='+type
              }else{
                var url = host+'web/#/teacherDetails?id='+id
              }
              window.open(url)
            }else{
              that.$Message.error(response.info)
              if(diff==1){
                that.getClassList(that.currentClass,that.displayClass);
              }else{
                that.getTeacherList(that.currentTeacher,that.displayTeacher);
              }
            }
          },
          fail: function (status) {
            // that.$Message.error(status)
            if(diff==1){
              that.getClassList(that.currentClass,that.displayClass);
            }else{
              that.getTeacherList(that.currentTeacher,that.displayTeacher);
            }
          }
        });
      },
      // // 签名验证
      // getStr(arr, studentkey, starttime, token) {
      //   var tostr = arr ? this.getStrings(arr, "") : '';
      //   return md5(sha1(md5(tostr) + md5(studentkey) + starttime) + token);
      // },
      // getStrings(data, str) {
      //   for (var x in data) {
      //     if (this.isJson(data[x])) {
      //       var i = 0;
      //       for (var k in data[x]) {
      //         i++;
      //         break;
      //       }
      //       if (i > 0) {
      //         str += x;
      //         str = this.getStrings(data[x], str);
      //       }
      //     } else if (this.isArray(data[x])) {
      //       if (data[x].length != 0) {
      //         str += x;
      //         str = this.getStrings(data[x], str);
      //       }
      //     } else {
      //       if (data[x] !== undefined && data[x] !== null && data[x] !== '' && data[x] !== false && data[x] !== true) {
      //         str += x.toString() + data[x].toString();
      //       }
      //     }
      //   }
      //   return str;
      // },
      // isArray(o) {
      //   return Object.prototype.toString.call(o) == "[object Array]";
      // },
      // isJson(obj) {
      //   return (
      //     typeof obj == "object" &&
      //     Object.prototype.toString.call(obj).toLowerCase() == "[object object]" &&
      //     !obj.length
      //   );
      // }
    },
  }
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
.nm_MainArea{
  padding-left: 58px;
  padding-right: 58px;
  // 收藏详情
  .evaluation-item {
    overflow: hidden;
    border-bottom: 1px solid #cfdce7;
    padding-top: 11px;
    padding-bottom: 15px;
    position: relative;
    //课程封面及老师头像
    .eval-header-img {
      margin-top: 4px;
      width: 216px;
      height: 130px;
      margin-right: 30px;
      border-radius: 6px;
      overflow: hidden;
      position: relative;
      &#special_head{
        border-radius:50%;
        width: 88px;
        height: 88px;
        margin-top: 0px;
      }
      img {
        width: 218px;
        height: 132px;
        position: absolute;
        left: -1px;
        top: -1px;
      }
      #special_default{
        width: 90px;
        height: 90px;
        position: absolute;
        left: -1px;
        top: -1px;
      }
    }
    &:last-of-type{
      border: none;
    }
    // 查看详情
    .stateButton{
      position: absolute;
      right: 6px;
      top: 70px;
      bottom: 21px;
      background-color: #fff;
      color: #FFAB39;
      width: 81px;
      height: 31px;
      line-height: 31px;
      text-align: center;
      font-size: 14px;
      border: 1px solid #FFAB39;
      cursor: pointer;
      border-radius: 2px;
    }
    // 取消收藏
    .icon_cancel{
      position: absolute;
      right: 0;
      top: 20px;
      cursor: pointer;
      .icon_bottom{
        color: #8A9299;
        width: 11px;
        height: 6px;
      }
    }
    .common_cancel{
      width: 60px;
      height: 24px;
      line-height: 24px;
      text-align: center;
      font-size: 12px;
      color: #F04747;
      position: absolute;
      right: 0;
      top: 35px;
      background:rgba(255,255,255,1);
      border-radius:2px;
      box-shadow:1px 0px 8px rgba(170,170,170,0.3);
      display: none;
      cursor: pointer;
      &.class_active{
        display: block;
      }
      &.teacher_active{
        display: block;
      }
    }
    // 收藏详细信息
    .eval-content{
      position: relative;
      margin-top: 15px;
      ul li {
        overflow: hidden;
        margin-bottom: 8px;
        font-size: 14px;
        height: 27px;
        &:nth-of-type(1){
          margin-bottom: 12px;
          height: 30px;
        }
        .eval-name {
          font-weight: bold;
          font-size: 22px;
          margin-bottom: 900px;
        }
        .eval-class-type {
          margin-right: 40px;
          color: #7A8599;
        }
        .courseText{
          overflow: hidden;
          text-overflow:ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          /*! autoprefixer: off */
          -webkit-box-orient: vertical;
          /* autoprefixer: on */
        }
      }
    }
  }
  // 分页
  .pageCommon{
    height: 50px;
  }
}
</style>



