<template>
  <div id="LessonDetails">
    <!-- 我的课表 已结束 课程评价 -->
    <panel-title :crumbs="[{name:$t('Main.Common.Schedule'),route:{path:'/'}},{name:$t('Main.MyLessonList.Completed'),route:{path:'/LessonList',query:{date:date,type:0}}},{name:$t('Main.LessonEvaluate.Title'),route:''}]" v-if="flag==1"/>
    <panel-title :crumbs="[{name:$t('Main.Common.Schedule'),route:{path:'/'}},{name:$t('Main.MyLessonList.Completed'),route:{path:'/LessonList',query:{date:date,type:0}}},{name:'课时详情',route:{path:'/LessonDetail',query:{paramsdate:date,type:0,buttonstatus:2}}},{name:$t('Main.LessonEvaluate.Title'),route:''}]" v-if="flag==2"/>
    <Card class='br8 cardP0 mg-bottom-20' dis-hover>
      <div> 
        <div class="LessonHead"> 
          <span class="headTitle">{{paramsData.coursename}}</span>
          <!-- 上课时间： -->
          <span class="headTime">{{$t('Main.Common.LessonTime')}}{{paramsData.starttime}}</span>
          <!-- 老师： -->
          <span class="headName">{{$t('Main.Common.Teacher')}}{{paramsData.teachername}}</span>  
        </div>
        <div class="addForm">
          <div class='evaluateContent'> 
            <!-- 评分： -->
            <span>{{$t('Main.LessonEvaluate.Score')}}</span>
            <Rate v-model="valueDisabled"></Rate>
          </div>
          <div class="evaluateContent">
            <!-- 评分内容： -->
            <span>{{$t('Main.LessonEvaluate.ScoreText')}}</span>
            <!-- 请输入评价内容 -->
            <Input v-model="value" type="textarea" :rows="10" :placeholder="$t('Main.LessonEvaluate.Placeholder')" class="evaluateLabel" :maxlength="numLimit"></Input>
            <div class="count">{{value.length}}/<div class="countText">300</div></div>
          </div>
          <!-- 提交 -->
          <div class="nm_commonBtn nm_confirmBtn confirm" @click="submitEvaluate">{{$t('Main.LessonEvaluate.Submit')}}</div>
        </div>
      </div>
    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import CardTitle from "@/components/common/CardTitle/CardTitle";
import bookImg from "@/container/InstitutionalSchedule/LessonList/assets/bookImg.png";
import { Card, Row, Col, Button, Icon, Avatar, Rate, Input } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "LessonDetails",
  components: {
    panelTitle,
    Card,
    Row,
    Col,
    pagination,
    bookImg,
    CardTitle,
    Button,
    Icon,
    Avatar,
    Rate,
    Input
  },
  created() {
    this.getParams();
  },
  data() {
    return {
      valueDisabled: 0,
      value: '',
      paramsData:{},
      date:'',
      numLimit:300,
      flag:''
    };
  },
  methods: {
    // 获取地址栏参数
    getParams () { 
      let date = this.$route.query.paramsdate;
      let flag = this.$route.query.flag;
      this.paramsData = JSON.parse(window.localStorage.getItem('evaluate'));
      this.date = date;
      this.flag = flag
    },
    //提交评价
    submitEvaluate(){
      let nickname = JSON.parse(window.localStorage.studentinfo).nickname;
      Server.post(Path.gotoComment,{
        'nickname':nickname,
        'curriculumid':this.paramsData.curriculumid,
        'classtype':this.paramsData.type,
        'studentid':this.$uid,
        'teacherid':this.paramsData.teacherid,  
        'score':this.valueDisabled,
        'schedulingid':this.paramsData.schedulingid,
        'content':this.value,
        'lessonsid':this.paramsData.lessonsid,
        'toteachid':this.paramsData.toteachid,
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.$Message.success(res.info);
          this.value = '';
          this.valueDisabled = 0
          this.$router.push('/')
        }else{
          this.$Message.error(res.info);
        }
      })
    },
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
// 课时信息
.LessonHead{
  border-bottom: 1px solid @line;
  line-height: 57px;
  padding-left: 56px;
  font-size: 16px;
  color:#7A8599;
  .headTitle{
    font-size: 18px;
    color: @fz-Color1;
  }
  .headTime,.headName,.headTitle{
    margin-right: 20px;
  }
}
// 评价区域
.addForm{
  padding: 63px 180px 30px 0;
  span{
    display: inline-block;
    width: 180px;
    text-align: right;
    font-size: 16px;
    color: @fz-Color1;
    line-height: 24px;
    margin-right: 20px;
    margin-bottom: 40px;
    float: left;
  }
  .evaluateContent{
    overflow: hidden;
    .count{
      font-size: 14px;
      position: relative;
      right: 43px;
      top: 223px;
    }
    .countText{
      position: absolute;
      display: inline-block;
      color: #F5496D;
    }
  }
  .evaluateLabel{
    float: left;
    width: 60%;
    height: 259px;
    resize: none;
    position: relative;
  }
  Input{
    width: 601px;
    height: 259px;
    bottom: 1px solid #CFDCE7;
  }
  .confirm{
    margin: 50px 0 30px 66%;
  }
}

</style>



