<template>
  <div>
    <panel-title :crumbs="[{name:'我的作业',route:{path:'/UserHomework'}},{name:subTitle,route:''}]" />
    <default-show v-if="data.length!=0"></default-show>
    <Card dis-hover>
      <div class="testPaper">
        <h3>{{choiceQues.periodname}}</h3>
        <!-- <p>学生：{{studentname}}</p> -->
        <!--分数表-->
        <table border="1" class="mg-bottom-20">
          <thead style="background-color: #f5faff;">
             <tr style="height:40px;">
              <th>题号</th>
              <th v-for="(val,key,index) in getQuesNum(choiceQues.subject)">第{{index==0?'一':(index==1?'二':(index==2?'三':'四'))}}题</th>
              <th>总分</th>
             </tr>
          </thead>
          <tbody>
              <tr style="height:40px;">
                <td>分数</td>
                <td v-if="worktype==2" v-for="val in getQuesNum(choiceQues.subject)">{{totalScore(val,2)}}</td>
                <td v-else></td>
                <td v-if="worktype==2">{{choiceQues.totalscore}}</td>
                <td v-else></td>
             </tr>
          </tbody>
        </table>
       <!-- 试卷展示-->
         <div v-for='(item,k,index) in  getQuesNum(choiceQues.subject)' class="outerPaper">
           <p>
              <span>{{index==0?'一':(index==1?'二':(index==2?'三':'四'))}}、</span>
              <span>{{k=='1'?'单选题':(k=='2'?'多选题':(k=='3'?'填空题':'作文题'))}}。</span>
              <span>({{totalScore(item,1)}}分)</span>
            </p>
            <dl v-for='(i,indexs) in item' :key="i.indexs" :class="((k==1||k==2)&i.score!=i.teacherscore&worktype==2)?'red':''">
                <dt>{{indexs+1}}、{{i.name}}</dt>
                <dd>
                  <img :src="'http://'+item"  style="width:150px;height:100px;" v-for="(item,index) in i.imageurl" :key="index"/>
                </dd>
                <dd v-if="k==1||k==2">
                    <p v-for="option in i.options" v-if="worktype==2">{{option}}</p>
                    <RadioGroup v-model="singleChecked[indexs].indexs" vertical v-if="worktype==0&&k==1" class="paperSingle">
                      <Radio :label="option.slice(0,1)" v-for="(option,radiolist) in i.options" :key="radiolist">{{option}}</Radio>
                    </RadioGroup>
                    <CheckboxGroup v-model="largeChecked[indexs].indexs" v-if="worktype==0&&k==2" class="paperCheckbox">
                      <Checkbox  :label="option.slice(0,1)" v-for="(option,checklist) in i.options" :key="checklist">{{option}}</Checkbox>
                    </CheckboxGroup>
                </dd>
                <!--<dd v-if="i.analysis">解析：{{i.analysis}}</dd>-->
                <dd v-if="k==3&&worktype!=2">
                  <Input style="width: 400px;margin-left: -20px;" v-model="editPaper[indexs].indexs"/>
                </dd>
                <dd v-if="k==4&&worktype!=2">
                  <Input style="width: 400px;margin-left: -20px;" type="textarea" :rows="10" v-model="editArticle[indexs].indexs"/>
                </dd>
                <dd>
                  <div v-if="worktype==2">
                    <p v-if="i.analysis">解析：{{i.analysis}}</p>
                    <p>学生答案：{{i.answers}}</p>
                    <p v-if="k!=4">正确答案：{{i.correctanswer}}</p>
                    <div class="grade" v-if="worktype==2">
                      <p>老师评分：<span>{{i.teacherscore}}分</span></p>
                      <p v-if="k==4">老师评语：<span>{{i.comment||'无'}}</span></p>
                    </div>
                  </div>
                </dd>
            </dl>
          </div>
          <div class="subWork" v-if="worktype!=2" @click="submitHomework()">提交作业</div>
        </div>
    </Card>
  </div>
</template>
<script>
import { Card,Button,Input,Form,RadioGroup,Radio,CheckboxGroup,Checkbox} from "iview";
import panelTitle from "@/components/panelTitle";
import defaultShow from "@/components/defaultShow";
import Server from '@/service/request';
import Path from '@/service/Path';
export default {
  name: "homeworkDetail",
  components: {panelTitle,Card,Button,Input,Form,RadioGroup,Radio,CheckboxGroup,Checkbox},
   data(){
    return {
          singleChecked:[],
          // singleChecked:[{0:''},{1:''}],
          largeChecked:[],
          // largeChecked:[{0:[]},{1:[]}],
          editPaper:[],
          // editPaper:[{0:''},{1:''}],
          editArticle:[],
          // editArticle:[{0:''},{1:''}],
          // editArticle:'',
          subTitle:'',
          isRead:false,
          data:[],
          worktype:'',
          lessonid:'',
          classid:'',
          choiceQues:{},
    }
  },
  methods: {
     //获取题号
    getQuesNum(obj){
      for( let k in obj){
        if(obj[k].length==0) {
          delete obj[k]
        }
      }
      return obj;
    },
     //计算满分
    totalScore(arr,type){
      let sum = 0;
      arr.forEach(item=>{
        if(type==1){
          sum += parseInt(item.score);
        }else {
          sum += parseInt(item.teacherscore);
        }
      })
      return sum;
    },
    // 获取未完成题目列表
    getQuesList(){
      Server.post(
        Path.getQuestionList,
        {
          'lessonid':this.lessonid,
          'classid':this.classid
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.choiceQues = data
            // 遍历单选题
            if(this.choiceQues.subject[1].length>0){
              var singleNum = this.choiceQues.subject[1].length;
              for(var i=0;i<singleNum;i++){
                this.singleChecked.push({i:''})
              }
            }
            // 遍历多选题
            if(this.choiceQues.subject[2].length>0){
              var checkNum = this.choiceQues.subject[2].length;
              for(var i=0;i<checkNum;i++){
                this.largeChecked.push({i:''})
              }
            }
            // 遍历填空题
            if(this.choiceQues.subject[3].length>0){
              var editNum = this.choiceQues.subject[3].length;
              for(var i=0;i<editNum;i++){
                this.editPaper.push({i:''})
              }
            }
            // 遍历作文题
            if(this.choiceQues.subject[4].length>0){
              var editNum = this.choiceQues.subject[4].length;
              for(var i=0;i<editNum;i++){
                this.editArticle.push({i:''})
              }
            }
          } else {
            
          }
        }
      );
    },
    // 获取已完成题目列表
    getCompleteQuesList(){
      Server.post(
        Path.getCompleteQuestionList,
        {
          'lessonid':this.lessonid,
          'classid':this.classid
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.choiceQues = data
          } else {
            
          }
        }
      );
    },
    // 提交作业
    submitHomework(){
      var answerArr = []
      var singleArr = []
      var largeArr = []
      var editArr = []
      var articleArr = []
      this.singleChecked.forEach((item,index)=>{
        singleArr.push(item.indexs)
      })
      this.largeChecked.forEach((item,index)=>{
        largeArr.push(item.indexs)
      })
      this.editPaper.forEach((item,index)=>{
        editArr.push(item.indexs)
      })
      this.editArticle.forEach((item,index)=>{
        articleArr.push(item.indexs)
      })
      for(var j=1;j<=4;j++){
        if(this.choiceQues.subject[j]){
          if(j==1){
            this.choiceQues.subject[j].forEach((item,index)=>{
              answerArr.push({
                "subjectid": item.subjectid,
                "answer": singleArr[index]
              })
            })
          }else if(j==2){
            this.choiceQues.subject[j].forEach((item,index)=>{
              answerArr.push({
                "subjectid": item.subjectid,
                "answer": largeArr[index]
              })
            })
          }else if(j==3){
            this.choiceQues.subject[j].forEach((item,index)=>{
              answerArr.push({
                "subjectid": item.subjectid,
                "answer": editArr[index]
              })
            })
          }else if(j==4){
            this.choiceQues.subject[j].forEach((item,index)=>{
              answerArr.push({
                "subjectid": item.subjectid,
                "answer": articleArr[index]
              })
            })
            // answerArr.push({
            //   "subjectid": this.choiceQues.subject[j][0].subjectid,
            //   "answer": this.editArticle
            // })
          }
          
        }
      }
      // 将多选题答案转为字符串
      answerArr.forEach((item,index)=>{
        if(typeof(item.answer)=='object'){
          item.answer.sort()
          item.answer = item.answer.join(',')
        }
        if(!item.answer){
          item.answer = ''
        }
      })
      // console.log(999,answerArr)
      // return false;
      Server.post(
        Path.submitQuestions,
        {
          "answers": answerArr,
          "classid": this.classid,
          "lessonid": this.lessonid,
          "homeworkid": this.choiceQues.homeworkid
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.$router.push('/UserHomework')
          } else {
            this.$Message.error(res.info)
          }
        }
      );
    }
  },
  created() {
    this.studentname = JSON.parse(window.localStorage.studentinfo).nickname
    let worktype = this.$route.query.worktype;//0 未完成  2 已批阅
    let lessonid = this.$route.query.lessonid;
    let classid = this.$route.query.classid;
    this.worktype = worktype;
    this.lessonid = lessonid;
    this.classid = classid;
    if(this.$route.query.worktype==0){
      this.subTitle = '未完成'
      this.getQuesList()
    }else if(this.$route.query.worktype==2){
      this.subTitle = '已批阅'
      this.getCompleteQuesList()
    }
  },
 mounted(){},
 computed:{},
 watch:{
 }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
table{
  border-collapse:collapse;
  width:750px;
  text-align: center;
  font-size:16px;
}
.red{
  color:red;
}
.testPaper{
    width:750px;
    margin:0 auto;
    >h3{
        text-align:center;
        line-height:45px;
        margin-bottom: 30px;
    }
    >p{
        text-align:right;
        line-height:40px; 
    }
    dt{
      text-indent:1rem;
      line-height:40px;
    }
    dd{
      text-indent:2rem;
      line-height:30px;
    }
    .grade{
      color:#888;
      font-size:16px;
      line-height:40px;
    } 
    .paperCheckbox{
      width: 40%;
    }
    .paperSingle{
      margin-left: -33px;
    }
    .outerPaper{
      margin-top: 10px;
    }
    .subWork{
      width: 161px;
      height: 45px;
      line-height: 45px;
      background-color: #3F9CE8;
      color: #fff;
      font-size: 16px;
      text-align: center;
      margin: 30px 0 30px 146px;
      cursor: pointer;
    }
}
</style>



