<template>
  <div id="LessonDetails">
    <!-- 我的课程 课时详情/录播课-->
    <panel-title :crumbs="[{name:$t('Main.Common.MyCourse'),route:{path:'/ArrangingLesson'}},{name:'课时详情',route:''}]" v-if="type==2"/>
    <panel-title :crumbs="[{name:$t('Main.Common.MyCourse'),route:{path:'/ArrangingLesson'}},{name:'录播课',route:''}]" v-if="type==1"/>
    <div class="nm_MainArea">
      <div class="cName" v-if="type==2">{{courser.classname}}</div>
      <div class="evaluation-item">
        <div class="eval-header-img fl">
          <img :src="courser.imageurl||''" alt="" @error="defaultImg">
        </div>
        <div class="eval-content">
          <ul>
            <li>
              <div class="fl eval-name">
                {{courser.coursename}}
              </div>
            </li>
            <li>
              <div class="fl eval-time">
                {{courser.subhead}}
              </div>
            </li>
            <li>
              <div class="fl eval-class-type">
                主讲老师：{{courser.teachername}}
              </div>
            </li>
            <li class="TotalPrice">
              <div class=" eval-class-type">
                总价：
                <span>{{$t('Main.Common.MoneyLogo')}}{{courser.price}}</span>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="lessonInfo">
        <span>课时安排</span>
      </div>
      <default-show v-if="unit.length==0" style="height:520px"></default-show>
      <div v-for="(item,index) in unit" class="courseList" v-else :key="index">
        <p class="pageTitle">{{item.unitname}}</p>
        <ul class="lessonDetail">
          <li 
          v-if="type==2"
          v-for="(innerItem,innerIndex) in item.period"
          :key="innerIndex"
          :class="{test:itemIndex==index&&itemInnerIndex==innerIndex}"
          @mouseenter="enter(index,innerIndex)"
          @mouseleave="enter(null,null)">
            <Icon type="android-arrow-dropright-circle" class="playIcon" :class="{test:itemIndex==index&&itemInnerIndex==innerIndex}" @mouseenter="enter(index,innerIndex)"></Icon>
            <span :class="{textTest:itemIndex==index&&itemInnerIndex==innerIndex}" @mouseenter="enter(index,innerIndex)">{{innerItem.periodname}}</span>
            <!-- 上课时间： -->
            <span :class="{textTest:itemIndex==index&&itemInnerIndex==innerIndex}" @mouseenter="enter(index,innerIndex)">{{$t('Main.Common.LessonTime')}}{{innerItem.starttime}}</span>
          </li>
            <li 
            v-if="type==1"
            style="cursor:pointer"
            v-for="(innerItem,innerIndex) in item.period"
            :key="innerIndex"
            @click="playDetail(innerItem.lessonsid)"
            :class="{test:itemIndex==index&&itemInnerIndex==innerIndex}"
            @mouseenter="enter(index,innerIndex)"
            @mouseleave="enter(null,null)">
              <Icon type="android-arrow-dropright-circle" class="playIcon" :class="{test:itemIndex==index&&itemInnerIndex==innerIndex}" @mouseenter="enter(index,innerIndex)"></Icon>
              <span :class="{textTest:itemIndex==index&&itemInnerIndex==innerIndex}" @mouseenter="enter(index,innerIndex)">{{innerItem.periodname}}</span>
              <span></span>
            </li>
        </ul>
      </div>
    </div>
    <!-- <Card style="overflow: hidden;
      padding:0px 26px 0px 16px;
      margin-bottom:30px;
      ">
      <div class="lessonPage">
        简介：
        <div class="lessonTitle">{{$t('Main.ArrangeLesson.Desc')}}</div>
        <div class="lessonContent">{{courser.generalize}}</div>
      </div>
    </Card> -->
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import CardTitle from "@/components/common/CardTitle/CardTitle";
import right from "@/assets/images/right.png";
import bookImg from "@/container/InstitutionalSchedule/LessonList/assets/bookImg.png";
import { Card, Row, Col, Button, Icon, Avatar } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultShow from "@/components/defaultShow";
import defaultClass from "@/assets/images/defaultClass.png";
export default {
  name: "LessonDetails",
  components: {
    panelTitle,
    right,
    Card,
    Row,
    Col,
    pagination,
    bookImg,
    CardTitle,
    Button,
    Icon,
    Avatar,
    defaultShow,
    defaultClass
  },
  data() {
    return {
      right,
      total: 100,
      current: 1,
      bookImg,
      itemIndex:null,
      itemInnerIndex: null,
      schedulingid: '',
      curriculumid:'',
      type:'',
      courser:{},
      unit:[]
    };
  },
  created() {
    this.getLessonData();
  },
  methods: {
    // 设置默认图片
    defaultImg(e){
      e.target.src = defaultClass;
    },
    //获取课时安排
    getLessonData(){
      let schedulingid = this.$route.query.schedulingid;
      let curriculumid = this.$route.query.curriculumid;
      let type = this.$route.query.type;
      this.schedulingid = schedulingid;
      this.curriculumid = curriculumid;
      this.type = type;
      Server.post(Path.getClassSchedule,{
        'coursetype':this.type,
        'schedulingid':this.schedulingid||'',
        'courseid':this.curriculumid
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.courser = data.courser;
          this.unit = data.unit
          if(!data.unit){
            this.unit.length=0
          }
          if(!data.courser){
            this.courser = {}
          }
          
        }else{

        }
      })
    },
    // 鼠标移入移出效果控制
    enter(index,innerIndex){
      this.itemIndex = index;
      this.itemInnerIndex = innerIndex;
    },
    //进入录播视频页面
    playDetail(lessonsid){
      this.$router.push({
        path:'/playVideo',
        query:{
          ordernum:this.ordernum,
          curriculumid:this.curriculumid,
          type:this.type,
          lessonsid:lessonsid
        }
      })
    }
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
#LessonDetails{
  padding-bottom: 5px;
  .nm_MainArea{
    padding: 60px 80px 30px 60px;
    // 课程名称
    .cName{
      color: #4D4D4D;
      font-size: 16px;
      border-bottom: 1px solid rgba(0,0,0,0.1);
      width: 100%;
      margin: -38px 50px 36px -20px;
      padding-bottom: 8px;
    }
    // 课程详情
    .evaluation-item {
      margin-top: -15px;
      margin-left: -20px;
      // 课程封面
      .eval-header-img {
        margin-top: -10px;
        width: 220px;
        height: 130px;
        margin-right: 20px;
        border-radius: 6px;
        overflow: hidden;
        margin-right: 30px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      // 课程详细信息
      .eval-content{
        ul{
          li{
            overflow: hidden;
            margin-bottom: 8px;
            font-size: 14px;
            height: 24px;
            .eval-name {
              font-weight: bold;
              font-size: 20px;
              color: #3A5166;
            }
            .eval-time {
              color: #8E9DAA;
              margin-right: 79px;
              font-size: 16px;
              width: 90%;
              white-space: nowrap; 
              overflow: hidden;
              text-overflow:ellipsis;
            }
            .eval-class-type {
              font-size: 16px;
              margin-right: 40px;
            }
          }
          .TotalPrice{
            margin-left: 250px;
            span{
              color: #F04747;
            }
          }
        }
      }
    }
  }
}
// 课时安排
.lessonInfo{
  border-bottom: 1px solid rgba(0,0,0,0.1);
  font-size: 16px;
  margin: 30px 22px 36px -20px;
  color: #3a5166;
  font-weight: Medium;
  padding-bottom: 10px;
}
// 详细列表
.courseList{
  .pageTitle{
    margin-bottom: 13px;
    margin-left: 25px;
    font-size:16px;
    font-family:PingFangSC-Medium;
    color:#3F9CE8;
    margin-top: 20px;
  }
  &:last-child ul{
    border-bottom: none;
  }
  .lessonDetail{
    // border-bottom: 1px solid #CFDCE7;
    width: 64%;
    margin-left: 25px;
    .test{
      color: #3F9CE8;
      background-color: #ECF6FE;
    }
    li{
      width:100%;
      height:48px;
      line-height: 48px;
      border-radius: 4px;
      padding-left: 28px;
      position: relative;
      color: #1D2833;
      :last-child{
        position: absolute;
        right: 20px;
        font-size: 14px;
      }
      .textTest{
        color: #3F9CE8;
      }
      .playIcon{
        color: #8e9daa;
        font-size: 18px;
        margin-right: 20px;
        position: relative;
        top: -17px;
      }
      span:first-of-type{
        display: inline-block;
        max-width: 49%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}
// 课程简介
// .lessonPage{
//   margin: 2px 0 3px 0;
//   font-family: PingFang SC;
//   font-size: 16px;
// }
// .lessonTitle{
//   color: #3A5166;
//   font-weight: Medium;
// }
// .lessonContent{
//   color: #565E66;
//   font-size: 14px;
//   font-weight: Regular;
//   margin-left: 53px;
//   margin-top: -21px;
//   padding-right: 42px;
// }
</style>



