<template>
  <div>
    <panel-title :crumbs="[{name:'精彩瞬间',route:''}]" />
    <default-show v-if="data1.length==0"></default-show>
    <div class="outerTable" v-if="data1.length!=0">
      <Card dis-hover style="height:65px;border-radius:8px 8px 0 0;">
        <template>
          <span class='fl-text'>输入搜索：</span>
          <Input
            icon="ios-search"
            placeholder="关键字"
            style="width: 365px"
            v-model="keywords"
          />
        </template>
      </Card>
      <template>
        <i-table ref="selection" border :columns="columns1" :data="data1" v-if="data1.length!=0"></i-table>
      </template>
    </div>
    <template>
      <pagination :total="total1" class="mg-top-20" :current-page='current1' @pagechange="pagechange1" :display='display1' v-if='data1.length!=0'></pagination>
    </template>
    <!-- 消息弹框 -->
    <!-- title="温馨提示" -->
    <Modal v-model="CancelInfo">
      <div slot="header" class="nm_headModal">温馨提示</div>
    <!-- 确认删除该消息吗？ -->
      <div class="nm_bodyModal">
        {{$t('Main.UserMessage.DelContent')}}
      </div>
      <div slot="footer" class="nm_footerModal">
        <!-- 取消 -->
        <Button type="text" @click="cancel()">{{$t('Main.Common.CancelBtn')}}</Button>
        <!-- 确定 -->
        <Button @click="ok()" shape="circle" class="confirm">{{$t('Main.Common.SaveBtn')}}</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import defaultShow from "@/components/defaultShow";
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import pagination from "@/components/common/pagination";
  import {
    Input,
    Card,
    Table,
    Modal,
    Form,
    Col,
    Button,
    Row,
  } from "iview";
  export default {
    name: "OverView",
    components: {
      panelTitle,
      defaultShow,
      Card,
      Modal,
      Form,
      Col,
      Button,
      Row,
      pagination,
      "i-table": Table,
      Input
    },
    data() {
      return {
        total1:1,
        current1:1,
        display1:20,
        keywords:'',
        CancelInfo: false,
        loading: false,
        columns1: [
          {
            title: '序号',
            align: "center",
            key: "number",
          },
          {
            title: '课程名称',
            align: "center",
            key: "coursename",
          },
          {
            title: '班级名称',
            align: "center",
            key: "classname",
          },
          {
            title: '课时名称',
            align: "center",
            key: "lessonname",
          },
          {
            title: '授课老师',
            align: "center",
            key: "teacher",
          },
          {
            title: '时长',
            align: "center",
            key: "length",
          },
          {
            title: '录制时间',
            align: "center",
            key: "time",
          },
          {
            title: '操作',
            key: "orderEdit",
            render: (h, params, index) => {
              return h('div',[
                h(
                  "span",
                  {
                    style: {
                      color: "#3F9CE8",
                      cursor:'pointer'
                    },
                    on: {
                      'click': () => {
                        this.$router.push('/LessonPlay')
                      }
                    },
                  },
                  // 查看
                  this.$t('Main.UserStar.Check')
                ),
                h(
                  "span",
                  {
                    style: {
                      color: "#FFA400",
                      marginLeft:'30px',
                      cursor:'pointer'
                    },
                    on: {
                      'click': () => {
                        
                      }
                    },
                  },
                  // 删除
                  this.$t('Main.UserMessage.SingleDel')
                ),
              ])

            },
            align: "center"
          }
        ],
        data1: [
          {
            number:1,
            coursename:'213',
            classname:'34',
            lessonname:'213',
            teacher:'34',
            length:'213',
            time:'34',
          }
        ]
      }
    },
    created() {

    },
    methods:  {
      ok(){
        this.deleteMessage()
        this.CancelInfo = false;
      },
      cancel(){
        this.CancelInfo = false;
      },
      pagechange1(val){
        this.current1 = val;
        window.scrollTo(0,0)
      },
      // 转换日期格式
      toDate(str) {
        let date = new Date(str*1000);
        let y = date.getFullYear(),
            m = (date.getMonth() + 1 < 10 ? `0${date.getMonth()+1}` : date.getMonth() + 1),
            d = (date.getDate() < 10 ? `0${date.getDate()}` : date.getDate());
        return `${y}-${m}-${d}`;
      },
      timetrans(date){
        var date = new Date(date*1000);//如果date为10位不需要乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes())
        var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
        // return Y+M+D+h+m+s;
        return Y+M+D+h+m;
      },
    }
  }

</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
.commonSize{
  font-size: 14px;
}
.confirm{
  background-color: #FDB44D;
  color: #fff;
  border:none;
  width: 56px;
  height: 32px;
}
.outerTable{
  border-radius: 8px;
  overflow: hidden;
}
</style>



