<template>
  <div id="">
    <!-- 我的课程 课时安排 -->
    <panel-title :crumbs="[{name:'我的课表',route:{path:'/'}},{name:'已结束',route:{path:'/LessonList',query:{date:date,type:type}}},{name:'课时详情',route:''}]" v-if="type==0"/>
    <panel-title :crumbs="[{name:'我的课表',route:{path:'/'}},{name:'今日课表',route:{path:'/LessonList',query:{date:date,type:type}}},{name:'课时详情',route:''}]" v-if="type==1"/>
    <panel-title :crumbs="[{name:'我的课表',route:{path:'/'}},{name:'待上课',route:{path:'/LessonList',query:{date:date,type:type}}},{name:'课时详情',route:''}]" v-if="type==2"/>
    <Card dis-hover class="outCard">
      <div class="cName">{{courseDetail.coursename}}</div>
      <Row>
        <Col span="24" style="padding: 0 0px;">
          <!-- <div class="evaluation-box"> -->
            <div class="evaluation-item">
              <div class="eval-header-img fl">
                <img :src="courseDetail.imageurl||''" alt="" @error="defaultImg">
              </div>
              <div class="eval-content">
                <ul>
                  <li>
                    <div class="fl eval-name">
                      {{courseDetail.periodname}}
                    </div>
                  </li>
                  <li style="margin-top;10px">
                    <div class="fl eval-time">
                      <span>{{courseDetail.subhead}}</span>
                    </div>
                  </li>
                  <li>
                    <div class="fl eval-class-type teaName">
                      主讲老师：{{courseDetail.teachername}}
                    </div>
                    <div class="fl eval-class-type">
                      <!-- 班级名称： -->
                      {{$t('Main.Common.ClassName')}}{{courseDetail.gradename}}
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <!-- 评价 -->
            <div class="nm_commonBtn nm_confirmBtn state-button"  v-if="buttonstatus==2" @click="handleEvaluate">评价</div>
          <!-- </div> -->
        </Col>
      </Row>
      <div v-if="buttonstatus==2||buttonstatus==3" class="outDetail">
        <!-- <div class="lessonInfo">
          <img src="../assets/icon-lessoninfo.png">
          <span>课程简介</span>
        </div>
        <p class="commonSet lessonName">
          <span class="lessonTitle">课程名称：</span>
          <span class="lessonTitle">{{courseDetail.coursename}}</span>
        </p>
        <div class="subHead">{{courseDetail.subhead}}</div>
        <div class="lessonPage commonSet">
          <div class="lessonTitle">课程简介：</div>
          <div class="lessonContent">{{courseDetail.generalize}}</div>
        </div> -->
        <div v-if="buttonstatus==2||buttonstatus==3">
          <div v-if="Object.keys(homeworkList) != 0">
            <div class="lessonInfo">
              <span>作业情况</span>
            </div>
            <div class="homework">
              {{homeworkList.periodname}}
              <div class="write" @click="writeHomework" v-if="homeworkList.issubmited==0">写作业</div>
              <div class="write" v-else>已完成</div>
            </div>
          </div>
          <div class="lessonInfo" v-if="ifChrome">
            <span>课时回放</span>
          </div>
          <div v-if="ifChrome">
            <div id="LessonPlay">
              <Card class='cardP0 mg-bottom-20' dis-hover>
                <div>
                  <div class="playArea" v-if="!playFlag">
                    <div class="nonPlay">
                      <img src="@/assets/images/replay@2x.png" alt="">
                    </div>
                    <!-- <div class="playText">课程回放制作中</div> -->
                    <div class="playText">{{$t('Main.LessonPlay.PlayText')}}</div>
                  </div>
                  <div  v-else class="haveReplay">
                    <div class="replayArea">
                      <img :src= stop class="stopimg" @click="play" v-show="imgflag">
                      <img :src="defaultCover" alt="" class="defaultPlay" v-show="imgflag">
                      <iframe name ="video" :src="playpath" width="100%" height="630px" frameborder="0" class="innerBody"></iframe>
                    </div>
                    <div class="menuList">
                      <div class="dash"></div>
                      <Menu :theme="theme3" active-name="1" :active-key="index" v-for="(item,index) in rePlayData.video" :key="index">
                        <span @click="videoShow(index)" class="listTest" :class="{'replaySpan': index===checkindex}">
                          <span class="littleCircle" :class="{'replay-active': index ===checkindex }">●</span>
                          <!-- 片段 -->
                          <span style="margin-left: 10px;" class="playTitle" :class="{'replay-active': index ===checkindex }">{{$t('Main.LessonPlay.Part')}}{{index+1}}</span> 
                          <span style="margin-left:20px;" class="playTitle" :class="{'replay-active': index ===checkindex }">{{item.duration}}</span>
                        </span>
                      </Menu>
                    </div>
                  </div>
                </div>  
              </Card>
            </div>
          </div>
          <div class="lessonInfo" v-if="commemtList.length>0">
            <span>老师点评</span>
          </div>
          <div class="mg-bottom-20" v-for="(item,index) in commemtList" :key="index">
            <div class="person-avatar">
              <img :src="item.imageurl||''" class="avatar" @error="setdefaultAvatar">
            </div>
            <div class="preson-content">
              <div class="content-main">
                <h3>{{item.teachername}}</h3>
                <Rate allow-half disabled class="rate" :value.sync="item.score">
                  <!-- <span class="rate-srore">{{item.score}}分</span> -->
                </Rate>
              </div>
              <p class="content">{{item.comment}}</p>
              <p class="ratting-time">{{item.addtime}}</p>
            </div>
          </div>
        </div>
      </div>
    </Card>
    
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import CardTitle from "@/components/common/CardTitle/CardTitle";
import right from "@/assets/images/right.png";
import bookImg from "@/container/InstitutionalSchedule/LessonList/assets/bookImg.png";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultShow from "@/components/defaultShow";
import defaultClass from "@/assets/images/defaultClass.png";
import pagination from "@/components/common/pagination";
import { Card, Row, Col, Button, Icon, Avatar, Rate, Input } from "iview";
import defaultCover from "@/assets/images/defaultReplay.png";
import stop from "@/assets/images/iconstop.png";
import defaultAvatar from "@/assets/images/lemonPic.png";
export default {
  name: "",
  components: {
    panelTitle,
    right,
    Card,
    Row,
    Col,
    bookImg,
    CardTitle,
    Button,
    Icon,
    Avatar,
    defaultShow,
    defaultClass,
    pagination,
    Rate,
    Input,
    defaultCover,
    stop
  },
  data() {
    return {
      right,
      bookImg,
      defaultAvatar,
      data: {},
      itemIndex: null,
      itemInnerIndex: null,
      ordernum: "",
      schedulingid: "",
      valueDisabled: 2,
      value6: "",
      paramsData: {},
      date: "",
      playFlag: true,
      theme3: "light",
      playpath: "",
      checkindex: "",
      rePlayData: {},
      defaultCover,
      defaultClass,
      stop,
      imgflag: true,
      type: "",
      courseDetail:{},
      buttonstatus:'',
      ifChrome: false,
      commemtList:[],
      homeworkList:{}
    };
  },
  created() {
    let type = this.$route.query.type;
    let date = this.$route.query.paramsdate;
    let buttonstatus = this.$route.query.buttonstatus;
    this.type = type;
    this.date = date;
    this.buttonstatus = buttonstatus;
    // 获取课程详情信息
    this.courseDetail = JSON.parse(window.localStorage.courseInfo);
    this.getReplay();
    this.getTeacherEvaluate();
    this.getHomework();
    // 判断是否是谷歌浏览器
    var ua = navigator.userAgent.toLocaleLowerCase();
    if(ua.match(/tencenttraveler/) != null || ua.match(/qqbrowse/) != null){
      this.ifChrome = false;
    }else if (ua.match(/firefox/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/ubrowser/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/opera/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/bidubrowser/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/metasr/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/msie/) != null || ua.match(/trident/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/maxthon/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/chrome/) != null) {
      var is360 = _mime("type", "application/vnd.chromium.remoting-viewer");
      function _mime(option, value) {
        var mimeTypes = navigator.mimeTypes;
        for (var mt in mimeTypes) {
          if (mimeTypes[mt][option] == value) {
            return true;
          }
        }
        return false;
      }
      if(is360){
        this.ifChrome = false;
      }else{
        this.ifChrome = true;
      }
    }else{
      this.ifChrome = false;
    }
  },
  methods: {
    // 设置默认课程封面
    defaultImg(e) {
      e.target.src = defaultClass;
    },
    // 设置老师默认头像
    setdefaultAvatar(e) {
      e.target.src = defaultAvatar;
    },
    // 点击播放按钮
    play() {
      this.checkindex = 0;
      this.playpath = this.rePlayData.video[0].playpath;
      this.imgflag = !this.imgflag;
    },
    // 点击播放列表
    videoShow(index) {
      this.checkindex = index;
      this.playpath = this.rePlayData.video[index].playpath;
      this.imgflag = false;
    },
    // 获取回放信息
    getReplay(){
      if(this.buttonstatus==0||this.buttonstatus==1){
        return false
      }
      Server.post(Path.getLessonsPlayback,{
        'toteachid': this.courseDetail.toteachid
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          if(data.length==0){
            this.playFlag = true;
          }else{
            this.rePlayData = data
          }
        }else{
          // this.$Message.info(res.info);
          this.playFlag = false;
          this.rePlayData = {}
        }
      })
    },
    // 写作业
    writeHomework(){
      this.$router.push({path:'/UnDone',query:{worktype:0,lessonid:this.courseDetail.lessonsid,classid:this.courseDetail.schedulingid}})
    },
    // 评价
    handleEvaluate(){
      window.localStorage.removeItem('evaluate');
      window.localStorage.setItem('evaluate',JSON.stringify(this.courseDetail));
      this.$router.push({path:'/LessonEvaluate',query:{paramsdate:this.date,flag:2}})
    },
    // 获取老师点评
    getTeacherEvaluate(){
      Server.post(Path.getteachercomment,{
        'lessonsid':this.courseDetail.lessonsid
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.commemtList = data;
        }else{
          this.$Message.info(res.info);
        }
      })
    },
    // 获取作业信息
    getHomework(){
      Server.post(Path.gethomeworkbylessionid,{
        'lessonsid':this.courseDetail.lessonsid
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.homeworkList = data
        }else{
          this.$Message.info(res.info);
        }
      })
    },
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
// 课程详情
.outCard{
  overflow: hidden;
  padding:37px 80px 8px 46px;
  margin-bottom:20px;
  // 课程名称
  .cName{
    color: #4D4D4D;
    font-size: 16px;
    border-bottom: 1px solid rgba(0,0,0,0.1);;
    width: 100%;
    margin: -38px 0 36px -20px;
    padding-bottom: 8px;
  }
  .evaluation-item {
    margin-top: -15px;
    margin-left: -20px;
    // 课程封面
    .eval-header-img {
      margin-top: -10px;
      width: 220px;
      height: 130px;
      margin-right: 20px;
      border-radius: 6px;
      overflow: hidden;
      margin-right: 30px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    // 课程详细信息
    .eval-content{
      ul li {
        overflow: hidden;
        margin-bottom: 4px;
        font-size: 14px;
        height: 36px;
        .eval-class-type,.eval-name,.eval-time{
          overflow:hidden; 
          text-overflow:ellipsis; 
          white-space:nowrap;
        }
        .eval-class-type {
          margin-right: 40px;
          color: #7a8599;
          font-size: 16px;
        }
        .teaName{
          max-width: 36%!important;
        }
        .eval-name {
          font-weight: bold;
          font-size: 20px;
          color: #525866;
          max-width: 94%;
        }
        .eval-class-type{
          max-width: 50%;
        }
        .eval-time {
          width: 77%;
          color: #8e9daa;
          font-size: 15px;
        }
      }
    }
  }
  .state-button{
    position: absolute;
    right: 60px;
    top: 10px;
  }
  .outDetail{
    margin-left: -20px;
    width: 100%;
    .lessonInfo {
      border-bottom: 1px solid rgba(0,0,0,0.1);
      font-size: 16px;
      margin-bottom: 29px;
      margin-top: 30px;
      color: #3a5166;
      font-weight: Medium;
      padding-bottom: 10px;
    }
    // 课时回放
    #LessonPlay{
      // 课程回放制作中
      .playArea {
        width: 900px;
        height: 354px;
        margin: 0px auto;
        background-color: #f3f3f3;
        line-height: 354px;
        position: relative;
        .nonPlay {
          width: 116px;
          height: 180px;
          margin: 0 auto;
          padding-top: 52px;
        }
        .playText {
          position: absolute;
          left: 395px;
          top: 73px;
          color: #bfbfbf;
        }
      }
      .haveReplay {
        width: 90%;
        margin: 0 auto;
        height: 668px;
        // 回放视频区域
        .replayArea{
          display: inline-block;
          width: 82%;
          float:left;
          position:relative;
          .stopimg {
            position: absolute;
            z-index: 1;
            width: 72px !important;
            height: 72px !important;
            cursor: pointer;
            margin-left: 50%;
            margin-top: 270px;
          }
          .defaultPlay{
            width:100%;
            height:99%;
            position:absolute;
          }
          .innerBody{
            border:1px #CFDCE7 solid;
          }
        }
        // 侧边回放列表
        .menuList {
          display: inline-block;
          float: left;
          width: 18%;
          height: 630px;
          background: #f3f3f3;
          border: 1px solid #cfdce7;
          border-left: none;
          box-sizing: border-box;
          margin-top: 0px;
          .dash {
            width: 1px;
            height: 626px;
            border-left: 1px dashed #cfdce7;
            position: absolute;
            margin-left: 23px;
          }
          .littleCircle {
            margin-left: 15px;
            font-size: 19px;
            color: #cfdce7;
            position: relative;
          }
          .listTest {
            height: 34px;
            line-height: 34px;
            display: inline-block;
            font-size: 15px;
            width: 100%;
            border-left: 2px solid #f3f3f3;
            .replay-active {
              color: #387bff;
            }
          }
          .listTest:hover {
            cursor: pointer;
          }
          .replaySpan {
            border-left: 2px solid #387bff;
            background-color: #fff;
          }
        }
      }
    }
    // 作业
    .homework {
      background-color: rgba(175, 187, 185, 0.1);
      height: 48px;
      line-height: 48px;
      padding-left: 22px;
      position: relative;
    }
    .write {
      width: 60px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      position: absolute;
      top: 20%;
      right: 30px;
      border: 1px solid #3F9CE8;
      color: #3F9CE8;
      background-color: #fff;
      border-radius: 2px;
      cursor: pointer;
    } 
    // 老师点评
    .person-avatar {
      float: left;
      height: 100%;
      margin-right: 22px;
      .avatar {
        width: 68px;
        height: 68px;
        border: rgba(56, 123, 255, 0.5) 2px solid;
        box-shadow: 0px 0px 5px rgba(140, 157, 158, 0.81);
        border-radius: 50%;
      }
    }
    .preson-content {
      overflow: hidden;
      position: relative;
      .content {
        clear: both;
        font-size: 14px;
        color: rgba(94, 98, 102, 1);
        line-height: 30px;
        padding-top: 15px;
        margin-bottom: 18px;
        padding-right: 30px;
        word-wrap: break-word;
      }
      p.ratting-time {
        position: absolute;
        top: 0px;
        right: 0px;
        font-size: 12px;
        font-family: PingFang-SC-Medium;
        color: #A6A6A6;
      }
      .content-main{
        h3 {
          float: left;
          font-size: 16px;
          color: rgba(47, 49, 51, 1);
        }
        .rate {
          margin-left: 30px;
          margin-top: -5px;
          .rate-srore {
            color: #f5496d;
            font-size: 16px;
            margin-left: -8px !important;
          }
        }
      }
    }
  }
}
// 课程简介
