<template>
  <div>
    <panel-title :crumbs="[{name:'我的套餐',route:''}]" />
      <div class="nm_TabTitle">
          <div
          v-for="(item,index) in titleContent"
          @click="tab(index)"
           :class="{nm_active:index == num}"
          >
            <span class="nm_titleText">{{item}}</span>
          </div>
        </div>
    <template>
      <div class="nm_MainArea">
          <div>
            <default-show v-if="PackageList.length==0" style="height:690px;padding-top:62px;"></default-show>
            <div
            v-else
            class="evaluation-item"
            v-for="(item,index) in PackageList"
            :class="{giftStyle:item.type==2,haveout:num==2,haveused:num==1}"
            :key="index">
              <div class="eval-header-img fl">
                <img @error="defaultImg" :src="item.setimgpath||''">
                <div class="coupon" v-if="item.type==2"><img :src="gift" alt="" class="giftImg"></div>
              </div>
              <div class="eval-content">
                <ul>
                  <li>
                    <div class="fl eval-name">
                      <span v-if="item.type==1">{{item.setmeal}}</span>
                      <span v-if="item.type==2">
                        <span v-if="item.sendlive!=0">{{item.sendlive}}课时直播课</span>
                        <span v-if="item.sendvideo!=0">{{item.sendvideo}}课时录播课</span>
                      </span>
                    </div>
                  </li>
                  <li v-if="item.type==1">
                    <div class="fl eval-time">
                      {{item.bughour}}课时
                    </div>
                  </li>
                  <li>
                    <div class="fl eval-time">
                      <span>666</span>
                    </div>
                  </li>
                  <li>
                    <div class="fl eval-time">
                      <span v-if="item.threshold!=0&&item.type==1">购买课程满{{item.threshold}}元可用</span>
                      <span v-if="item.giftthreshold!=0&&item.type==2">购买课程满{{item.giftthreshold}}元可用</span>
                    </div>
                    <div class="fl eval-time" v-if="item.type==1">
                      <span v-if="item.trialtype==1">全部课程均可使用</span>
                      <span v-if="item.trialtype==2">指定分类可使用</span>
                      <span v-if="item.trialtype==3">指定课程可使用</span>
                    </div>
                    <div class="fl eval-time" v-if="item.type==2">
                      <span v-if="item.gifttrialtype==1">全部课程均可使用</span>
                      <span v-if="item.gifttrialtype==2">指定分类可使用</span>
                      <span v-if="item.gifttrialtype==3">指定课程可使用</span>
                    </div>
                  </li>
                  <li>
                    <div class="fl eval-time">
                      有效期至：<span v-if="item.efftype==1">{{timetrans(item.effendtime)}}</span>
                      <span v-if="item.efftype==2">购买后{{item.efftime}}天</span>
                    </div>
                  </li>
                </ul>
              </div>
              <div  shape="circle" class="delButton" v-if="num==1||num==2" @click="deletePackage(item.packageuseid)">删除</div>
              <div class="restcoupon" v-if="num==1">已使用{{item.total-item.surplus}}课时，剩余{{item.surplus}}课时</div>
            </div>
            <pagination v-if="PackageList.length!=0" :total="totalClass" :display="displayClass" class="mg-top-20" :current-page='current' @pagechange="classChange" style="height:50px"></pagination>
        </div>
      </div>
    </template>
    <!-- 删除的提示弹框 -->
    <Modal v-model="deleteModal">
      <div slot="header" class="nm_headModal">温馨提示</div>
      <div class="nm_bodyModal">
        删除后将不可恢复，您确定要删除吗？
      </div>
      <div slot="footer" class="nm_footerModal">
        <!-- 确定 -->
        <div @click="ok()" class="nm_commonBtn nm_confirmBtn">{{$t('Main.Common.SaveBtn')}}</div>
        <!-- 取消 -->
        <div @click="cancel()" class="nm_commonBtn nm_cancelBtn">{{$t('Main.Common.CancelBtn')}}</div>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import pagination from "@/components/common/pagination";
  import HeaderImg from '@/container/UserCenter/assets/images/avatar_upload.png';
  import defaultShow from "@/components/defaultShow";
  import defaultClass from "@/assets/images/defaultClass.png";
  import gift from "@/assets/images/packagegift.png";
  import {
    Card,
    Row,
    Col,
    Button,
    Tabs,
    TabPane,
    Icon,
    Modal
  } from 'iview'
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default {
    name: "UserEdit",
    components: {
      Card,
      panelTitle,
      Row,
      Col,
      Button,
      Tabs,
      TabPane,
      Icon,
      pagination,
      defaultShow,
      defaultClass,
      Modal
    },
    props: {},
    data() {
      return {
        num:0,
        totalClass:null,
        current:1,
        displayClass:20,
        PackageList:[],
        classFlag:false,
        checkedClass:'',
        status:0,
        deleteModal:false,
        packageuseid:'',
        titleContent:['待使用','已使用','已过期'],
        gift
      }
    },
    created() {
      this.getPackageList(1,this.status);
    },
    methods: {
      defaultImg(e){
        e.target.src = defaultClass;
      },
      // tab切换
      tab(index){
        this.num = index;
        this.status = index;
        this.getPackageList(1,this.status);
      },
      // 分页
      classChange: function(currentPage) {
        this.current = currentPage;
        this.getPackageList(currentPage,this.status);
        window.scrollTo(0,0)
      },
      // 获取套餐列表
      getPackageList(pagenum=1,status=0){
        Server.post(Path.packageUseList,{
          'status': status,
          'pagenum': pagenum,
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              this.PackageList = [];
            }else{
              this.PackageList = data.data;
              this.totalClass = data.pageinfo.total;
            }
          }else{
            this.PackageList = [];
          }
        })
      },
      // 转换时间格式
      timetrans(date){
        var date = new Date(date*1000);//如果date为10位不需要乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes())
        var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
        // return Y+M+D+h+m+s;
        return Y+M+D;
      },
      // 删除套餐
      deletePackage(value){
        this.packageuseid = value
        this.deleteModal = true
      },
      // 确认删除
      ok(){
        Server.post(Path.deletePackageUse,{
          'packageuseid':this.packageuseid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$Message.success(res.info)
            this.getPackageList(this.currentPage,this.status);
          }else{
            this.$Message.error(res.info)
          }
        })
        this.deleteModal = false;
      },
      // 取消删除
      cancel(){
        this.deleteModal = false;
      },
    },
  }
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
.nm_MainArea{
  padding: 20px 45px 0;
  // 套餐详情
  .evaluation-item {
    overflow: hidden;
    border-bottom: 1px solid #cfdce7;
    padding-top: 14px;
    padding-bottom: 18px;
    position: relative;
    padding-left: 10px;
    border-radius: 4px;
    background-size: 108px;
    // 套餐封面
    .eval-header-img {
      margin-top: 4px;
      width: 216px;
      height: 130px;
      margin-right: 30px;
      border-radius: 6px;
      overflow: hidden;
      position: relative;
      img {
        width: 218px;
        height: 132px;
        position: absolute;
        left: -1px;
        top: -1px;
      }
      .coupon{
        position: absolute;
        top: 0px;
        left: 0px;
        font-size: 22px;
        font-weight: 600;
        width: 35px;
        height: 40px;
        .giftImg{
          width: 100%!important;
          height: 100%!important;
          overflow: hidden;
        }
      }
    }
    &:last-of-type{
      border: none;
    }
    // 背景图标
    &.haveout{
      background: url('assets/packageout@2x.png') no-repeat 80%;
      background-size: 110px;
    }
    &.haveused{
      background: url('assets/packageused@2x.png') no-repeat 80%;
      background-size: 110px;
    }
    &.giftStyle{
      border: 1px solid #FFA400!important;
      margin-top: -1px;
      box-shadow: 2px 1px 9px rgba(0,0,0,0.1);
    }
    .restcoupon{
      position: absolute;
      right: 10px;
      bottom: 10px;
      color: #FFA400;
    }
    // 删除按钮
    .delButton{
      position: absolute;
      right: 26px;
      bottom: 66px;
      color: #3F9CE8;
      width: 81px;
      height: 31px;
      line-height: 31px;
      text-align: center;
      font-size: 14px;
      border-radius: 2px;
      border: 1px solid #3F9CE8;
      cursor: pointer;
    }
    // 套餐详细信息
    .eval-content{
      position: relative;
      ul li {
        overflow: hidden;
        margin-bottom: 8px;
        font-size: 14px;
        height: 20px;
        &:nth-of-type(1){
          height: 30px;
        }
        .eval-time {
          margin-right: 50px;
          font-size: 16px;
        }
        .eval-name {
          font-weight: bold;
          font-size: 22px;
        }
      }
    }
  }
}
</style>



