<template>
  <div id="cardTitle">
    <slot></slot>
  </div>
</template>

<script>
export default {
    name: "CardTitle"
}
</script>

<style scoped>
#cardTitle {
  position: absolute;
  left: -16px;
  top: 0;
  padding: 0 0 0 68px;
  /*margin-left: -16px;*/
  width: 247px;
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  background: url(./assets/cardtitle.png) no-repeat;
  background-size: 100% 100%;
  color: #fff;
}
</style>



