<template>
  <div class="panelTitle green">
    <!--<slot></slot>-->
    <ul class="bread-crumbs">
      <router-link tag="li" v-for="(el, i) in crumbs" :key="i" :to="el.route">{{el.name}}</router-link>
    </ul>
  </div>
</template>
<script>
  export default {
    name: "panelTitle",
    props: {
      crumbs:{
        default:function(){
          return [
            {
              name:'一级',
              route:''
            }
          ]
        },
        type:Array
      }
    }
  }
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
  .panelTitle {
    font-size: 16px;
    margin: 29px 0 19px;
  }

  .bread-crumbs li {
    position: relative;
    display: inline-block;
    color: #A4ABB3;
    cursor: pointer;
  }

  .bread-crumbs li:last-of-type {
    color: #525866;
    pointer-events: none;
  }
  .bread-crumbs li:first-of-type {
    border-left: 2px solid #3F9CE8;
    padding-left: 20px;
  }
  .bread-crumbs li:after {
    margin: 0 6px;
    background-image: url('./panelTitle/images/icon_right.png');
    content: '';
    display: inline-block;
    color: #A4ABB3;
    font-weight: bold;
    width: 20px;
    height: 20px;
    background-size: 73%;
    background-repeat: no-repeat;
    background-position-y: 7px;
  }
  .bread-crumbs li:last-of-type::after{
    background-image: url('');

  }

  .bread-crumbs li:last-of-type:after {
    content: '';
  }
</style>



