<template>
  <div class="menu" id="menu">
    <Layout>
      <Sider hide-trigger :style="{background: '#fff', position: 'fixed', height: '100vh', left: 0, top: '68px', overflow: 'auto', boxShadow: '0px 10px 16px rgba(21,46,74,0.14) '}">
        <!-- <div class="sideHeader">
          <defaultHeader :sourceimg="HeaderImg"/>
        </div> -->
        <div class="person-avatar">
          <!-- <span @click="setStudentInfo()" class="setIcon"><Icon type="ios-set"></Icon></span> -->
          <defaultHeader :sourceimg="data.imageurl"/>
          <div class="nickname common">
            {{data.nickname}}
          </div>
          <div class="borderFlag"></div>
          <div class="studentGrade common">{{data.gradename||'无'}}</div>
        </div>
        <Menu ref="leftMenu" :active-name="activeName" theme="light" width="101%" :open-names="[openNames]">
            <li class="ivu-menu-item">
              <Icon type="ios-class" class="iconImg"></Icon>
              <span>我的积分</span>
              <span class="fr orange commonSize">0</span>
            </li>
            <router-link to="/UserMessage" class="ivu-menu-item" tag="li" >
              <Icon type="ios-msg" class="iconImg"></Icon>
              <!-- <span>我的消息</span> -->
              <span>{{$t('Main.UserMessage.Title')}}</span>
              <span class="fr commoncolor commonSize">{{msgCount}}</span>
            </router-link>
            <router-link to="/UserStar" class="ivu-menu-item" tag="li" >
              <Icon type="ios-starr" class="iconImg"></Icon>
              <!-- <span>我的收藏</span> -->
              <span>{{$t('Main.UserStar.Title')}}</span>
              <Icon type="chevron-right" class="fr"></Icon>
            </router-link>
            <router-link to="/UserPackage" class="ivu-menu-item" tag="li" >
              <Icon type="ios-course" class="iconImg"></Icon>
              <span>课时套餐</span>
              <Icon type="chevron-right" class="fr"></Icon>
            </router-link>
            <router-link to="/UserFeedback" class="ivu-menu-item" tag="li" >
              <Icon type="ios-feedback" class="iconImg"></Icon>
              <span>分析反馈</span>
              <div class="fr commoncolor specialLine commonSize">{{feedbackCount}}</div>
            </router-link>
            <!-- <router-link to="/UserMoment" class="ivu-menu-item" tag="li" >
              <Icon type="ios-course" class="iconImg"></Icon>
              <span>精彩瞬间</span>
              <Icon type="chevron-right" class="fr"></Icon>
            </router-link> -->
        </Menu>
      </Sider>
      <Layout :style="{padding: '0 0 0 0', marginLeft: '230px',paddingTop:'68px'}">
        <div style="padding: 0 50px;min-width: 1110px;">
          <router-view />
        </div>
      </Layout>
    </Layout>
  </div>
</template>

<script>
  import {
    Layout,
    Sider,
    Menu,
    MenuItem,
    Submenu,
    Icon
  } from "iview";
  import Routes from "@/router/Route";
  import defaultHeader from "@/components/defaultHeader";
  import HeaderImg from '@/assets/images/lemonPic.png';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default {
    name: "vmenu",
    components: {
      Layout,
      Sider,
      Menu,
      MenuItem,
      Submenu,
      Icon,
      Routes,
      defaultHeader,
    },
    data() {
      return {
        // Routes: RouteDatas
        Routes: Routes.RouteDatas,
        openNames: "MainIndex",
        activeName: "Main",
        HeaderImg,
        data:{},
        msgCount:'',
        feedbackCount:''
      };
    },
    created(){
      this.getInfo();
      this.getMessageList();
      this.getFeedbackList();
    },
    mounted(){
      this.$nextTick(r=>{
        this.$refs.leftMenu.updateOpened();
        this.$refs.leftMenu.updateActiveName();
      })
    },
    methods:{
      // 设置个人资料
      setStudentInfo(){
        this.$router.push('/UserCenter')
      },
      // 获取个人资料信息
      getInfo(){
        Server.post(Path.getStudentInfo,{
          studentid:this.$uid
        },res=>{
          // console.log(res)
          let { code, data } = res;
          if(code===0){
            this.data = data;
          }else{
            this.data = {};
          }
        });
      },
      // 获取消息列表
      getMessageList(pagenum){
        Server.post(Path.messageList,{
          "studentid": this.$uid,
          "pagenum": 1
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.msgCount = data.pageinfo.total;
            
          }else{
            this.$Message.info(res.info)
          }
        });
      },
      // 获取分析反馈列表
      getFeedbackList(){
        Server.post(
          Path.getFeedbackList,
          {
            'pagenum':1,
            'search':''
          },
          res => {
            let { code, data } = res;
            if (code === 0) {
              this.feedbackCount = data.pageinfo.total
            } else {
              
            }
          }
        );
      }
    },
    watch: {
      getHeadImg(val) {
        this.data.imageurl = val;
      },
      getName(val){
        this.data.nickname = val;
      },
      getGradename(val){
        this.data.gradename = val;
      },
      getMsgcount(val){
        this.msgCount = val;
      },
      getFeedbackcount(val){
        this.feedbackCount = val;
      },
    },
    computed: {
      getHeadImg() {
        return this.$store.store.state.headimg;
      },
      getName(){
        return this.$store.store.state.nickname;
      },
      getGradename(){
        return this.$store.store.state.gradename;
      },
      getMsgcount(){
        return this.$store.store.state.msgcount;
      },
      getFeedbackcount(){
        return this.$store.store.state.feedbackcount;
      },
    },
  };
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
/*清楚默认样式*/
.ivu-layout {
  background: none;
}
.ivu-layout-sider{
  width: 230px !important;
  min-width: 230px !important;
}
/*侧边栏背景颜色*/
.ivu-layout-sider,
.ivu-layout-sider-children,
.ivu-menu-light {
  background: #fefefe !important;
  color: #8F9BB3;
  font-size: 16px;
  // height: 100%;
}
.ivu-menu-item {
  position: relative;
  padding: 13px 30px;
  color:#8F9BB3;
  font-size: 14px;
  padding: 13.5px 24px;
  border-bottom: 1px solid #F4FAFF;
  .iconImg{
    margin-right: 24px;
  }
  &.router-link-active {
    background: #E6E6E6 !important;
  }
  .commoncolor{
    color: #3F9CE8;
  }
  .commonSize{
    font-size: 14px;
    margin-top: 3px;
  }
  .specialLine{
    border-bottom: 1px solid;
    height: 19px;
  }
}
.ivu-icon{
  color: @color1;
  width: 20px;
  height: 20px;
  font-size: 20px;
  vertical-align: middle;
  margin-top: -2px;
  text-align: center;
  color:#8F9BB3;
}
.ivu-icon-chevron-right{
  font-size: 12px;
  margin-top: 7px;
  text-align: right;
}
.ivu-icon-ios-class{
  background: url('../assets/images/score@2x.png') no-repeat center;
  background-size: 100%;
}
.ivu-icon-ios-course{
  background: url('../assets/images/package@2x.png') no-repeat center;
  background-size: 100%;
}
.ivu-icon-ios-feedback{
  background: url('../assets/images/feedback@2x.png') no-repeat center;
  background-size: 100%;
}
.ivu-icon-ios-starr{
  background: url('../assets/images/star@2x.png') no-repeat center;
  background-size: 100%;
}
.ivu-icon-ios-msg{
  background: url('../assets/images/msg@2x.png') no-repeat center;
  background-size: 100%;
}
.ivu-icon-ios-set{
  background: url('../assets/images/set@2x.png') no-repeat center;
  background-size: 100%;
}
.sideHeader{
  height: 300px;
}
.person-avatar {
    width: 230px;
    text-align: center;
    padding-bottom: 30px;
    #default-img {
      width: 96px;
      height: 96px;
      border-radius: 50%;
      border: 2px solid rgba(217,217,217,0.8);
      overflow: hidden;
      margin: 46px auto 18px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .nickname{
      font-size: 16px;
      font-weight: 600;
      color: @fz-Color1;
      border-bottom: 3px solid #3F9CE8;
      width: 36%;
      margin: 20px auto 0;
    }
    .borderFlag{
      margin: 0 auto;
      width: 22%;
      height: 10px;
      border-left: 3px solid #3F9CE8;
      border-right: 3px solid #3F9CE8;
    }
    .studentGrade{
      margin: 0 auto;
      width: 36%;
      border: 1px solid #3F9CE8;
      border-radius: 6px;
      color: #3F9CE8;
    }
    .setIcon{
      position: absolute;
      top: 20px;
      right: 25px;
      cursor: pointer;
    }
    .common{
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }
  }
</style>



