<template>
  <div>
    <!-- [{name:'我的消息',route:''}] -->
    <panel-title :crumbs="[{name:$t('Main.UserMessage.Title'),route:''}]" />
    <div class="outerTable">
      <Card dis-hover style="padding-bottom:40px;">
        <div>
          <!-- 批量删除 -->
          <div class="totalDelete" @click="totalDelete">{{$t('Main.UserMessage.Del')}}</div>
        </div>
        <div class="outTable">
          <i-table :loading="loading" stripe ref="selection" :columns="columns" :data="data" @on-selection-change="changeChose"></i-table>
        </div>
        <template>
          <pagination :total="total" class="mg-top-20" :current-page='current' @pagechange="pagechange" :display='display' v-if='data.length!=0'></pagination>
        </template>
      </Card>
    </div>
    <!-- 删除的提示弹框 -->
    <!-- title="温馨提示" -->
    <Modal v-model="CancelInfo">
      <div slot="header" class="nm_headModal">温馨提示</div>
      <div class="nm_bodyModal">
        删除后将不可恢复，您确定要删除吗？
      </div>
      <div slot="footer" class="nm_footerModal">
        <!-- 确定 -->
        <div @click="ok()" class="nm_commonBtn nm_confirmBtn">{{$t('Main.Common.SaveBtn')}}</div>
        <!-- 取消 -->
        <div class="nm_commonBtn nm_cancelBtn" @click="cancel()">{{$t('Main.Common.CancelBtn')}}</div>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import defaultShow from "@/components/defaultShow";
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import pagination from "@/components/common/pagination";
  import {
    Card,
    Table,
    Modal,
    Form,
    Col,
    Button,
    Row,
  } from "iview";
  export default {
    name: "OverView",
    components: {
      panelTitle,
      defaultShow,
      Card,
      Modal,
      Form,
      Col,
      Button,
      Row,
      pagination,
      "i-table": Table,
    },
    data() {
      return {
        total:1,
        current:1,
        display:20,
        checkInfo: false,
        newInfo: true,
        CancelInfo: false,
        loading: false,
        messageId: '',
        delType: '',
        columns: [
          {
            type: "selection",
            width: 60,
            align: "center"
          },
          {
            // title: "发送时间",
            title: this.$t('Main.UserMessage.SendTime'),
            align: "center",
            key: "addtime",
          },
          {
            // title: "内容",
            title: this.$t('Main.UserMessage.SendContent'),
            align: "center",
            key: "content",
            render: (h,params,index)=>{
              // 【   】
              return h('div',this.$t('Main.UserMessage.SingleLeft')+params.row.title+this.$t('Main.UserMessage.SingleRight')+params.row.content)
            }
          },
          {
            // title: "操作",
            title: this.$t('Main.UserMessage.Options'),
            key: "orderEdit",
            render: (h, params, index) => {
              return h('div',[
                h(
                  "span",
                  {
                    style: {
                      color: "#FFA400",
                      // marginLeft:'60px',
                      cursor:'pointer'
                    },
                    on: {
                      'click': () => {
                        this.delType = 1;
                        this.messageId = params.row.id;
                        this.CancelInfo = true;
                      }
                    },
                  },
                  // 删除
                  this.$t('Main.UserMessage.SingleDel')
                ),
              ])

            },
            align: "center"
          }
        ],
        data: []
      }
    },
    created() {
      this.getMessageList(1);
    },
    methods:  {
      // 确认删除
      ok(){
        this.deleteMessage()
        this.CancelInfo = false;
      },
      // 取消删除
      cancel(){
        this.CancelInfo = false;
      },
      // 翻页
      pagechange(val){
        this.current = val;
        this.getMessageList(val)
        window.scrollTo(0,0)
      },
      // 转换日期格式
      toDate(str) {
        let date = new Date(str*1000);
        let y = date.getFullYear(),
            m = (date.getMonth() + 1 < 10 ? `0${date.getMonth()+1}` : date.getMonth() + 1),
            d = (date.getDate() < 10 ? `0${date.getDate()}` : date.getDate());
        return `${y}-${m}-${d}`;
      },
      timetrans(date){
        var date = new Date(date*1000);//如果date为10位不需要乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes())
        var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
        // return Y+M+D+h+m+s;
        return Y+M+D+h+m;
      },
      // 获取所选ID
      changeChose(val){
        // console.log(val)
        this.messageId = ''
        val.forEach((item,index) => {
          this.messageId += item.id+','
        });
      },
      // 获取消息列表
      getMessageList(pagenum){
        Server.post(Path.messageList,{
          "studentid": this.$uid,
          "pagenum": pagenum
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.data = data.data;
            this.$store.store.commit('updateMsgcount', data.pageinfo.total)
            this.total = data.pageinfo.total;
          }else{
            this.$Message.info(res.info)
          }
        });
      },
      // 批量删除
      totalDelete(){
        this.delType = 2;
        if(this.messageId){
          this.messageId=(this.messageId.substring(this.messageId.length-1)==',')?this.messageId.substring(0,this.messageId.length-1):this.messageId;
          this.CancelInfo = true;
        }else{
          this.$Message.info(this.$t('Main.UserMessage.CheckInfo'))
        }
        
      },
      // 删除消息
      deleteMessage(){
        Server.post(Path.deleteMsg,{
          "messageids": this.messageId
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$Message.success(res.info)
            this.getMessageList(this.current)
            this.messageId = ''
          }else{
            this.$Message.info(res.info)
          }
        });
      }
    }
  }

</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
.outerTable{
  overflow: hidden;
  margin-bottom: 20px;
  // 批量删除
  .totalDelete{
    width: 100px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 2px;
    margin: 18px 46px -10px;
    background-color: #3F9CE8;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
  }
  .outTable{
    padding-top: 45px;
    margin-left: 45px;
    margin-right: 45px;
  }
}
</style>



