<template>
  <div>
    <panel-title :crumbs="[{name:'我的作业',route:''}]" />
    <div class="nm_TabTitle">
      <div
      v-for="(item,index) in titleList"
      :key="index"
      @click="tab(index)"
      :class="{nm_active:index == num}"
      >
        <span class="nm_titleText">{{item}}</span>
      </div>
    </div>
    <Card dis-hover style="padding-bottom:40px;margin-bottom:20px;">
      <div class="searchTitle">
        <input
          placeholder="请输入习题名称"
          class="searchForm"
          v-model.trim="keywords"
          @keydown.enter="SearchHomework"
        />
        <span class="searchText" @click="SearchHomework">搜索</span>
      </div>
      <div class="outTable">
        <i-table ref="selection" stripe :columns="columns" :data="data"></i-table>
      </div>
      <template>
        <pagination :total="total" class="mg-top-20" :current-page='current' @pagechange="pagechange" :display='display' v-if='data.length!=0'></pagination>
      </template>
    </Card>
    <Modal v-model="sendInfo">
      <div slot="header" class="nm_headModal">提醒成功</div>
      <div class="nm_bodyModal">老师收到您的提醒，正在给您批改作业喔~</div>
      <div slot="footer" class="nm_footerModal">
        <div @click="sendInfo = false" class="nm_commonBtn nm_confirmBtn">确定</div>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import defaultShow from "@/components/defaultShow";
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import pagination from "@/components/common/pagination";
  import {
    Input,
    Card,
    Table,
    Modal,
    Form,
    Col,
    Button,
    Row,
  } from "iview";
  export default {
    name: "OverView",
    components: {
      panelTitle,
      defaultShow,
      Card,
      Modal,
      Form,
      Col,
      Button,
      Row,
      pagination,
      "i-table": Table,
      Input
    },
    data() {
      return {
        num:0,
        total:1,
        current:1,
        display:20,
        keywords:'',
        CancelInfo: false,
        sendInfo:false,
        titleList:['未完成','未批阅','已批阅'],
        columns: [
          {
            title: '编号',
            align: "center",
            key: "homeworkid",
          },
          {
            title: '课程名称',
            align: "center",
            key: "coursename",
          },
          {
            title: '班级名称',
            align: "center",
            key: "gradename",
          },
          {
            title: '习题名称',
            align: "center",
            key: "periodname",
          },
          {
            title: '授课老师',
            align: "center",
            key: "teachername",
          },
          {
            title: '截止时间',
            align: "center",
            key: "endtime",
            // render:(h,params,index) => {
            //   return h('div',this.timetrans(params.row.submittime))
            // }
          },
          {
            title: '操作',
            key: "orderEdit",
            render: (h, params, index) => {
              if(this.num==0){
                return h('div',[
                  h(
                    "span",
                    {
                      style: {
                        color: "#FFA400",
                        cursor:'pointer'
                      },
                      on: {
                        'click': () => {
                          this.$router.push({path:'/UnDone',query:{worktype:0,lessonid:params.row.lessonid,classid:params.row.classid}})
                        }
                      },
                    },
                    '写作业'
                  )
                ])
              }else if(this.num==1){
                if(params.row.sendstatus==0){
                  return h('div',[
                    h(
                      "span",
                      {
                        style: {
                          color: "#387BFF",
                          cursor:'pointer'
                        },
                        on: {
                          'click': () => {
                            Server.post(
                              Path.sendHomeworkMessage,
                              {
                                'homeworkid':params.row.homeworkid
                              },
                              res => {
                                let { code } = res;
                                if (code === 0) {
                                  this.sendInfo = true
                                  this.getHomeworkList(this.num)
                                } else {
                                  this.$Message.error(res.info)
                                }
                              }
                            );
                          }
                        },
                      },
                      '提醒'
                    )
                  ])
                }else{
                  return h('div',[
                    h(
                      "span",
                      {
                        style: {
                          color: "#387BFF",
                        },
                        on: {
                          'click': () => {

                          }
                        },
                      },
                      '已提醒'
                    )
                  ])
                }
              }else{
                return h('div',[
                  h(
                    "span",
                    {
                      style: {
                        color: "#3F9CE8",
                        cursor:'pointer'
                      },
                      on: {
                        'click': () => {
                          this.$router.push({path:'/UnDone',query:{worktype:2,lessonid:params.row.lessonid,classid:params.row.classid}})
                        }
                      },
                    },
                    '查看'
                  )
                ])
              }
            },
            align: "center"
          }
        ],
        data: [],
      }
    },
    created() {
      this.getHomeworkList(0)
    },
    methods:  {
      tab(index){
        this.num = index;
        this.keywords = ''
        this.getHomeworkList(index)
        // tab切换改变表格列数
        if(index==2){
          this.columns.forEach((item,index)=>{
            if(item.title=='截止时间'){
              this.columns.splice(index+1,0,
                {
                  title: '成绩',
                  align: "center",
                  key: "score",
                },)
            }
          })
        }else{
          this.columns.forEach((item,index)=>{
            if(item.title=='成绩'){
              this.columns.splice(index,1)
            }
          })
        }
      },
      ok(){
        this.deleteMessage()
        this.CancelInfo = false;
      },
      cancel(){
        this.CancelInfo = false;
      },
      pagechange(val){
        this.current = val;
        window.scrollTo(0,0)
      },
      // 转换日期格式
      timetrans(date){
        var date = new Date(date*1000);//如果date为10位不需要乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes())
        var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
        // return Y+M+D+h+m+s;
        return Y+M+D+h+m;
      },
      // 查询作业列表
      getHomeworkList(status){
        Server.post(
          Path.getHomeworkList,
          {
            'pagenum':1,
            'status':status
          },
          res => {
            let { code, data } = res;
            if (code === 0) {
              this.data = data.data
            } else {
              
            }
          }
        );
      },
      // 搜索作业
      SearchHomework(){
        Server.post(
          Path.searchHomework,
          {
            'pagenum':1,
            'status':this.num,
            "search": this.keywords
          },
          res => {
            let { code, data } = res;
            if (code === 0) {
              this.data = data.data
            } else {
              
            }
          }
        );
      }
    }
  }

</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
.searchTitle{
  margin: 20px 0 32px 42px;
  .searchForm{
    width: 260px;
    height: 40px;
    border: 1px solid #EBEBEB;
    border-radius:2px;
    font-size: 12px;
    padding-left: 8px;
    background:rgba(250,252,252,1);
  }
  .searchText{
    display: inline-block;
    width: 100px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background-color: #3F9CE8;
    color: #fff;
    font-size: 14px;
    position: relative;
    left: 87px;
    top: 0px;
    cursor: pointer;
    border-radius:2px;
  }
}
.outTable{
  padding: 5px 42px 0px;
}
.fl-text{
  font-size: 16px;
}
</style>



