<template>
  <div id="UserLessonList">
    <!-- 我的课表 待上课 -->
    <panel-title v-if="type==2" :crumbs="[{name:$t('Main.Common.Schedule'),route:{path:'/'}},{name:$t('Main.MyLessonList.Ready'),route:{path:''}}]" />
    <!-- 已结束 -->
    <panel-title v-if="type==0" :crumbs="[{name:$t('Main.Common.Schedule'),route:{path:'/'}},{name:$t('Main.MyLessonList.Completed'),route:{path:''}}]" />
    <!-- 今日课表 -->
    <panel-title v-if="type==1" :crumbs="[{name:$t('Main.Common.Schedule'),route:{path:'/'}},{name:$t('Main.MyLessonList.CurrentSchedule'),route:{path:''}}]" />
    <Card class='cardP0 mg-bottom-20' dis-hover>
      <div class="fixTitle">
        <div class="cardTitle">{{date}}</div>
        <span class="smallTitle"></span>
      </div>
      <Row>
        <Col span="24" style="padding: 0 80px;">
          <div class="evaluation-box">
            <default-show v-if="data.length==0" style="height:658px"></default-show>
            <div
            v-else
            class="evaluation-item"
            v-for="(item,index) in data"
            :key="index">
              <div class="eval-header-img fl">
              <img :src="item.imageurl||''" alt="" @error="defaultImg">
              </div>
              <div class="eval-content">
                <ul>
                  <li>
                    <div class="fl eval-name">
                      {{item.periodname}}
                    </div>
                    <div class='fr'>
                      <!-- 进教室 -->
                      <div type="success" shape="circle" size="small" class="nm_commonBtn state-button" v-if="item.buttonstatus==1" @click="enterClass(item.toteachid)">{{$t('Main.MyLessonList.EnterClass')}}</div>
                      <router-link :to="{path:'/LessonEvaluate',query:{paramsdate:date,flag:1}}">
                        <div class="state-button nm_commonBtn" v-if="item.buttonstatus==2" @click="handleEvaluate(item)">评价</div>
                      </router-link>
                        <div class="nm_commonBtn unstart" @click="checkDetial(item)">查看</div>
                    </div>
                  </li>
                  <li>
                    <div class="fl eval-time mr67">
                      <!-- 开始时间： -->
                      {{$t('Main.MyLessonList.StartTime')}}{{item.starttime}}
                    </div>
                    <div class="fl eval-time">
                      <!-- 结束时间： -->
                      {{$t('Main.MyLessonList.EndTime')}}{{item.endtime}}
                    </div>
                  </li>
                  <li>
                    <div class="fl eval-class-type mr40">
                      {{item.coursename}}
                    </div>
                    <div class="fl className mr40">
                      班级名称：{{item.gradename}}
                    </div>
                    <div class="fl teaName">
                      <!-- 老师： -->
                      {{$t('Main.Common.Teacher')}}{{item.teachername}}
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </Card>

  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import defaultShow from "@/components/defaultShow";
import pagination from "@/components/common/pagination";
import CardTitle from "@/components/common/CardTitle/CardTitle";
import right from "@/assets/images/right.png";
import { Card, Row, Col, Button, Modal, Select, Option } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultClass from "@/assets/images/defaultClass.png";
export default {
  name: "UserLessonList",
  components: {
    panelTitle,
    right,
    Card,
    Row,
    Col,
    pagination,
    CardTitle,
    Button,
    defaultShow,
    defaultClass,
    Modal,
    Select,
    Option
  },
  created() {
    this.getLessonData();

    // 判断是否是谷歌浏览器
    var ua = navigator.userAgent.toLocaleLowerCase();
    if(ua.match(/tencenttraveler/) != null || ua.match(/qqbrowse/) != null){
      this.ifChrome = false;
    }else if (ua.match(/firefox/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/ubrowser/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/opera/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/bidubrowser/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/metasr/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/msie/) != null || ua.match(/trident/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/maxthon/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/chrome/) != null) {
      var is360 = _mime("type", "application/vnd.chromium.remoting-viewer");
      function _mime(option, value) {
        var mimeTypes = navigator.mimeTypes;
        for (var mt in mimeTypes) {
          if (mimeTypes[mt][option] == value) {
            return true;
          }
        }
        return false;
      }
      if(is360){
        this.ifChrome = false;
      }else{
        this.ifChrome = true;
      }
    }else{
      this.ifChrome = false;
    }
  },
  data() {
    return {
      right,
      total: 100,
      current: 1,
      data: [],
      date: '',
      newDate:'',
      type:'',
      ifChrome: false,
    };
  },
  methods: {
    // 设置默认课程封面
    defaultImg(e){
      e.target.src = defaultClass;
    },
    //进教室
    enterClass(toteachid){
      var host = '/';
      let url = host+'student/enterClassMiddle/index.html?toteachid='+toteachid;
      window.open(url);
    },
    //获取课表信息
    getLessonData(){
      // 取到路由带过来的参数   
      let date = this.$route.query.date;
      let type = this.$route.query.type;
      // 将数据放在当前组件的数据内  
      this.type = type;
      this.date = date;
      // this.newDate = date.replace('年','-').replace('月','-').replace('日','');
      this.newDate = date;
      Server.post(Path.getLessonsByDate,{
        'date': this.newDate
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.data = data;
          data.forEach((item,index)=>{
            if(item.buttonstatus==0||item.buttonstatus==1){
              this.ifChange = true;
            }
          })
        }else{
          this.data = [];
        }
      })
    },
    // 回退
    goBack(){
      this.$router.push('/');
    },
    // 评价
    handleEvaluate(item){
      window.localStorage.removeItem('evaluate');
      window.localStorage.setItem('evaluate',JSON.stringify(item));
    },
    // 查看课程详情
    checkDetial(item){
      window.localStorage.setItem('courseInfo',JSON.stringify(item))
      this.$router.push({
        path:'/LessonDetail',
        query:{
          type:this.type,
          paramsdate:this.date,
          buttonstatus:item.buttonstatus
        }
      })
    },
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
// 头部时间
.cardTitle{
  color: #FEFEFE;
  background-color: #FDB44D;
  line-height: 43px;
  text-align: center;
  width: 188px;
  height: 43px;
  margin: 30px 0 18px;
}
// 课时详情
.evaluation-item {
  overflow: hidden;
  padding: 20px 0;
  border-bottom: 1px solid @line2;
  // 课程封面
  .eval-header-img {
    width: 198px;
    height: 108px;
    margin-right: 32px;
    border-radius: 8px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  // 课时详细信息
  .eval-content li {
    overflow: hidden;
    margin-bottom: 13px;
    font-size: 16px;
    color: #7A8599;
    .eval-name {
      font-size: 18px;
      color: #525866;
      line-height: 36px;
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
      max-width: 83%;
    }
    .eval-time {
      color: #525866;
    }
    &:last-child{
      margin-bottom: 0;
    }
    .mr67{
      margin-right: 67px;
    }
    .eval-class-type,.className,.teaName{
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
      max-width: 25%;
    }
    .className{
      max-width: 36%;
    }
    .teaName{
      max-width: 29%;
    }
    .mr40{
      margin-right: 30px;
    }
    // 按钮
    .unstart{
      color: #FFAB39;
      border: 1px solid #FFAB39;
      background-color: #fff;
      border-radius: 2px;
      margin: 0 10px;
    }
    .state-button{
      background-color: #FFF;
      color: #3F9CE8;
      border: 1px solid #3F9CE8;
      border-radius: 2px;
      margin: 0 10px;
    }
  }
  &:last-child{
    border-bottom: none;
  }
}
</style>



