<template>
  <div>
    <!-- name:'个人资料' -->
    <panel-title :crumbs="[{name:$t('Main.Common.UserInfo'),route:''}]" />
    <Card class='cardP0 mg-bottom-20 intro-card' dis-hover>
      <div class="person-avatar">
        <defaultHeader :sourceimg="data.imageurl"/>
        <h3>
          <span class="nameText">{{data.nickname}}</span>
          <!-- <img src="./assets/images/unknown.png" alt="" v-if="data.sex==0" class="iconGender"> -->
          <img src="./assets/images/male.png" alt="" v-if="data.sex==1" class="iconGender">
          <img src="./assets/images/female.png" alt="" v-if="data.sex==2" class="iconGender">
        </h3>
      </div>
      <div class="preson-content">
        <p>
          <!-- 手机号： -->
          <span>{{$t('Main.UserCenter.Mobile')}}</span>{{data.mobile}}
        </p>
        <p>
          <!-- 生日： -->
          <span>{{$t('Main.UserEdit.Birth')}}</span>{{data.birth||'2010-01-01'}}
        </p>
        <p>
          <!-- 所在地： -->
          <span>{{$t('Main.UserEdit.Area')}}</span>
          {{country}}
          {{province}}
          {{city}}
        </p>
        <p>
          <span>年级：</span>{{data.gradename||'无'}}
        </p>
        <!-- 个性签名： -->
        <p>
          <span>{{$t('Main.Common.PersonDesc')}}</span>
          <p class="userdesc">{{data.profile||'这个人很懒，什么都没有留下...'}}</p>
        </p>
      </div>
      <router-link :to="{path:'/UserEdit'}">
        <!-- 编辑 -->
        <div class="right-btn">{{$t('Main.UserCenter.Edit')}}</div>
      </router-link>
      <router-link :to="{path:'/UserAddress'}">
        <!-- 编辑 -->
        <div class="address-btn">收货地址</div>
      </router-link>
    </Card>

    <Card class='cardP0 mg-bottom-20 intro-amount' dis-hover>
      <div class="fl">
        <!-- 账户余额：￥ -->
        <p class="balance">{{$t('Main.UserCenter.AccountRest')}}{{this.data.usablemoney}}</p>
      </div>
      <div class="fr">
        <!-- 充值 -->
        <Button class="recharge-btn" @click="ShowConsumeModal">{{$t('Main.UserCenter.Recharge')}}</Button>
      </div>
      <div style="clear:both"></div>
    </Card>
    
    <Card class='cardP0 intro-trade' dis-hover>
      <!-- <h3 class="detail">账户明细</h3> -->
      <h3 class="detail">{{$t('Main.UserCenter.AccountDetail')}}</h3>
      <ul class="trade">
        <default-show v-if="countData.length==0" style="height:364px;padding-top:0px;margin-top:-60px;"></default-show>
        <li
        v-else
        class="trade-item clearfloat"
        v-for="(item,index) in countData"
        :key="index">
          <div class="fl">
            <!-- <img src="@/assets/images/icon_refund@2x.png" widtd="100%" v-if="item.paystatus==3"> -->
            <img src="@/assets/images/icon_recharge@2x.png" widtd="100%" v-if="item.paystatus==2">
            <img src="@/assets/images/icon_consume@2x.png" widtd="100%" v-if="item.paystatus==1||item.paystatus==3">
            <span class="trade-desc">
              {{item.paytime}}
              <!-- 购买课程 -->
              <span v-if="item.paystatus==1" style="font-weight:bold">{{$t('Main.UserCenter.Buy')}} {{item.coursename}}</span>
              <span v-if="item.paystatus==2" style="font-weight:bold">
                <!-- 余额充值 微信充值 支付宝充值 银联充值 -->
                <span v-if="item.paytype==1">{{$t('Main.UserCenter.BalanceRecharge')}}</span>
                <span v-if="item.paytype==2">{{$t('Main.UserCenter.ChatRecharge')}}</span>
                <span v-if="item.paytype==3">{{$t('Main.UserCenter.AlipayRecharge')}}</span>
                <span v-if="item.paytype==4">{{$t('Main.UserCenter.CardRecharge')}}</span>
              </span>
              <span v-if="item.paystatus==3" style="font-weight:bold">购买套餐</span>
            </span>
          </div>
          <div
          class="fr"
          :class="{'price-add':item.paystatus==2,'price-reduce':item.paystatus==1||item.paystatus==3}"
          >
            <span v-if="item.paystatus==2">+</span>
            <span v-if="item.paystatus==1||item.paystatus==3">-</span>
            <!-- ￥ -->
            {{$t('Main.Common.MoneyLogo')}}{{item.paynum}}
          </div>
        </li>
        <pagination v-if="countData.length!=0" :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange"></pagination>
      </ul>
    </Card>
    <!-- title="充值" -->
    <Modal
    v-model="consumeModal"
    class-name="vertical-center-modal"
    width="600px"
    >
      <div slot="header" class="nm_headModal">充值</div>
      <div class="type-content" style="margin-bottom:-20px">
        <Form :label-width="120">
          <!-- label="充值金额：" -->
          <FormItem :label="$t('Main.UserCenter.RechargeNum')" style="font-size: 16px;margin-top:30px;">
            <!-- placeholder="请输入充值金额" -->
            <Input v-model="input" :placeholder="$t('Main.UserCenter.Placeholder')" style="height:46px;width:416px;border-radius: 5px;" size="large"></Input>
          </FormItem>
          <FormItem style="margin-left:-1px;padding:0 20px">
            <Row :gutter="33">
              <div class="payImg">
                <img src="./assets/images/zhifubao.png" alt="" @click="flag=true;nextFlag=false;thirdFlag=false;paytype=3" v-if="!flag">
                <img src="./assets/images/zhifubao_checked.png" alt="" v-if="flag">
              </div>
              <div class="payImg">
                <img src="./assets/images/weixin.png" alt="" @click="nextFlag=true;flag=false;thirdFlag=false;paytype=2" v-if="!nextFlag">
                <img src="./assets/images/weiixin_checked.png" alt="" v-if="nextFlag">
              </div>
            </Row>
          </FormItem>
        </Form>
      </div>
      <div slot="footer" class="nm_footerModal">
        <!-- 确定 -->
        <div @click="ok" class="nm_commonBtn nm_confirmBtn">{{$t('Main.Common.SaveBtn')}}</div>
        <!-- 取消 -->  
        <div @click="cancel" class="nm_commonBtn nm_cancelBtn">{{$t('Main.Common.CancelBtn')}}</div>
      </div>
    </Modal>
  </div>
</template>

<script>
import panelTitle from "@/components/panelTitle";
import defaultHeader from "@/components/defaultHeader";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultShow from "@/components/defaultShow";
import cityJson from '@/assets/city/city2.json';
import cityTotal from '@/assets/city/city.json';
import pagination from "@/components/common/pagination";
import {
  Card,
  Input,
  Form,
  FormItem,
  Table,
  Row,
  Col,
  Switch,
  ButtonGroup,
  Button,
  Modal,
  Avatar,
  Rate,
  Icon
} from "iview";
export default {
  name: "UserCenter",
  components: {
    panelTitle,
    Button,
    Form,
    FormItem,
    Card,
    Avatar,
    Modal,
    Icon,
    Table,
    Row,
    Col,
    Input,
    pagination,
    defaultHeader,
    defaultShow
  },
  data() {
    return {
      consumeModal: false,
      data: {},
      countData: [],
      country: {},
      province: {},
      city: {},
      flag: null,
      nextFlag: null,
      thirdFlag: null,
      input: "",
      total: 0,
      current: 1,
      display: 20,
      paytype: null,
      wxFlag:false,
    };
  },
  created() {
    this.getInfo();
    this.getCountList();
  },
  methods: {
    // 分页
    pagechange(currentPage) {
      let { current, display } = this;
      this.current = currentPage;
      this.getCountList(currentPage, display);
    },
    // 显示弹框
    ShowConsumeModal: function() {
      this.consumeModal = true;
    },
    ok: function() {
      var reg = /^-?(?!0$|0\.00|0\d+$)([1-9]?\d+(\.\d{2})?)$/
      if(!reg.test(this.input)){  
        // this.$Message.error("请输入正确的充值金额");
        this.$Message.error(this.$t('Main.UserCenter.MoneyRule'));
        this.input = '';
        this.paytype = '';
        this.flag = false;
        this.nextFlag = false;
        this.thirdFlag = false;
        return false;
      }
      if(!this.paytype){
        // this.$Message.error("请选择充值方式");
        this.$Message.error(this.$t('Main.UserCenter.StyleRequired'));
        // this.input = '';
        this.paytype = null;
        this.flag = false;
        this.nextFlag = false;
        this.thirdFlag = false;
        return false;
      }
      var host = '/';
      let url = host+'student/StudentPayMiddle/index.html?studentid='+this.$uid+'&amount='+this.input+'&paytype='+this.paytype+'&source=1'+'&type=recharge';
      window.open(url);
      this.paytype = null;
      this.flag = false;
      this.nextFlag = false;
      this.thirdFlag = false;
      this.input = '';
      this.consumeModal = false;
    },
    cancel: function() {
      this.consumeModal = false;
      this.flag = false;
      this.nextFlag = false;
      this.thirdFlag = false;
      this.input = "";
      this.paytype = null;
    },
    // 获取个人资料信息
    getInfo() {
      Server.post(
        Path.getStudentInfo,
        {
          studentid: this.$uid
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.data = data;
            if(data.country == 0 || data.country == null){
               this.country = this.$t('Main.UserEdit.unknow') //未知
               this.province = ''
               this.city = ''
            }else{
              if(this.$i18n.locale=='zh-cn'){
                this.country = cityJson[data.country];
                this.province = cityJson[data.province];
                this.city = cityJson[data.city];
              }else{
                cityTotal.forEach((item,index) => {
                  if(item.id==data.country){
                    this.country = item.en_name
                  }
                  if(item.id==data.province){
                    this.province = item.en_name
                  }
                  if(item.id==data.city){
                    this.city = item.en_name
                  }
                });
              }
            }
          } else {
            this.data = {};
          }
        }
      );
    },
    // 转换日期格式
    toDate(str) {
      let date = new Date(str*1000);
      let y = date.getFullYear(),
          m = (date.getMonth() + 1 < 10 ? `0${date.getMonth()+1}` : date.getMonth() + 1),
          d = (date.getDate() < 10 ? `0${date.getDate()}` : date.getDate());
      return `${y}-${m}-${d}`;
    },
     // 获取账户明细
    getCountList(pagenum = 1, limit = this.display) {
      Server.post(
        Path.getStudentPaylog,
        {
          studentid: this.$uid,
          pagenum: pagenum,
          limit: limit
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            if(data.length==0){
              this.countData = []
            }else{
              this.countData = data.data;
              this.total = data.pageinfo.total;
            }
          } else {
            this.countData = [];
          }
        }
      );
    }
  }
};
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
// 个人信息
.intro-card {
  position: relative;
  .person-avatar {
    float: left;
    height: 100%;
    width: 277px;
    text-align: center;
    #default-img {
      width: 90px;
      height: 90px;
      border-radius: 50%;
      border: #ffffff 2px solid;
      box-shadow: 5px 0px 13px rgba(126, 142, 166, 0.41);
      overflow: hidden;
      margin: 52px auto 18px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    h3{
      font-size: 18px;
      color: @fz-Color1;
      .iconGender{
        width: 22px;
        height: 22px;
        position: relative;
        top: -3px;
      }
    }
  }
  .preson-content {
    overflow: hidden;
    font-size: 16px;
    padding: 35px 30px 15px 33px;
    border-left: 1px solid #e6e6e6;
    p {
      margin-bottom: 20px;
      color:#7A8599;
      span{
        color:@fz-Color1;
        text-align: right;
        display: inline-block;
        width: 94px;
        margin-right: 7px;
      }
      &.userdesc{
        margin-top: -44px;
        margin-left: 102px;
        margin-bottom: -15px;
      }
    }
    margin: 0 auto;
  }
  .right-btn {
    position: absolute;
    top:35px;
    right: 130px;
    margin-right: 10px;
    height: 30px;
    color: #3F9CE8;
    border: none;
    font-size: 16px;
  }
  .address-btn{
    position: absolute;
    top:35px;
    right: 30px;
    margin-right: 10px;
    height: 30px;
    color: #3F9CE8;
    border: none;
    font-size: 16px;
  }
}
//充值
.intro-amount{
  padding: 10px 42px;
  .balance {
    color: #3F9CE8;
    font-size: 20px;
    line-height: 44px;
  }
  .recharge-btn {
    font-size: 18px;
    padding: 7px 25px;
    box-shadow: 2px 1px 9px rgba(37,71,123,0.24);
    background-color: #3F9CE8;
    color: #fff;
    border: none;
  }
}
//账户明细
.intro-trade{
  margin-bottom: 16px;
  .detail {
    font-size: 16px;
    color: @fz-Color1;
    line-height: 18px;
    padding: 20px 0 25px 43px;
  }
}
.trade {
  padding: 0 78px 73px 63px;
  .trade-item {
    padding: 15px 30px 15px 0;
    border-bottom: 1px #E6E6E6 solid;
    line-height: 50px;
    color: @fz-Color1;
    font-size: 14px;
    img {
      width: 50px;
      height: 50px;
      margin-right: 50px;
      vertical-align: middle;
    }
    span span{
      margin-left: 10px;
    }
    .price-add {
      color: rgba(49, 194, 166, 1);
    }
    .price-reduce {
      color: rgba(240, 71, 71, 1);
    }
  }

}
.panel-title {
  font-size: 18px;
  height: 22px;
  font-weight: 500;
  line-height: 22px;
  color: rgba(94, 98, 102, 1);
  margin: 30px 0 20px 0;
  font-family: "PingFangSC-Regular";
}


.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;
}
#wxPay{
  position:absolute;
  left:50%;
  top:70%;
  width:200px;
  height:200px;
}
.payImg{
  display: inline-block;
  margin-right: 10px;
}
.nameText{
  display: inline-block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 120px;
}
</style>



