<template>
  <div>
    <!-- 个人资料 资料编辑 -->
    <panel-title :crumbs="[{name:$t('Main.Common.UserInfo'),route:{path:'/UserCenter'}},{name:$t('Main.UserEdit.EditInfo'),route:''}]" />
    <template>
      <!-- tab切换 -->
      <div class="TabTitle">
        <div
        v-for="(item,index) in tabText"
        :class="{active:index == num}"
        @click="tab(index)">
        <!-- 资料编辑 -->
          <span class="titleText">{{item}}</span>
        </div>
      </div>
      <div class="nm_MainArea">
        <div class="tabCon">
            <div v-if="num==0">
              <Form :label-width="110" :model="formInfo" ref="formInfo" :rules="infoValidate" key="1">
                <Row>
                  <Col class="mg-top-20">
                    <FormItem class="headDistance">
                      <Col span="20">
                        <Upload
                          class="HeadImg"
                          @success="handleSuccess"
                          :data="[1,1,4]">
                          <!-- <img :src="this.data.imageurl||HeaderImg" style="border-radius:50%; width:135px;height:135px; vertical-align: middle;"> -->
                          <defaultHeader :sourceimg="data.imageurl" class="imgShow"/>
                        </Upload>
                        <div class="headimg-des">*（图片格式支持jpg、png，需小于2M）</div>
                      </Col>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                  <!-- 昵称： -->
                    <FormItem class='form-confrol' :label="$t('Main.UserEdit.NickName')" prop="nickname">
                      <Input style="width: 364px;" v-model="formInfo.nickname" :maxlength="16"/>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                  <!-- 性别： -->
                    <FormItem class='form-confrol' :label="$t('Main.UserEdit.Gender')">
                      <RadioGroup v-model="formInfo.sex">
                        <Radio :label="1">
                          <!-- 男 -->
                          <span style="font-size:16px;margin-right:80px">{{$t('Main.UserEdit.Male')}}</span>
                        </Radio>
                        <Radio :label="2">
                          <!-- 女 -->
                          <span style="font-size:16px">{{$t('Main.UserEdit.Female')}}</span>
                        </Radio>
                      </RadioGroup>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                  <!-- 所在地： -->
                    <FormItem class='form-confrol' :label="$t('Main.UserEdit.Area')" prop="countryid">
                      <Select style="width: 156px;" v-model="countryId" @on-change="getArea(countryId)" class="selectArea">
                        <Option :value="item.id" v-for="item in countryList" :key="item.id" v-if="$i18n.locale=='zh-cn'">{{item.name}}</Option>
                        <Option :value="item.id" v-for="item in countryList" :key="item.id" v-if="$i18n.locale=='en-us'">{{item.en_name}}</Option>
                      </Select>
                      <Select style="width: 156px;" v-model="provinceId" @on-change="getCity(provinceId)" v-if="provinceList.length" class="selectArea">
                        <Option :value="item.id" v-for="item in provinceList" :key="item.id" v-if="$i18n.locale=='zh-cn'">{{item.name}}</Option>
                        <Option :value="item.id" v-for="item in provinceList" :key="item.id" v-if="$i18n.locale=='en-us'">{{item.en_name}}</Option>
                      </Select>
                      <Select style="width: 156px;" v-model="cityId" v-if="cityList.length" class="selectArea">
                        <Option :value="item.id" v-for="item in cityList" :key="item.id" v-if="$i18n.locale=='zh-cn'">{{item.name}}</Option>
                        <Option :value="item.id" v-for="item in cityList" :key="item.id" v-if="$i18n.locale=='en-us'">{{item.en_name}}</Option>
                      </Select>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                  <!-- 生日： -->
                    <FormItem class='form-confrol' :label="$t('Main.UserEdit.Birth')" id="specialDate">
                      <!-- 请选择日期 -->
                      <DatePicker type="date" :placeholder="$t('Main.UserEdit.Placeholder')" style="width: 364px" editable :options="options3"
                        @on-change="formInfo.birth=$event" format="yyyy-M-d" :value="formInfo.birth" class="selectDate"></DatePicker>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <FormItem class='form-confrol' label="年级：">
                      <Select style="width:364px" v-model="formInfo.categoryid">
                        <Option :value="item.categoryid" v-for="item in gradeList" :key="item.categoryid">{{item.name}}</Option>
                      </Select>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                  <!-- 个性签名： -->
                    <FormItem class='specialText' :label="$t('Main.Common.PersonDesc')" prop="profile">
                      <Input type="textarea" :rows="4" style="width: 476px;" v-model.trim="formInfo.profile" :maxlength="100"/>
                      <div class="count">{{formInfo.profile.length}}/100</div>
                    </FormItem>
                  </Col>
                </Row>
                <!-- <Row>
                  <Col>
                    <FormItem class='form-confrol specialText' label="兴趣课程：" prop="pid">
                      <Select style="width:364px" v-model="formInfo.gradename">
                        <Option :value="111">111</Option>
                      </Select>
                    </FormItem>
                  </Col>
                </Row> -->
                <!-- <div class="custom-btn-cancel">
                  取消
                  <router-link to="/UserCenter">{{$t('Main.Common.CancelBtn')}}</router-link>
                </div> -->
                <!-- 保存 -->
                <div class="saveInfo" @click="submitInfo()">{{$t('Main.UserEdit.Save')}}</div>
              </Form>
            </div>
            <div v-if="num==1" class="distance">
              <Form :label-width="180" :model="formPassword" ref="formPassword" :rules="pwdValidate" key="2">
                <Row>
                  <Col>
                  <!-- 手机号码： -->
                    <FormItem class='form-confrol' :label="$t('Main.UserEdit.Mobile')" prop="mobile">
                      <span class="mobileNum" style='margin: 5px 0px 0px 18px;display: inline-block;font-size:16px'>+{{this.data.prphone}} {{this.data.mobile}}</span>
                      <a class="checkCode" @click="SendPwdCode()">{{btnObject.btnText}}</a>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                  <!-- 验证码： -->
                    <FormItem class='form-confrol' :label="$t('Main.UserEdit.Validate')" prop="validate">
                      <Input style="width: 364px;" v-model="formPassword.validate"/>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                  <!-- 新密码： -->
                    <FormItem class='form-confrol' :label="$t('Main.UserEdit.NewPwd')" prop="password">
                      <Input style="width: 364px;" v-model="formPassword.password" type="password" placeholder="（6~16位，支持数字、字母、下划线）"/>
                    </FormItem>
                  </Col>
                </Row>
                <Row>
                  <Col>
                  <!-- 重复密码： -->
                    <FormItem class='form-confrol' :label="$t('Main.UserEdit.RePwd')" prop="rePassword">
                      <Input style="width: 364px;" v-model="formPassword.rePassword" type="password" placeholder="  请重复输入新密码"/>
                    </FormItem>
                  </Col>
                </Row>
                <div class="commonBtn savepwd" @click="submitPassword()">确定</div>
                <div class="commonBtn cancelmobile" @click="cancelsubmit()">取消</div>
              </Form>
            </div>
            <div v-if="num==2" class="distance">
              <Form :label-width="180" :model="formMobile" ref="formMobile" :rules="mobileValidate" key="3">
                  <Row>
                    <Col>
                    <!-- 原手机号： -->
                      <FormItem class='form-confrol' :label="$t('Main.UserEdit.prMobile')" prop="oldMobile">
                        <span class="mobileNum" style='margin: 5px 0px 0 0;display: inline-block;font-size:16px;'>+{{this.data.prphone}} {{this.data.mobile}}</span>
                      </FormItem>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                    <!-- 新手机号： -->
                      <FormItem class='form-confrol login-area' :label="$t('Main.UserEdit.NewMobile')" prop="newMobile">
                        <areaCode :editCode = 'formMobile.prphone' @selectValue = 'selectValue' class="form-selectValue"></areaCode>
                        <Input style="width: 259px;" v-model="formMobile.newMobile"/>
                        <div class="newBtn" @click="SendTelCode()">{{TelObject.btnText}}</div>
                      </FormItem>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                    <!-- 验证码： -->
                      <FormItem class='form-confrol' :label="$t('Main.UserEdit.Validate')" prop="validater">
                        <Input style="width: 346px;" v-model="formMobile.validater"/>
                      </FormItem>
                    </Col>
                  </Row>
                  <div class="commonBtn savemobile" @click="submitMobile()">确定</div>
                  <div class="commonBtn cancelmobile" @click="cancelsubmit()">取消</div>
                </Form>
            </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import HeaderImg from '@/container/UserCenter/assets/images/avatar_upload.png';
  import areaCode from '@/components/areaCode';
  import '@/assets/areaCode.css';
  // import cityJson from '@/assets/city/city2.json';
  import {
    Input,
    Form,
    FormItem,
    Radio,
    RadioGroup,
    Select,
    Option,
    DatePicker,
    Switch,
    Card,
    Row,
    Col,
    Button,
    Tabs,
    TabPane,
    Icon,
    Modal,
    Table,
  } from 'iview'
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import axios from 'axios';
  import city from '@/assets/city/city2.json';
  import defaultHeader from "@/components/defaultHeader";
  import Upload from "@/components/UploadPicture";
  export default {
    name: "UserEdit",
    components: {
      Input,
      Form,
      FormItem,
      Radio,
      RadioGroup,
      Select,
      Option,
      DatePicker,
      'i-switch': Switch,
      "i-table": Table,
      Card,
      panelTitle,
      areaCode,
      Row,
      Col,
      Button,
      Tabs,
      TabPane,
      Upload,
      Icon,
      Modal,
      defaultHeader
    },
    props: {},
    data() {
      const validatePassCheck = (rule, value, callback) => {
          if (value !== this.formPassword.password) {
            // callback(new Error('两次密码不一致'));
              callback(new Error(this.$t('Main.UserEdit.PwdInfo')));
          } else {
              callback();
          }
      };
      return {
        HeaderImg,
        formInfo: {
          nickname: '',
          sex: '',
          birth: '',
          profile: '',
          accountstatus: false,
          categoryid:''
        },
        formPassword: {
          validate: '',
          password: '',
          rePassword: '',
        },
        formMobile: {
          newMobile: '',
          validater: '',
          prphone: '86'
        },
        countryList: [],
        provinceList: [],
        cityList: [],
        gradeList:[],
        countryId: 0,
        provinceId: 0,
        cityId: 0,
        studentParams:{},
        num:0,
        show:true,
        btnObject:{
          btnText:'发 送 验 证 码',
          timer:null,
          count:''
        },
        TelObject:{
          // 发送验证码
          btnText:this.$t('Main.UserEdit.BtnText'),
          timer:null,
          count:''
        },
        infoValidate: {
          nickname: [
            // 请填写昵称
            {required: true, message: this.$t('Main.UserEdit.NickNameRequired'), trigger: 'blur'},
            // 请填写2到16位昵称
            {min: 2,max:16, message: this.$t('Main.UserEdit.NickNameRule'), trigger: 'blur'}
          ],
          sex: [
            // 请选择性别
            {required: true, message: this.$t('Main.UserEdit.GenderRequired'), trigger: 'change'}
          ],
          birth: [
            // 请选择出生日期
            {required: true, message: this.$t('Main.UserEdit.BirthRequired'), trigger: 'change'}
          ],
          profile: [
            // 请填写个性签名
            // {required: true, message: this.$t('Main.UserEdit.ProfileRequired'), trigger: 'blur'}
          ]
        },
        pwdValidate: {
          validate: [
            // message:'请输入数字验证码'
            {message:this.$t('Main.UserEdit.ValidateRule'),trigger:'blur', required:true, pattern:/^[0-9]*$/}
          ],
          password: [
            // message: '请输入6到16位密码'
            { type: 'string', min: 6, max: 16, message: this.$t('Main.UserEdit.PwdRequired'), trigger: 'blur', required: true },
            // message: '密码只可以是数字、字母或者下划线'
            { type: 'string', message: this.$t('Main.UserEdit.PwdRule'), trigger: 'blur' , pattern:/^[0-9a-zA-Z_]{1,}$/}
          ],
          rePassword: [
            // message: '请输入6到16位密码'
            { type: 'string', min: 6, max: 16, message: this.$t('Main.UserEdit.PwdRequired'), trigger: 'blur', required: true },
            // message: '密码只可以是数字、字母或者下划线'
            { type: 'string', message: this.$t('Main.UserEdit.PwdRule'), trigger: 'blur' , pattern:/^[0-9a-zA-Z_]{1,}$/},
            {validator: validatePassCheck, trigger: 'blur'}
          ]
        },
        mobileValidate: {
          newMobile: [
            // message: '请填写正确手机号'
            {message: this.$t('Main.UserEdit.MobileRequired'), trigger: 'blur', pattern:/^[0-9]{6,15}$/, required:true}
          ],
          validater: [
            // message:'请输入数字验证码'
            {message:this.$t('Main.UserEdit.ValidateRule'),trigger:'blur', required:true, pattern:/^[0-9]*$/}
          ]
        },
        data:{},
        student_sign:'',
        student_token:'',
        student_starttime:'',
        tabText:['资料编辑','修改密码','修改手机号'],
        options3: {
            disabledDate (date) {
                return date && date.valueOf() > Date.now() - 86400000;
            }
        },
      }
    },
    created() {
      this.getInfo();
    },
    mounted(){
      var dateStyle = document.getElementById('specialDate')
      var inputStyle = dateStyle.getElementsByTagName('input')
      inputStyle[0].setAttribute('readonly','readonly')
    },
    methods: {
      // tab切换
      tab(index){
        this.num = index;
      },
      // 获取区号
      selectValue(val){
        this.formMobile.prphone = val;
      },
      // 获取个人资料信息
      getInfo(){
        Server.post(Path.getStudentInfo,{
          studentid:this.$uid
        },res=>{
          let { code, data } = res;
          if(code===0){
            this.data = data;
            this.formInfo = data;
            this.getCountry();
            this.getGradeList();
            if(this.formInfo.country !== 0) {
              this.getArea(this.formInfo.country);
            }
            if(this.formInfo.province !== 0) {
              this.getCity(this.formInfo.province);
            }
            this.countryId = this.formInfo.country;
            this.provinceId = this.formInfo.province;
            this.cityId = this.formInfo.city;
            this.formInfo.birth = data.birth||'2010-01-01'
          }
        });
      },
      // 查询国家
      getCountry() {
        Server.post(Path.getCityList,{
          parentid:0
        },res=>{
          let { code, data } = res;
          if(code===0){
            this.countryId = this.formInfo.country;
            this.countryList = data;
          }else{
            this.$Message.info(res.info);
          }
        });
      },
      // 查询地区
      getArea(findArea) {
        this.provinceList = [];
        this.cityList = [];
        this.provinceId = 0;
        this.cityId = 0;
        Server.post(Path.getCityList,{
          parentid:findArea
        },res=>{
          let { code, data } = res;
          if(code===0){
            // this.getCity(this.formInfo.province||this.provinceId)
            if(data.length == 0) { //如果选择完国家省级列表返回为空数组，那么清空citylist
              this.cityList = [];
              this.cityId = 0;
            }
            this.provinceId = this.formInfo.province;
            this.provinceList = data;
          }else{
            this.$Message.info(res.info);
          }
        });
      },
      // 查询城市
      getCity(findCity) {
        Server.post(Path.getCityList,{
          parentid:findCity
        },res=>{
          let { code, data } = res;
          if(code===0){
            this.cityId = this.formInfo.city;
            this.cityList = JSON.parse(JSON.stringify(data));
          }else{
            this.$Message.info(res.info);
          }
        });
      },
      // 查询年级列表
      getGradeList(){
        Server.post(Path.getUserFavorCategory,{
          
        },res=>{
          let { code, data } = res;
          if(code===0){
            this.gradeList = data
          }else{
            this.$Message.info(res.info);
          }
        });
      },
      // 编辑信息提交
      submitInfo() {
        this.$refs.formInfo.validate((valid) => {
        if(valid){
          if(this.countryList.length === 0) {
            this.countryId = 0;
          }
          if(this.provinceList.length === 0) {
            this.provinceId = 0;
          }
          if(this.cityList.length === 0) {
            this.cityId = 0;
          }
            Server.post(Path.updateStudentInfo, {
              'imageurl':this.data.imageurl,
              'nickname':this.formInfo.nickname,
              'sex':this.formInfo.sex,
              'country':this.countryId,
              'province':this.provinceId,
              'city':this.cityId,
              'birth':this.formInfo.birth,
              'profile':this.formInfo.profile,
              'studentid':this.$uid,
              'categoryid':this.formInfo.categoryid
            }, (res) => {
              if (res.code === 0) {
                // this.$Message.success('修改成功');
                this.gradeList.forEach((item,index)=>{
                  if(item.categoryid==this.formInfo.categoryid){
                    this.gradename = item.name
                  }
                })
                this.$Message.success(this.$t('Main.UserEdit.ChangeOk'));
                this.$router.push('/UserCenter');
                this.$store.store.commit('updateName', this.formInfo.nickname)
                this.$store.store.commit('updateGradename', this.gradename)
                this.$store.store.commit('uploadHeadimg', this.data.imageurl)
              }else{
                this.$Message.info(res.info);
              }
            });
          }
        });
      },
      //修改密码
      submitPassword(){
        this.$refs.formPassword.validate((valid) => {
          if(valid){
            Server.post(Path.updateUserPass, {
              'mobile':this.data.mobile,
              'code':this.formPassword.validate,
              'newpass':this.formPassword.password
            }, (res) => {
              if (res.code === 0) {
                // this.$Message.success('修改成功');
                this.$Message.success(this.$t('Main.UserEdit.ChangeOk'));
                this.$router.push('/UserCenter');
              }else{
                this.$Message.error(res.info);
              }
            });
          }
        });
      },
      //修改手机号
      submitMobile(){
        this.$refs.formMobile.validate((valid) => {
          if(valid){
            Server.post(Path.updateMobile, {
              'oldmobile':this.data.mobile,
              'prphone':this.formMobile.prphone,
              'newmobile':this.formMobile.newMobile,
              'studentid':this.$uid,
              'code':this.formMobile.validater
            }, (res) => {
              if (res.code === 0) {
                // this.$Message.success('修改成功');
                this.$Message.success(this.$t('Main.UserEdit.ChangeOk'));
                this.$router.push('/UserCenter');
              }else{
                this.$Message.error(res.info);
              }
            });
          }
        });
      },
      //修改密码验证码
      SendPwdCode(){
        const TIME_COUNT = 60;
        if (!this.btnObject.timer) {
        Server.post(Path.sendUpdateMobileMsg, {
          'mobile':this.data.mobile,
          'prphone':this.data.prphone
        }, (res) => {
          if (res.code === 0) {
            this.$Message.success(res.info);
          }else{
            this.$Message.error(res.info);
          }
        });
          this.btnObject.count = TIME_COUNT;
          this.btnObject.btnText = this.btnObject.count+'s';
          this.btnObject.timer = setInterval(() => {
          if (this.btnObject.count > 0 && this.btnObject.count <= TIME_COUNT) {
            this.btnObject.count--;
            this.btnObject.btnText = this.btnObject.count+'s';
            } else {
            clearInterval(this.btnObject.timer);
            this.btnObject.timer = null;
            this.btnObject.btnText = '重 发 验 证 码';
            }
          }, 1000)
        }
      },
      //修改手机号验证码
      SendTelCode(){
        if(this.formMobile.prphone==''){
          // this.$Message.error("请选择区号");
          this.$Message.error(this.$t('Main.UserEdit.areaCode'));
          return false;
        }
        if(this.formMobile.newMobile==''){
          // this.$Message.error("请输入手机号");
          this.$Message.error(this.$t('Main.UserEdit.MobileRule'));
          return false;
        }
        var reg = /^[0-9]{6,12}$/;  
        if(!reg.test(this.formMobile.newMobile)){  
          // this.$Message.error("请填写正确手机号");
          this.$Message.error(this.$t('Main.UserEdit.MobileRequired'));
          return false;
        }
        const TIME_COUNT2 = 60;
        if (!this.TelObject.timer) {
        Server.post(Path.sendUpdateMobileMsg, {
          'mobile':this.formMobile.newMobile,
          'prphone':this.formMobile.prphone
        }, (res) => {
          if (res.code === 0) {
            this.$Message.success(res.info);
          }else{
            this.$Message.error(res.info);
          }
        });
          this.TelObject.count = TIME_COUNT2;
          this.TelObject.btnText = this.TelObject.count+'s';
          this.TelObject.timer = setInterval(() => {
          if (this.TelObject.count > 0 && this.TelObject.count <= TIME_COUNT2) {
            this.TelObject.count--;
            this.TelObject.btnText = this.TelObject.count+'s';
            } else {
            clearInterval(this.TelObject.timer);
            this.TelObject.timer = null;
            // this.TelObject.btnText = '重发验证码';
            this.TelObject.btnText = this.$t('Main.UserEdit.ReValidate');
            }
          }, 1000)
          }
      },
      //上传头像
      handleSuccess (url) {
        this.data.imageurl = 'http://'+url;
      },
      // 取消提交
      cancelsubmit(){
        this.$router.push('/UserCenter')
      }
    },
  }
</script>

<style scoped lang='less'>
@import "~@/assets/less/tab.less";
@import "~@/assets/base.less";
// 取消按钮
// .custom-btn-cancel {
//   margin-right: 20px;
//   width: 100px;
//   height: 38px;
//   padding: 0;
//   a{
//     color: #666;
//   }
// }
// tab切换
.TabTitle{
  background-color: #fff;
  div{
    width: 200px;
    height: 50px;
    text-align: center;
    margin-right: 14px;
    font-size: 16px;
    line-height: 50px;
    color: #666666;
    display: inline-block;
    cursor: pointer;
    border-radius: 8px 8px 0 0 ;
  }
  // 选中样式
  .active{
    background-color: #fff!important;
    color: #3F9CE8!important;
    border-bottom: 3px solid #3F9CE8;
  }
}
// 编辑区域
.nm_MainArea {
  height: 710px;
  border-top: 1px solid #e6e6e6;
  padding-left: 0;
  padding-top: 30px;
  .tabCon{
    margin: 0 auto;
    width: 600px;
    .distance{
      margin-top: 80px;
      // 按钮
      .commonBtn{
        width: 120px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        font-size: 16px;
        border-radius: 10px;
        display: inline-block!important;
        cursor: pointer;
      }
      .savemobile,.savepwd{
        background-color: #3F9CE8;
        color: #fff;
        margin-left: 200px;
        margin-right: 30px;
        margin-top: 40px;
      }
      .cancelmobile,.cancelpwd{
        background-color: #808080;
        color: #fff!important;
        margin: 0 auto!important;
      }
      // 发送验证码
      .checkCode{
        text-decoration: underline;
        margin-left: 50px;
        font-size: 14px;
      }
      .newBtn{
        width: 90px;
        margin-left: -3px;
        margin-top: -1px;
        border-radius: 0px;
        background-color: #3F9CE8;
        color: #fff;
        border: none;
        height: 40px;
        line-height: 40px;
        text-align: center;
        display: inline-block;
        position: relative;
        top: 1px;
        cursor: pointer;
      }
    }
    // 上传头像
    .headDistance{
      padding-left: 75px;
      .headimg-des{
        font-size:14px;
        color: #BDC2C8;
        margin-left: -33px;
      }
      .HeadImg {
        margin: -20px 0 10px 40px;
        width: 91px;
        height: 91px;
        border: 2px solid #fff;
        border-radius: 50%;
        -webkit-box-shadow: 5px 0 13px rgba(126, 142, 166, 0.41);
        box-shadow: 5px 0 13px rgba(126, 142, 166, 0.41);
        overflow: hidden;
        cursor: pointer;
        display: inline-block;
        .imgShow{
          border-radius:50%; 
          width:87px;
          height:87px; 
          vertical-align: middle;
          box-shadow:0px 0px 10px rgba(126,142,166,0.41);
        }
      }
    }
    // 保存按钮
    .saveInfo{
      width: 300px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      background-color: #3F9CE8;
      color: #fff;
      border-radius:8px;
      font-size: 16px;
      margin: 8px auto!important;
      cursor: pointer;
    }
    // 个性签名
    .specialText{
      position: relative;
      .count{
        font-size: 12px;
        position: absolute;
        right: 22px;
        bottom: -28px;
        color: #808080;
      }
    }
  }
}
</style>



