<template>
  <div class="calendar_schedule" id="Main">
    <panel-title :crumbs="[{name:$t('Main.Common.Schedule'),route:''}]" />
    <Card dis-hover style="margin-bottom:20px;padding:0 57px">
      <div class="CalendarBox">
        <div class="Calendar-content">
          <div class="Cal-table-head">
            <Button class="today-btn" @click="getToDayData">今日</Button>
            <div class="chooseCourse" @click="chooseClass"><span class="chooseIcon">+</span>我要选课</div>
            <div class="Cal-table-head-prev Cal-table-head-btn" @click="getPrevData()"></div>
            <div class="Cal-table-head-currentMonth">{{y}}年{{Zh[m > 12 ? 0 : m - 1]}}</div>
            <div class="Cal-table-head-next Cal-table-head-btn" @click="getNextData()"></div>
            <div class="changeCourse" @click="changeClass">调课</div>
          </div>
          <div class="Col-table-content">
            <table class="Cal-table" border="1" cellspacing="0" cellpadding="0">
              <thead>
              <tr>
                <th>一</th>
                <th>二</th>
                <th>三</th>
                <th>四</th>
                <th>五</th>
                <th>六</th>
                <th>日</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="da,index in data" :key="index">
                <td v-for="d,i in da" :key="i">
                  <div
                    class="cal-item-box"
                    :class="{
                      'activeMonth':d.activeMonth,
                      'pre-month-active':d.prevMonth || d.nextMonth,
                      'active': d.active,
                      'had-class-avtive':d.had,
                      'next-class-avtive':d.dai,
                      'today-prev-active':getNowTime(`${d.timestr}`, 1),
                      'today-next-active':getNowTime(`${d.timestr}`, 0),
                      'nextmonth-class-active':getNowTime(`${d.year}-${d.month}`, 2),
                      }"
                    @click="getDataList(d.year,d.month,d.day,d.had?'prev':'next', d)"
                  >
                    <span class="cal-item-date" v-if="!d.active" :class="{specialDate:d.num>0}">{{d.day}}</span>
                    <!-- 共x节课 -->
                    <span class="cal-item-text" v-if="d.active || d.had || d.dai">{{d.num}}节课</span>
                  </div>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="Cal-table-tip" v-if="todoList">
          <div class="table-tip-triangle"></div>
          <div class="table-tip-content">
            <div class="table-tip-content-title">课程安排：</div>
            <div class="table-tip-content-coursetime" v-for="(el, i) in todoList" :key="i">
              <span class="time">{{el.todayStartTime}}~{{el.todayEndTime}}</span>
              <span class="course">{{el.periodname}}</span>
            </div>
          </div>
        </div>
      </div>
      <Modal v-model="isShowCloseModal" width="528">
        <div slot="header" class="nm_headModal">调课申请</div>
        <Form :label-width="150" :model="formValidate" ref="formInfo" :rules="infoValidate">
          <div class="totalModal">
            <div class="commonStyle">
              <div>
                <FormItem prop="preCourseId" label="课程名称：">
                  <Select style="width:250px" v-model="formValidate.preCourseId" @on-change="getPreClass(formValidate.preCourseId)">
                    <Option :value="item.curriculumid" v-for="item in preCourse" :key="item.curriculumid">{{item.coursename}}</Option>
                  </Select>
                </FormItem>
              </div>
            </div>
            <div class="commonStyle">
              <div>
                <FormItem prop="preClassId" label="原班级名称：">
                  <Select style="width:250px" v-model="formValidate.preClassId" @on-change="getLesson(formValidate.preClassId)">
                    <Option :value="item.schedulingid" v-for="item in preClass" :key="item.schedulingid">{{item.gradename}}</Option>
                  </Select>
                </FormItem>
              </div>
            </div>
            <div class="commonStyle">
              <div>
                <FormItem prop="lessonId" label="课时名称：">
                  <Select style="width:250px" v-model="formValidate.lessonId" @on-change="getNewClass(formValidate.lessonId)">
                    <Option :value="item.lessonsid" v-for="item in lessonList" :key="item.lessonsid">{{item.periodname}}</Option>
                  </Select>
                </FormItem>
              </div>
            </div>
            <div class="commonStyle">
              <div>
                <FormItem prop="newClassId" label="新班级名称：">
                  <Select style="width:250px" v-model="formValidate.newClassId" @on-change="getInfo(formValidate.newClassId)">
                    <Option :value="item.newlessonsid" v-for="item in newClass" :key="item.newlessonsid" v-if="newClass.length>0">{{item.gradename}}</Option>
                    <Option :value="noClass" v-if="newClass.length==0">暂无符合条件的班级</Option>
                  </Select>
                </FormItem>
              </div>
            </div>
            <div class="commonStyle">
              <div class="rightAlign">老师/时间：</div>
              <div class="totalInfo"><span class="infoName">{{teachername}}</span>{{info}}</div>
            </div>
          </div>
        </Form>
        <div slot="footer"  class="nm_footerModal">
          <div class="nm_commonBtn nm_confirmBtn footerBtn" @click="submitChange()">提交申请</div>
        </div>
      </Modal>
      <Modal v-model="changeInfo">
        <div slot="header" class="nm_headModal">温馨提示</div>
        <div class="nm_bodyModal">
          您暂无可调的课程
        </div>
        <div slot="footer" class="nm_footerModal">
          <!-- 确定 -->
          <div @click="changeInfo = false" class="nm_commonBtn nm_confirmBtn">知道了</div>
        </div>
      </Modal>
      <Modal v-model="applyResult">
        <div slot="header" class="nm_headModal">申请提交成功</div>
        <div class="applyBody">
          <img :src="successPic" alt="" class="fl">
          <div class="msgBody fr">我们会在12小时以内通过系统消息或短信的形式通知您，请注意查收！</div>
        </div>
        <div slot="footer" class="nm_footerModal">
          <div @click="applyResult = false" class="nm_commonBtn nm_confirmBtn">确定</div>
        </div>
      </Modal>
    </Card>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import {Card, Button , Modal , Select , Option , Form , FormItem} from "iview";
  import Server from "@/service/request";
  import Path from "@/service/Path";
  import successPic from '@/assets/images/applysuccess@2x.png'
  import left from "@/assets/images/left.png";
  import right from "@/assets/images/right.png";

  export default {
    name: "Main",
    components: {
      Card,
      Button,
      panelTitle,
      Modal,
      Select,
      Option,
      Form,
      FormItem
    },
    data() {
      return {
        nowDate: this.getNowTime(),
        applyResult:false,
        left,
        right,
        successPic,
        data: [],
        y: 0,
        m: 0,
        day: 0,
        En: [
          "January",
          "February",
          "March",
          "April",
          "May",
          "June",
          "July",
          "August",
          "September",
          "October",
          "November",
          "December"
        ],
        Zh: [
          '一月',
          '二月',
          '三月',
          '四月',
          '五月',
          '六月',
          '七月',
          '八月',
          '九月',
          '十月',
          '十一月',
          '十二月',
        ],
        organid: 0,
        teacherid: 0,
        todoList: [], //今日课表
        // 调课
        isShowCloseModal:false,
        selectItem:'',
        ifChange:false,
        preCourse:[],
        preClass:[],
        lessonList:[],
        lessonsid:'',
        newClass:[],
        info:'',
        teachername:'',
        newlessonsid:'',
        schedulingid:'',
        curriculumid:'',
        periodid:'',
        changeInfo:false,
        noClass:'',
        formValidate:{
          preCourseId:'',
          preClassId:'',
          lessonId:'',
          newClassId:'',
        },
        infoValidate: {
          preCourseId: [
            { required: true, message: "请选择课程名称", trigger: "change", type: "number" }
          ],
          preClassId: [
            { required: true, message: "请选择原班级名称",trigger: "change",type: "number" }
          ],
          lessonId: [
            { required: true, message: "请选择课时名称",trigger: "change",type: "number" }
          ],
          newClassId: [
            { required: true, message: "请选择新班级名称",trigger: "change",type: "number" }
          ],
        },
      };
    },
    
    created() {
      this.organid = JSON.parse(window.localStorage.getItem('studentinfo')).organid;
        this.teacherid = JSON.parse(window.localStorage.getItem('studentinfo')).uid;
      
      this.getToDayData();
      this.getTodayTodoList();
    },
    watch:{
      isShowCloseModal(val, oldVal){
        if(val == false){
          this.$refs.formInfo.resetFields()
        }
      }
    },
    methods: {
      /**
       * 获取当前时间，比较当前与目标时间
       * @param d2 时间字符串 yy-mm-dd
       * @param status 0:1 0为是否小于当前条件 1为大于
       */
      getNowTime(d2, status) {
        let date = new Date(),
          year = date.getFullYear(),
          month = date.getMonth() + 1,
          day = date.getDate(),
          hour = date.getHours(),
          minute = date.getMinutes();
        let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
        let YearMDStr = `${year}-${month}-${day}`;
        let YearMStr = `${year}-0${month}`;
        let YearMSD2Str = `${new Date(d2).getFullYear()}-${new Date(d2).getMonth() + 1}`;

        /**
         * 如果存在参数，返回当前时间是否大于目标时间
         */
        if (d2) {
          if (status == 1) {
            return (
              new Date(YearMDStr.replace(/-/g, "/")) > new Date(d2.replace(/-/g, "/"))
            );
          } else if (status == 0) {
            // if (this.$Utils.isBroswer().name == 'mozilla') {
            //   if (new Date(YearMStr).getTime() < new Date(YearMSD2Str).getTime()) {
            //     return false;
            //   }
            // }
            //判断是否是当月，如果大于本月，返回false
            if (new Date(YearMStr.replace(/-/g, "/")) < new Date(YearMSD2Str.replace(/-/g, "/"))) {
              return false;
            }
            return (
              new Date(YearMDStr.replace(/-/g, "/")) < new Date(d2.replace(/-/g, "/"))
            );
          } else if (status == 2) {
            // if (this.$Utils.isBroswer().name == 'mozilla') {
            //   return new Date(YearMStr).getTime() < new Date(d2).getTime();
            // }
            return (
              new Date(YearMStr.replace(/-/g, "/")) < new Date(d2.replace(/-/g, "/"))
            );
          }
        } else {
          return dateStr;
        }
      },
      /**
       * 获取当日课表
       * @param y 年
       * @param m 月
       * @param d 日
       * @param type
       * @param data
       */
      getDataList(y, m, d, type, data) {
        if (data.num === 0) {
          return;
        }
        var type = ''
      if(data.had){
        type = 0
      }else if(data.active){
        type = 1
      }else if(data.dai){
        type = 2
      }
        this.$router.push({
          path: "/LessonList",
          query: {type: type, date: `${y}-${m}-${d}`,}
        });
      },
      getNextData() {
        let {m} = this;
        this.m += 1;
        if (this.m > 12) {
          this.y += 1;
          this.m = 1;
        }
        this.organCourseList(this.y, this.m, this.day);
      },
      getPrevData() {
        let {m} = this;
        this.m -= 1;
        if (this.m < 1) {
          this.m = 12;
          this.y -= 1;
        }
        this.organCourseList(this.y, this.m, this.day);
      },
      getToDayData() {
        var date = new Date();
        this.y = date.getFullYear();
        this.m = date.getMonth() + 1;
        this.day = date.getDate();
        this.organCourseList(this.y, this.m, this.day);
      },
      organCourseList(year, month, day) {
        var y = year;
        var m = month;
        var day = day;
        Server.post(
          Path.studentCourseList,
          {
            date: `${year}-${month}-1`
          },
          res => {
            let {code, data} = res;
            if (code === 0) {
              this.data = data.map((el, index) => {
                var d = el.map((e, i) => {
                  if (e.month < m) {
                    e.prevMonth = true;
                  } else {
                    e.prevMonth = false;
                  }
                  if (e.month > m) {
                    e.nextMonth = true;
                  } else {
                    e.nextMonth = false;
                  }
                  if (e.month == m) {
                    e.activeMonth = true;
                  }
                  if (
                    e.day == day &&
                    e.year == new Date().getFullYear() &&
                    e.month == new Date().getMonth() + 1
                  ) {
                    e.active = true;
                  } else {
                    e.active = false;
                  }
                  //判断有课状态下，是待上课还是已结课
                  if (e.day < day && e.num > 0) {
                    e.had = true;
                  } else {
                    e.had = false;
                  }
                  if (e.day > day && e.num > 0) {
                    if (Number(e.month) < new Date().getMonth() + 1) {
                      e.had = true;
                    } else {
                      e.dai = true;
                    }
                  } else {
                    e.dai = false;
                  }
                  return e;
                });

                // let last = d.splice(-1)[0];
                // console.log(last);
                // d.unshift(last)
                return d;
              });
            }
          }
        ).catch(err => {
          throw err;
        });
      },

      getTodayTodoList() {
        let date = new Date(),
          year = date.getFullYear(),
          month = (date.getMonth() + 1) > 10 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`,
          day = date.getDate() > 10 ? date.getDate() : `0${date.getDate()}`;
        let newDate = `${year}-${month}-${day}`;

        Server.post(Path.getLessonsByDate, {
            date: newDate,
            pagenum: 1,
            pagesize: 20,
          }, (res) => {
            let {code, data} = res;
            if (code === 0 && data !== null) {
              this.todoList = data.data;

              this.todoList.forEach(el => {
                this.$set(el, 'todayStartTime', this.$Utils.formatDateStr(new Date(el.starttime), 'HH:mm'));
                this.$set(el, 'todayEndTime', this.$Utils.formatDateStr(new Date(el.endtime), 'HH:mm'));
              })

            }
          }
        ).catch(err => {
          throw err;
        });
      },
      // 调课
      changeClass(){
        Server.post(Path.getbuycourselist,{
          
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              this.changeInfo = true;
            }else{
              this.preCourse = data;
              this.info = '';
              this.teachername = ''
              this.isShowCloseModal=true
            }
          }else{
            this.$Message.error(res.info)
          }
        })
        
      },
      // 获取原班级列表
      getPreClass(value){
        this.lessonList = []
        this.newClass = []
        this.teachername = ''
        this.info = ''
        this.schedulingid = ''
        this.curriculumid = ''
        // this.lessonsid = ''
        // this.periodid = ''
        this.preCourse.forEach((item,index)=>{
          if(item.curriculumid==value){
            this.preClass = item.classinfo
            this.curriculumid = item.curriculumid
          }
        })
      },
      // 获取课时列表
      getLesson(value){
        this.lessonList = []
        this.newClass = []
        this.teachername = ''
        this.info = ''
        this.preClass.forEach((item,index)=>{
          if(item.schedulingid==value){
            this.lessonList = item.lessons
            this.schedulingid = item.schedulingid
          }
        })
      },
      // 获取新班级列表
      getNewClass(value){
        this.info = ''
        this.teachername = '';
        this.lessonList.forEach((item,index)=>{
          if(item.lessonsid==value){
            this.lessonsid = item.lessonsid
            this.periodid = item.periodid
          }
        })
        if(this.schedulingid){
          Server.post(Path.getselectablelessons,{
            'lessonsid':this.lessonsid,
            'schedulingid':this.schedulingid,
            'curriculumid':this.curriculumid,
            'periodid':this.periodid
          },(res)=>{
            let { code, data } = res;
            if(code===0){
              this.newClass = data
            }else{
              
            }
          })
        }else{
          
        }
      },
      // 获取新班级的老师和时间
      getInfo(value){
        this.newClass.forEach((item,index)=>{
          if(item.newlessonsid==value){
            this.teachername = item.newteachername;
            this.info = ' / '+this.timetrans(item.starttime)+' ~ '+this.timetrans(item.endtime)
            this.newlessonsid = item.newlessonsid
          }
        })
      },
      // 确认调课
      submitChange(){
        this.$refs.formInfo.validate(valid => {
          if (valid) {
            Server.post(Path.submitapplylession,{
              'newlessonsid':this.newlessonsid,
              'oldlessonsid':this.lessonsid,
              'curriculumid':this.curriculumid
            },(res)=>{
              let { code, data } = res;
              if(code===0){
                this.applyResult = true
              }else{
                this.$Message.error(res.info)
              }
              this.isShowCloseModal=false
              this.formValidate.preCourseId = ''
              this.formValidate.preClassId = ''
              this.formValidate.lessonId = ''
              this.formValidate.newClassId = ''
              this.info = ''
              this.teachername = '';
              this.preClass = []
              this.lessonList = []
              this.newClass = []
            })
            this.isShowCloseModal=false;
          } else {
            
          }
      });
      },
      // 我要选课
      chooseClass(){
        window.open('/web/#/classpage?type=0')
      },
      // 转换日期格式
      timetrans(date){
        var date = new Date(date*1000);//如果date为10位不需要乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes())
        var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
        // return Y+M+D+h+m+s;
        return Y+M+D;
      },
    }
  };
</script>
<style scoped lang="less">
  @import "~@/assets/less/variable";
  @import "~@/assets/base";
  .CalendarBox {
    max-width: 1250px;
    margin: auto;
    margin-bottom: 20px;
    .Calendar-content {
      margin-top: 10px;
      .Cal-table-head {
        position: relative;
        height: 55px;
        line-height: 55px;
        background: @color-theme;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        text-align: center;
        color: @color-white;
        .today-btn {
          position: absolute;
          left: 60px;
          top: 16px;
          border: none;
          font-size: 14px;
          background: @color-highlight-blue;
          color: @color-white;
          width: 72px;
          height: 26px;
          line-height: 16px;
        }
        .Cal-table-head-btn,
        .Cal-table-head-currentMonth {
          display: inline-block;
          cursor: pointer;
        }
        .Cal-table-head-currentMonth {
          font-size: @font-size-large-s;
          margin-left: 14%;
        }
        .Cal-table-head-btn {
          vertical-align: middle;
          line-height: 22px;
          width: 22px;
          height: 22px;
          margin: 10px;
          &.Cal-table-head-prev {
            position: absolute;
            left: 260px;
            top: 8px;
            background: url(~@/assets/images/prepic@2x.png) no-repeat center;
            background-size: 100% 100%;
          }
          &.Cal-table-head-next {
            position: absolute;
            right: 260px;
            top: 8px;
            background: url(~@/assets/images/nextpic@2x.png) no-repeat center;
            background-size: 100% 100%;
          }
        }
      }
      .Col-table-content {
        .Cal-table {
          width: 100%;
          text-align: center;
          border-collapse: collapse;
          border: 1px solid transparent;
          box-sizing: border-box;
          tr:first-child th {
            color: @color-theme;
            background: #ECF6FF;
          }
          tr:nth-child(1) {
            td {
              border-top: 1px solid #ECF6FF;
            }
          }
          tr:first-child th:last-child {
            border-right: none;
          }
          tr td:first-child {
            border-left: none;
          }
          tr td:last-child {
            border-right: none;
          }
          tr:last-child td {
            border-bottom: 1px solid #CCCCCC;
          }
          thead tr {
            height: 50px;
            font-size: 18px;
            color: @color-gray;
            background: #FCFCFC;
          }
          tbody tr td {
            position: relative;
            height: 85px;
            color: #637e95;
            vertical-align: middle;
            text-align: center;
            position: relative;
            width: 100px;
            border: 1px solid #CCCCCC;
            .cal-item-box {
              width: 100%;
              height: 100%;
              // margin: 0 auto;
              padding-top: 14px;
              &.had-class-avtive {
                cursor: pointer;
              }
              .cal-item-date {
                text-align: center;
                box-sizing: border-box;
                font-size: 42px;
                font-family: MicrosoftYaHei-Bold;
                color: #B8B8B8;
              }
              .specialDate{
                // font-size: 64px;
                position: absolute;
                top: 15px;
                left: 0;
                right: 0;
              }
            }
          }
        }
      }
    }
    .Cal-table-tip {
      position: relative;
      margin-top: 30px;
      padding: 0 30px;
      border: 1px dashed #BBBBBB;
      border-radius: 10px;
      .table-tip-content {
        .table-tip-content-title {
          line-height: 40px;
          font-size: @font-size-normal;
          color: #666666;
        }
        .table-tip-content-coursetime {
          line-height: 40px;
          span {
            font-size: @font-size-small;
            &.time {
              display: table-cell;
              width: 100px;
              color: @color-theme;
            }
            &.course {
              display: table-cell;
            }
          }
        }
      }
    }
  }

  .had-class-avtive .cal-item-date,
  .next-class-avtive .cal-item-date {
    color: #B8B8B8;
  }

  .had-class-avtive .cal-item-text,
  .next-class-avtive .cal-item-text,
  .active .cal-item-text {
    font-size: 14px;
  }

  /** 将来的课程 **/
  .next-class-avtive {
    pointer-events: auto;
    cursor: pointer;
    .cal-item-text {
      width: 66px;
      height: 26px;
      line-height: 26px;
      background: @color-orange;
      color: #fff;
    }
  }

  .nextmonth-class-active {
    background: #F4F8FC;
    .cal-item-date {
      color: #DFE3E6 !important;
    }
  }

  /** 大于今天的日期颜色 **/
  .today-next-active {
    .cal-item-date {
      color: @color-highlight-blue !important;
    }
  }

  /** 今天 **/
  .active {
    position: absolute;
    z-index: 3;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    border: 2px solid @color-orange;
    cursor: pointer;
    padding-top: 0px!important;
    box-sizing: content-box;
    .cal-item-text {
      width: 100%;
      height: 100%;
      left: 0;
      right: 0;
      bottom: 0;
      line-height: 85px;
      font-size: 32px;
      color: @color-orange;
      background: none;
    }
  }

  /** 存在课程的课程文字说明 **/
  .cal-item-text {
    width: 65px;
    height: 25px;
    line-height: 25px;
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    font-size: @font-size-normal;
    background: #CCCCCC;
    color: #666565;
  }

  /** 已过去的课程 **/
  .pre-month-active {
    color: #a0abb4;
    pointer-events: none;
    cursor: pointer;
  }

  /** 今天以前 **/
  .today-prev-active {
    background: #F4F8FC;
  }

  /** 今天以前且上个月的 **/
  .pre-month-active.today-prev-active {
    .cal-item-date {
      color: #DFE3E6!important;
    }
  }
  // 调课
  .changeCourse{
    float: right;
    width: 72px;
    height: 26px;
    line-height: 26px;
    text-align: center;
    border-radius: 6px;
    cursor: pointer;
    background-color: rgba(255,255,255,0.2);
    color: #fff;
    margin-right: 53px;
    margin-top: 15px;
  }
  // 调课弹窗
  .totalModal{
    margin-left: 6%;
    margin-top: 20px;
    .commonStyle{
      font-size: 15px;
      div{
        display: inline-block;
        &:last-of-type{
          color: #7A8599;
        }
      }
      .rightAlign{
        width: 100px;
        text-align: right;
        color: #7A8599;
        font-size: 16px;
        margin-left: 38px;
      }
      .totalInfo{
        margin-top: -10px;
        .infoName{
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          display: inline-block;
          max-width: 135px;
          position: relative;
          top: 6px;
        }
      }
    }
    .footerBtn{
      width: 140px;
      height: 40px;
      line-height: 40px;
      font-weight:bold;
      margin-right: 0;
    }
  }
  // 申请成功弹窗
  .applyBody{
    height: 100px;
    margin-top: 20px;
    img{
      margin-left: 60px;
    }
    .msgBody{
      width: 272px;
      font-size: 16px;
      color: #808080;
      margin-top: 40px;
    }
  }
  // 我要选课
  .chooseCourse{
    position: absolute;
    left: 150px;
    top: 16px;
    border: 1px solid #fff;
    height: 26px;
    line-height: 26px;
    background-color: #FFA400;
    border-radius: 4px;
    text-align: center;
    width: 92px;
    cursor: pointer;
    .chooseIcon{
      font-size: 19px;
      font-weight: bold;
      margin-right: 8px;
      vertical-align: middle;
    }
  }
</style>



