import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
      step:1,
      headimg: 0,
      nickname: 2,
      gradename:3,
      changeModal:false,
      msgcount:'',
      feedbackcount:''
    },
    mutations: {
      selectStep(state,d){
        state.step = d;
      },
      uploadHeadimg(state, d) {
        state.headimg = d;
      },
      updateName(state,d){
        state.nickname = d;
      },
      updateGradename(state,d){
        state.gradename = d;
      },
      updateMsgcount(state,d){
        state.msgcount = d;
      },
      updateFeedbackcount(state,d){
        state.feedbackcount = d;
      },
      ApplyChange(state,d){
        state.changeModal = d;
      }
    }
  }
)

export default {
  store
}



