<template>
  <div>
    <!-- [{name:'我的订单',route:''}] -->
    <panel-title :crumbs="[{name:$t('Main.Common.MyOrder'),route:''}]" />
      <div class="nm_TabTitle">
          <div
          :class="{nm_active:1 == tabNum}"
          @click="tab(1)"
          >
            <span class="nm_titleText">课程订单</span>
          </div>
          <div 
          :class="{nm_active:2 == tabNum}" 
          @click="tab(2)"
          >
            <span class="nm_titleText">套餐订单</span>
          </div>
        </div>
    <default-show v-if="data.length==0"></default-show>
    <div class="nm_MainArea" v-else>
      <Card
      dis-hover
      class="order-card"
      v-for="(item,index) in data"
      :key="index">
        <p slot="title" class="overview-title">
          订单编号：{{item.ordernum}}
          <span class="order-time">下单时间：{{item.ordertime}}</span>
          <!-- 该订单将于XXX失效，请尽快支付 -->
          <!-- <span class="order-time outdate" v-if="item.orderstatus==0&&$i18n.locale=='zh-cn'">{{$t('Main.UserOrder.OrderInfo1')}}{{item.ordertime | calcDate}}{{$t('Main.UserOrder.OrderInfo2')}}</span>
          <span class="order-time outdate" v-if="item.orderstatus==0&&$i18n.locale=='en-us'">{{$t('Main.UserOrder.OrderInfo1')}}{{item.ordertime | calcDate}}{{$t('Main.UserOrder.OrderInfo2')}}</span> -->
        </p>
        <div class="order-content">
          <!-- 课程订单 -->
          <div class="order-left" v-if="tabNum==1">
            <div class="left-img">
              <img :src="item.imageurl" @error="defaultImg">
            </div>
            <div class="order-desc">
              <div class="courseTitle">
                <div class="firstTitle">{{item.coursename}}</div>
                <div class="courseType" v-if="item.coursetype==2">直播</div>
                <div class="courseType" v-if="item.coursetype==1">录播</div>
              </div>
              <p v-if="item.coursetype==2">{{item.classname}}</p>
              <ul>
                <li class="desc-item">
                  <!-- 老师： -->
                  <p>{{$t('Main.Common.Teacher')}}{{item.teachername}}</p>
                </li>
              </ul>
              <ul>
                <li class="desc-item">
                  <!-- 课程总价： -->
                  <p>
                    <span class="color-red">总价：￥{{item.amount}}</span>
                  </p>
                </li>
              </ul>
            </div>
          </div>
          <!-- 套餐订单 -->
          <div class="order-left" v-if="tabNum==2">
            <div class="left-img">
              <img :src="item.setimgpath" @error="defaultImg">
            </div>
            <div class="order-desc count-order">
              <div class="courseTitle">
                {{item.setmeal}}
              </div>
              <p class="packageContent">{{item.content}}</p>
              <ul>
                <li class="desc-item" v-if="item.trialtype==1">
                  全部课程均可使用
                </li>
                <li class="desc-item" v-if="item.trialtype==2">
                  指定分类可使用
                </li>
                <li class="desc-item" v-if="item.trialtype==3">
                  指定课程可使用
                </li>
              </ul>
              <ul>
                <li class="desc-item">
                  有效期至：
                  <span v-if="item.efftype==1">{{timetrans(item.effendtime)}}</span>
                  <span v-if="item.efftype==2">购买后{{item.efftime}}天</span>
                </li>
              </ul>
              <ul>
                <li class="desc-item">
                  <!-- 课程总价： -->
                  <p>套餐价格：
                    <span class="color-red">￥{{item.amount}}</span>
                  </p>
                </li>
              </ul>
            </div>
          </div>
          <div class="order-right">
            <Row :gutter="16" type="flex" justify="space-around" class="code-row-bg">
              <div class="hover-btn" v-if="item.orderstatus==0" @click="goPay(item.ordernum)">立即购买</div>
              <!-- 取消 -->
              <div class="leave-btn" v-if="item.orderstatus==0" @click="OrderCancel(item.ordernum)">{{$t('Main.Common.CancelBtn')}}</div>
              <!-- 已支付 已取消 已退款 -->
              <div class="already"  v-if="item.orderstatus==20">{{$t('Main.UserOrder.AlreadyPay')}}</div>
              <div class="already"  v-if="item.orderstatus==10">{{$t('Main.UserOrder.AlreadyCancel')}}</div>
              <div class="already"  v-if="item.orderstatus==40">{{$t('Main.UserOrder.AlreadyRefund')}}</div>
            </Row>
          </div>
        </div>
      </Card>
      <pagination :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange" style="height:50px"></pagination>
    </div>
    
    <!-- <Page :current="current" :total="total" @on-change="pagechange" :page-size="display" v-if="data.length!=0" class="pageStyle"/> -->
    <!-- title="温馨提示" -->
    <Modal v-model="modal">
      <div slot="header" class="nm_headModal">温馨提示</div>
      <p class="nm_bodyModal">您确定要取消订单吗？</p>
      <div slot="footer" class="nm_footerModal">
        <!-- 确定 -->
        <div @click="ok" class="nm_commonBtn nm_confirmBtn">{{$t('Main.Common.SaveBtn')}}</div>
        <!-- 取消 -->  
        <div @click="cancel" class="nm_commonBtn nm_cancelBtn">{{$t('Main.Common.CancelBtn')}}</div>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import pagination from "@/components/common/pagination";
  import defaultShow from "@/components/defaultShow";
  import defaultClass from "@/assets/images/defaultClass.png";
  import Server from '@/service/request'
  import Path from '@/service/Path'
  import {
    Card,
    Input,
    Table,
    Row,
    Col,
    Switch,
    ButtonGroup,
    Button,
    Modal,
    Avatar,
    Rate,
    Icon,
    Page
  } from "iview";
  export default {
    name: "OverView",
    components: {
      panelTitle,
      Card,
      Input,
      Table,
      Row,
      Col,
      "i-switch": Switch,
      "i-table": Table,
      ButtonGroup,
      Button,
      Modal,
      Avatar,
      Rate,
      Icon,
      pagination,
      defaultShow,
      defaultClass,
      Page
    },
    data() {
      return {
        data: [],
        total: 0, // 记录总条数
        display: 20, // 每页显示条数
        current: 1, // 当前的页数
        modal:false,
        num:null,
        tabNum:1,
      }
    },
    created() {
      let { current, display } = this;
      this.getListData(1,display);
    },
    //提醒订单失效时间
    filters: {
      calcDate(date) {
        var newDate=new Date(date);
        newDate = newDate.setMinutes(newDate.getMinutes()+30);
        function add0(m){return m<10?'0'+m:m }
        //shijianchuo是整数，否则要parseInt转换
        var time = new Date(newDate);
        var y = time.getFullYear();
        var m = time.getMonth()+1;
        var d = time.getDate();
        var h = time.getHours();
        var mm = time.getMinutes();
        var s = time.getSeconds();
        // return y+'-'+add0(m)+'-'+add0(d)+' '+add0(h)+':'+add0(mm)+':'+add0(s);
        return add0(h)+':'+add0(mm)+':'+add0(s);
      }
    },
    methods:  {
      // tab 切换
      tab(index){
        this.tabNum = index;
        this.current = 1;
        this.getListData(this.current,this.display);
      },
      // 设置默认图片
      defaultImg(e){
        e.target.src = defaultClass;
      },
      // 查询订单状态（是否失效）
      goPay(ordernum){
        var that = this;
        var host = window.location.origin=='http://192.168.0.31:8080'?'http://www.lemonedu.com/':'/';
        ajax({
          url: host+"student/Myorder/queryOrderStatus",
          type: "POST",
          data: {'ordernum':ordernum, 'type':that.tabNum },
          dataType: "json",
          success: function (response, xml) {
            response = JSON.parse(response)
            if(response.code==0){
              // var a = document.createElement('a');
              // a.setAttribute('target','_blank');
              // a.setAttribute('href','/organweb/#/paymentCenter?ordernum='+ordernum)
              // a.click();
              if(that.tabNum==1){
                let url = host+'web/#/paymentCenter?ordernum='+ordernum
                window.open(url)
              }else{
                let url = host+'web/#/comboPaymentCenter?ordernum='+ordernum
                window.open(url)
              }
            }else{
              that.$Message.info(response.info)
              that.getListData(this.current,that.display);
            }
          },
          fail: function (status) {
            // that.$Message.error(status)
            that.getListData(this.current,that.display);
          }
        });
      function ajax(options) {
        options = options || {};
        options.type = (options.type || "GET").toUpperCase();
        options.dataType = options.dataType || "json";
        var params = formatParams(options.data);
        //创建 - 非IE6 - 第一步
        if (window.XMLHttpRequest) {
              var xhr = new XMLHttpRequest();
          } else { //IE6及其以下版本浏览器
              var xhr = new ActiveXObject('Microsoft.XMLHTTP');
          }
          //接收 - 第三步
          xhr.onreadystatechange = function () {
              if (xhr.readyState == 4) {
                  var status = xhr.status;
                  if (status >= 200 && status < 300) {
                      options.success && options.success(xhr.responseText, xhr.responseXML);
                  } else {
                      options.fail && options.fail(status);
                  }
              }
          }
          //连接 和 发送 - 第二步
          if (options.type == "GET") {
              xhr.open("GET", options.url + "?" + params, true);
              xhr.send(null);
          } else if (options.type == "POST") {
              xhr.open("POST", options.url, false);
              //设置表单提交时的内容类型
              xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
              xhr.send(params);
          }
        }
        //格式化参数
        function formatParams(data) {
          var arr = [];
          for (var name in data) {
              arr.push(encodeURIComponent(name) + "=" + encodeURIComponent(data[name]));
          }
          // arr.push(("v=" + Math.random()).replace(".",""));
          return arr.join("&");
        }
      },
      OrderCancel(num){
        this.modal = true;
        this.num = num;
      },
      // 翻页
      pagechange: function(currentPage) {
        this.current = currentPage;
        this.getListData(currentPage,this.display); 
        window.scrollTo(0,0)
      },
      // 获取订单列表
      getListData(pagenum=1,limit=this.display){
        if(this.tabNum==1){
          // 课程订单
          Server.post(Path.getMyOrderList,{
            'studentid':this.$uid,
            'pagenum':pagenum,
            'limit':limit
          },(res)=>{
            let { code, data } = res;
            if(code===0){
              this.data = data.data;
              this.total = data.pageinfo.total;
            }else{
              this.data = [];
            }
          });
        }else{
          // 套餐订单
          Server.post(Path.getPackageOrderList,{
            'pagenum':pagenum,
          },(res)=>{
            let { code, data } = res;
            if(code===0){
              this.data = data.data;
              this.total = data.pageinfo.total;
            }else{
              this.data = [];
            }
          });
        }
      },
      // 取消订单
      ok() {
        let {display}=this;
        if(this.tabNum==1){
          // 课程订单
          Server.post(Path.cancelOrder,{
            'ordernum':this.num
          },(res)=>{
            let { code, data } = res;
            if(code===0){
              this.getListData(this.current,this.display);
              this.$Message.success(res.info);
              this.modal = false;
            }else{
              this.$Message.error(res.info);
              this.modal = false;
            }
          });
        }else{
          // 套餐订单
          Server.post(Path.cancelCountOrder,{
            'ordernum':this.num
          },(res)=>{
            let { code, data } = res;
            if(code===0){
              this.getListData(this.current,this.display);
              this.$Message.success(res.info);
              this.modal = false;
            }else{
              this.$Message.error(res.info);
              this.modal = false;
            }
          });
        }
      },
      cancel() {
        this.modal = false;
      },
      // 转换时间格式
      timetrans(date){
        var date = new Date(date*1000);//如果date为10位不需要乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
        var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        // var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
        var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes())
        var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
        // return Y+M+D+h+m+s;
        return Y+M+D;
      },
    }
  }
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
.nm_MainArea{
  padding: 60px 49px 0;
  // 订单信息
  .order-card {
    margin-bottom: 20px;
    border: 1px solid #D9D9D9!important;
    .overview-title {
      padding-left: 14px;
      height:24px;
      font-size:14px;
      line-height:24px;
      color:#333333;
      font-family:'PingFangSC-Medium';
      font-weight: 400;
      .order-time {
        color: #999;
        font-size: 14px;
        display: inline-block;
        margin-left: 44px;
        font-family:'PingFangSC-Medium';
        font-weight: 400;
        float: right;
        margin-right: 22px;
      }
    }
    // 订单详细信息
    .order-content {
      height: 152px;
      margin-left: 15px;
      .order-left {
        float: left;
        width: 75%;
        // 订单封面
        .left-img {
          float: left;
          height: 150px;
          width: 250px;
          margin-right:26px;
          border-radius: 4px;
          overflow: hidden;
          position: relative;
          img{
            position: absolute;
            left: -1px;
            top: -1px;
            width: 252px;
            height: 152px;
          }
        }
        .order-desc {
          float: left;
          line-height: 39px;
          max-width: 47%;
          h3 {
            font-size:16px;
            color:rgba(64,64,64,1);
            font-family:'PingFangSC-Medium';
          }
          p {
            font-size:16px;
            color:#666;
            font-family:'PingFangSC-Regular';
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          .courseTitle{
            font-size: 18px;
            color: #404040;
            font-weight: 700;
            position: relative;
            height: 36px;
            div{
              display: inline-block;
            }
            .firstTitle{
              max-width: 80%;
              margin-right: 20px;
            }
            .courseType{
              font-size: 14px;
              color: #8e9daa;
              font-weight: 400;
              position: relative;
              top: -12px;
            }
          }
          .packageContent,.courseTitle,.firstTitle{
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          &.count-order{
            line-height: 31px!important;
          }
          .desc-item {
            margin-right: 40px;
            font-size: 14px;
            p {
              font-size:14px;
              color:rgba(153,153,153,1);
              font-family:'PingFangSC-Regular';
            }
            .color-red {
              color:#F04747;
            }
          }
        }
      }
      // 右侧支付状态
      .order-right {
        position: absolute;
        right: 0;
        color: #93999F;
        font-size: 14px;
        text-align: center;
        line-height: 147px;
        width: 300px;
        border-left:1px solid #CFDCE7;
        padding: 0 80px;
        .leave-btn {
          background-color: #fff;
          width: 81px;
          height: 31px;
          line-height: 31px;
          font-size: 14px;
          text-align: center;
          color: #93999F;
          border-radius: 2px;
          border: 1px solid #E6E6E6;
          margin-bottom: 30px;
          cursor: pointer;
        }
        .hover-btn {
          background-color: #FFAB39;
          width: 81px;
          height: 31px;
          line-height: 31px;
          font-size: 14px;
          text-align: center;
          color: #fff;
          border-radius: 2px;
          margin-top: 30px;
          margin-bottom: 25px;
          cursor: pointer;
        }
        .already{
          position: relative;
        }
      }
    }
  }
}
// .outdate{
//   color: #F04747;
//   position: absolute;
//   right: 20px;
//   font-size: 12px;
// }
</style>



