import Vue from 'vue';
import Router from 'vue-router';
// import HelloWorld from '@/components/HelloWorld';
import inner from '@/components/inner';
//学生端-我的课程
import ArrangingLesson from '@/container/ArrangingManagement';
//学生端-课程详情
import LessonContent from '@/container/ArrangingManagement/ArrangeLesson';
//学生端-约课
import OrderLesson from '@/container/ArrangingManagement/OrderLesson';
//学生端-我的课表
import Main from '@/container/InstitutionalSchedule';
//学生端-课程列表 
import LessonList from '@/container/InstitutionalSchedule/LessonList/LessonListTotal';
//学生端-课程回放
import LessonPlay from '@/container/InstitutionalSchedule/LessonList/LessonPlay';
//学生端-课程评价
import LessonEvaluate from '@/container/InstitutionalSchedule/LessonList/LessonEvaluate';
// 学生端-学生订单
import UserOrder from '@/container/UserOrder';
// 学生端-学生资料
import UserCenter from '@/container/UserCenter';
// 学生端-资料编辑
import UserEdit from '@/container/UserCenter/UserEdit';
//学生端-我的收藏
import UserStar from '@/container/UserStar';
//学生端-我的消息
import UserMessage from '@/container/UserMessage';
//学生端-分析反馈
import UserFeedback from '@/container/UserFeedback';
//学生端-精彩瞬间
import UserMoment from '@/container/UserMoment';
//学生端-我的作业
import UserHomework from '@/container/UserHomework';
//学生端-我的套餐
import UserPackage from '@/container/UserPackage';
//学生端-课程查看
import LessonDetail from '@/container/InstitutionalSchedule/LessonList/LessonDetail';
//学生端-录播视频
import playVideo from '@/container/ArrangingManagement/playVideo';
//学生端-我的作业-未提交
import UnDone from '@/container/UserHomework/UnDone';
//学生端-个人资料-收货地址
import UserAddress from '@/container/UserCenter/UserAddress';
Vue.use(Router);
export default new Router({
  routes: [
    // 动态路径参数 以冒号开头
    //学生端-我的课表
    {
        path: '/',
        component: inner,
        redirect: '/',
        children:[
          {
            path: '',
            component: Main,
          },
          //学生端-课程列表
          {
            path: '/ArrangingLesson',
            component: ArrangingLesson,
          },
          //学生端-课程详情
          {
            path: '/LessonContent',
            component: LessonContent,
            name: 'LessonContent'
          },
          //学生端-约课
          {
            path: '/OrderLesson',
            component: OrderLesson,
            name: 'OrderLesson'
          },

          // 学生端-课程列表
          {
            path: '/LessonList',
            component: LessonList,
            name: 'LessonList',
          },
          // 学生端-课程回放
          {
            path: '/LessonPlay',
            component: LessonPlay,
            name: 'LessonPlay'
          },
          // 学生端-课程评价
          {
            path: '/LessonEvaluate',
            component: LessonEvaluate,
            name: 'LessonEvaluate'
          },
          // 学生端-我的订单
          {
            path: '/UserOrder',
            component: UserOrder,
            name: 'UserOrder'
          },
          // 学生端-学生资料
          {
            path: '/UserCenter',
            component: UserCenter,
            name: 'UserCenter'
          },
          // 学生端-资料编辑
          {
            path: '/UserEdit',
            component: UserEdit,
            name: 'UserEdit'
          },
          // 学生端-我的收藏
          {
            path: '/UserStar',
            component: UserStar,
            name: 'UserStar'
          },
          // 学生端-我的消息
          {
            path: '/UserMessage',
            component: UserMessage,
            name: 'UserMessage'
          },
           // 学生端-分析反馈
          {
            path: '/UserFeedback',
            component: UserFeedback,
            name: 'UserFeedback'
          },
           // 学生端-精彩瞬间
          {
            path: '/UserMoment',
            component: UserMoment,
            name: 'UserMoment'
          },
           // 学生端-我的作业
           {
            path: '/UserHomework',
            component: UserHomework,
            name: 'UserHomework'
          },
           // 学生端-我的套餐
           {
            path: '/UserPackage',
            component: UserPackage,
            name: 'UserPackage'
          },
           // 学生端-课程查看
           {
            path: '/LessonDetail',
            component: LessonDetail,
            name: 'LessonDetail'
          },
           // 学生端-录播视频
           {
            path: '/playVideo',
            component: playVideo,
            name: 'playVideo'
          },
           // 学生端-我的作业-未提交
           {
            path: '/UnDone',
            component: UnDone,
            name: 'UnDone'
          },
           // 学生端-个人资料-收货地址
           {
            path: '/UserAddress',
            component: UserAddress,
            name: 'UserAddress'
          },
        ]
    },
  ]
})



